import React, { Component, Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

// import { initialsString } from '../../../utils/initialsString';

import { API } from '../../../constants/api';
import {
  StyledHeader,
  StyledNav,
  StyledLogo,
  StyledListGeneral,
  StyledElementsList,
  StyledIconUser,
  StyledIconAppsDesktop,
  StyledHamburguer,
  StyledUser,
  StyledInterior,
  StyledIconBell,
  StyledBoxLogo,
  StyledAvatarPerson,
  StyledLinkMobile
} from './style';
import AppsHeader from './AppsHeader';
import SearchHeaderTop from './SearchHeaderTop';
import UserMenu from './UserMenu';
import Icon from '../../core/Icon';

import { Menu, Apps, Bell, Dashboard, Dots } from '../../../../assets/images';
import Notification from './Notification';
import MenuList from './MenuList';
import BoxLogoAvatar from './BoxLogoAvatar';
import Logo from '../../../../assets/images/logo.svg';

import ModalAvatar from '../Employee/ChangeAvatar';

/** Utils */
import { navigateToFile } from '../../../utils/navigate';
import { AVATAR_MEDIUM } from '../../../utils/crops';

class Header extends Component {
  constructor() {
    super();

    this.state = {
      activeMenuBurguer: false,
      activeMobile: null,
      menuUser: false,
      showUser: false,
      showUserList: true,
      activeApps: false,
      activeNotifications: false,
      editAvatar: false,
      userProfileView: false,
      isMe: false
    };
  }

  componentDidMount() {
    this.upateAvatar();
  }

  componentDidUpdate(prevProps) {
    const { query } = this.props;
    const { query: prevQuery } = prevProps;

    if (query !== prevQuery) {
      this.onChangeSearch({ target: { value: query } });
    }

    if (prevProps.userProfile) {
      const { userId } = prevProps.userProfile;

      if (userId !== this.props.userProfile.userId) {
        this.upateAvatar(this.props.userProfile.userId);
      }
    }
  }

  hiddenScroll = () => {
    if (window.innerWidth < 1024) {
      document.body.style.overflow = 'hidden';
    }
  };

  activeScroll = () => {
    if (window.innerWidth < 1024) {
      document.body.style.overflow = 'unset';
    }
  };

  onToggleMenuBurguer = ev => {
    const { activeMenuBurguer } = this.state;
    ev.stopPropagation();
    this.setState(
      {
        activeMenuBurguer: !activeMenuBurguer,
        menuUser: false,
        showUserList: true
      },
      () => {
        if (!activeMenuBurguer) {
          this.hiddenScroll();
        } else {
          this.activeScroll();
        }
      }
    );
  };

  onChangeSearch = ({ target }) => {
    const { setShowSearch, enterSearch } = this.props;
    enterSearch(target.value);

    setShowSearch(true);
  };

  onToggleSearch = () => {
    const { setShowSearch } = this.props;
    setShowSearch(true);
  };

  onFocusSearch = () => {
    const { setShowSearch } = this.props;
    setShowSearch(true);
  };

  onSelectedMenuMobile = id => {
    const { activeMobile } = this.state;
    if (id === activeMobile) {
      this.setState({ activeMobile: null });
    } else {
      this.setState({ activeMobile: id });
    }
  };

  onSetShowUser = () => {
    const { showUser } = this.state;
    this.setState({ showUser: !showUser, showUserList: true });
    this.hiddenScroll();
  };

  onCloseShowUser = () => {
    this.setState({ showUser: false, showUserList: true, menuUser: false });
    this.activeScroll();
  };

  onSetShowApps = e => {
    const { activeApps } = this.state;
    e.stopPropagation();
    this.setState({ activeApps: !activeApps, showUserList: true });
  };

  onSetShowNotifications = e => {
    const { activeNotifications } = this.state;
    e.stopPropagation();
    this.setState({
      activeNotifications: !activeNotifications,
      showUserList: true
    });
  };

  onHideNotifications = () => {
    this.setState({ activeNotifications: false, showUserList: true });
  };

  onClickOutsideMenu = () => {
    this.setState({ menuUser: false, showUserList: true });
  };

  onClickOutsideMenuList = () => {
    this.setState({ activeMenuBurguer: false, showUserList: true });
  };

  onClickOutsideApps = () => {
    this.setState({ activeApps: false, showUserList: true });
  };

  onLogout = () => {
    const callbackUrl = window.location.href.replace('logout', '');
    window.location.replace(`${API}logout?redirect=${callbackUrl}`);
  };

  onGoCMS = () => {
    navigateToFile('/cms');
  };

  onGoOffice = () => {
    navigateToFile('/office');
  };

  onClickOutsideMenuUser = ev => {
    ev.stopPropagation();
    this.setState({ menuUser: false, showUserList: true });
    this.activeScroll();
  };

  showMenuUser = ev => {
    const { menuUser } = this.state;
    ev.stopPropagation();
    this.setState({
      menuUser: !menuUser,
      activeApps: false,
      activeNotifications: false,
      showUserList: false
    });
    this.hiddenScroll();
  };

  toggleEditAvatar = editState => {
    this.setState({
      editAvatar: editState
    });
  };

  upateAvatar = idEmployee => {
    const { userId } = this.props.user;
    let pathUser = '';

    if (this.props.location) {
      pathUser = this.props.location.pathname;
    }

    const isProfile = pathUser.indexOf('mi_perfil') !== -1;

    if (isProfile) {
      const arrHref = pathUser.split('/');
      const idProfilePath = Number(arrHref[2]);
      let idProfile = 0;

      if (idEmployee) {
        idProfile = idEmployee;
      } else {
        idProfile = idProfilePath;
      }

      this.setState({
        userProfileView: isProfile,
        isMe: userId === idProfile
      });
    }
  };

  render() {
    const {
      headerMenu,
      showHeader,
      user,
      userProfile,
      fixed,
      query,
      userId,
      className
    } = this.props;
    let withPhoto;

    const { name, lastname, idDepartment, avatarPhoto } = user;
    if (userProfile) {
      withPhoto = userProfile.avatarPhoto;
    }

    const {
      activeMenuBurguer,
      activeMobile,
      menuUser,
      showUser,
      showUserList,
      activeApps,
      activeNotifications,
      editAvatar,
      userProfileView,
      isMe
    } = this.state;

    return (
      headerMenu && (
        <Fragment>
          <StyledHeader
            showHeader={showHeader}
            id="headerHome"
            fixed={fixed}
            className={className}
            userProfileView={userProfileView && !isMe}
          >
            <StyledNav className="Header-nav">
              <StyledInterior>
                <StyledHamburguer
                  id="menuHamburguer"
                  className="Header-hamburger"
                  svg={Menu}
                  onClick={this.onToggleMenuBurguer}
                  hover
                />
                <MenuList
                  onToggleMenuBurguer={this.onToggleMenuBurguer}
                  show={activeMenuBurguer}
                  onToggleMobileMenuCategory={this.onSelectedMenuMobile}
                  showMobileMenuCategory={activeMobile}
                  menu={headerMenu}
                  onClickOutside={this.onClickOutsideMenuList}
                />
                <StyledLinkMobile to="/">
                  <StyledBoxLogo>
                    <StyledLogo svg={Logo} className="Header-logo" />
                  </StyledBoxLogo>
                </StyledLinkMobile>
                <StyledListGeneral
                  active={activeApps}
                  className="Header-listGeneral"
                >
                  <StyledElementsList>
                    <SearchHeaderTop
                      onChangeSearch={this.onChangeSearch}
                      value={query}
                      onFocus={this.onFocusSearch}
                      onToggleSearch={this.onToggleSearch}
                      name={name}
                    />
                  </StyledElementsList>
                  <StyledElementsList>
                    <Icon size="m" svg={Dashboard} hover />
                  </StyledElementsList>
                  <StyledElementsList>
                    <StyledIconBell
                      onClick={this.onSetShowNotifications}
                      svg={Bell}
                      hover
                    />
                    <Notification
                      show={activeNotifications}
                      onClickOutside={this.onHideNotifications}
                    />
                  </StyledElementsList>
                  <StyledElementsList>
                    <StyledIconAppsDesktop
                      size="m"
                      svg={Apps}
                      onClick={this.onSetShowApps}
                      hover
                    />
                    <StyledIconUser
                      size="l"
                      svg={Dots}
                      onClick={this.onSetShowUser}
                      hover
                    />
                    <AppsHeader
                      show={activeApps}
                      onClickOutside={this.onClickOutsideApps}
                      onClickBack={() => {
                        this.setState({ activeApps: false });
                      }}
                    />
                  </StyledElementsList>
                  <StyledElementsList
                    userProfileView={userProfileView && !isMe}
                  >
                    <StyledUser
                      onClick={this.showMenuUser}
                      onClickOutside={this.onClickOutsideMenuUser}
                      userProfileView={userProfileView && !isMe}
                    >
                      <StyledAvatarPerson
                        name={name}
                        lastname={lastname}
                        userId={String(userId)}
                        crop={AVATAR_MEDIUM}
                        avatarPhoto={avatarPhoto}
                      />
                    </StyledUser>
                  </StyledElementsList>
                </StyledListGeneral>
                <UserMenu
                  name={name}
                  lastname={lastname}
                  show={showUser}
                  menuUser={menuUser}
                  onCloseShowUser={this.onCloseShowUser}
                  logout={this.onLogout}
                  onClickOutside={this.onClickOutsideMenu}
                  goCMS={this.onGoCMS}
                  showApps={e => {
                    this.setState({ activeApps: true });
                    this.onCloseShowUser();
                    e.stopPropagation();
                  }}
                  idEmployee={userId}
                  idDepartment={idDepartment}
                  upateAvatar={this.upateAvatar}
                />
              </StyledInterior>
            </StyledNav>
            <BoxLogoAvatar
              logout={this.onLogout}
              goCMS={this.onGoCMS}
              goOffice={this.onGoOffice}
              user={userProfileView && userProfile ? userProfile : user}
              show={showHeader}
              showUserList={showUserList}
              showEditAvatar={() => this.toggleEditAvatar(true)}
              userProfileView={userProfileView}
              isMe={isMe}
              avatarPhoto={userProfileView ? !!withPhoto : !!avatarPhoto}
            />
          </StyledHeader>
          {editAvatar && (
            <ModalAvatar hideEditAvatar={() => this.toggleEditAvatar(false)} />
          )}
        </Fragment>
      )
    );
  }
}

Header.propTypes = {
  showHeader: PropTypes.bool,
  fixed: PropTypes.bool,
  query: PropTypes.string
};

const mapStateToProps = ({
  user: {
    user: { userId }
  },
  header: { searchQuery },
  user
}) => ({
  userId,
  query: searchQuery,
  userProfile: user.userProfile
});

export default connect(mapStateToProps)(Header);
