import React from 'react';
import storiesOf from '../../../../.storybook/util';
import TitleWidget from '.';
import {
  Audiencias,
  MenuDia,
  Usuario,
  Agenda,
  Note,
  Resume,
  EmployeeDirectoryImg
} from '../../../../assets/images/index';

storiesOf('core/TitleWidget', module)
  .add('default', () => (
    <TitleWidget title="shortcut" type="shortcut" icon={Usuario} />
  ))
  .add('Accesos directos', () => (
    <TitleWidget title="shortcut" type="shortcut" icon={Usuario} />
  ))
  .add('Menú del día', () => (
    <TitleWidget title="Menú del día" type="menuofday" icon={MenuDia} />
  ))
  .add('Audiencias', () => (
    <TitleWidget title="Audiencias" type="audiences" icon={Audiencias} />
  ))
  .add('AgendaAudiencias', () => (
    <TitleWidget title="agenda" type="agenda" icon={Agenda} />
  ))
  .add('Notas de Prensa', () => (
    <TitleWidget title="notas de prensa" type="shortcut" icon={Note} />
  ))
  .add('Resumen de Prensa', () => (
    <TitleWidget title="resumen de prensa" type="resumen" icon={Resume} />
  ))
  .add('Audiencias small', () => (
    <TitleWidget title="Audiencias" type="audiences" icon={Audiencias} small />
  ))
  .add('Directorio de empleado small', () => (
    <TitleWidget
      title="Directorio de empleados"
      type="employeeDirectory"
      icon={EmployeeDirectoryImg}
      small
    />
  ));
