import React from 'react';
import PropTypes from 'prop-types';

import Button from '../../core/Buttons';

import { StyledModal } from './style';

const ModalLegalWarning = ({ setaceptLegalConditions, onAceptLegal }) => (
  <StyledModal title="PROTECCIÓN DE DATOS">
    <p className="legal-text">
      Queremos informarte de que el Grupo Atresmedia cuenta con un Modelo de
      Cumplimiento de la normativa de protección de datos, que incluye una serie
      de políticas, protocolos y herramientas de seguimiento y control, que
      recogen los objetivos del Grupo en esta materia y los principios que deben
      regir sus actuaciones, las de sus empleados y colaboradores.
    </p>
    <p className="legal-text">
      En la intranet, en el apartado Documentación relevante puedes consultar
      toda la información, completa y actualizada, sobre el modelo de gobierno,
      los órganos que intervienen en él, funciones y competencias, normas
      internas, políticas y protocolos aplicables, los mecanismos establecidos
      para su adecuado funcionamiento así como recursos formativos en
      privacidad.
    </p>
    <p className="legal-text">
      Te comunicamos que además el Grupo cuenta con una{' '}
      <strong>Delegada de Protección de Datos (DPO)</strong> que vela por el
      cumplimiento de la normativa y con la que se debe consultar cualquier
      aspecto relacionado con la privacidad de los datos y su tratamiento a
      través del correo electrónico:{' '}
      <a href="privacidad@atresmedia.com">privacidad@atresmedia.com</a>{' '}
    </p>
    <div className="center">
      <Button
        className="legal-acept"
        dark
        onClick={() => {
          onAceptLegal();
          setaceptLegalConditions(true);
        }}
      >
        Aceptar
      </Button>
    </div>
  </StyledModal>
);

ModalLegalWarning.propTypes = {
  setaceptLegalConditions: PropTypes.func,
  onAceptLegal: PropTypes.func
};

export default ModalLegalWarning;
