import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  StyledWrapper,
  StyledElementsList,
  StyledArrowRight,
  StyledArrowLeft,
  BoxGeneral,
  StyledTabs,
  ItemSelected
} from './style';

import ButtonActions from './ButtonActions';

class MenuTabs extends Component {
  constructor(props) {
    super(props);

    const { dataMenu } = props;

    const txtItemActiveUpdated =
      dataMenu && dataMenu.find(item => item.route === props.currentPath);

    this.state = {
      isOpen: false,
      posX: 0,
      txtItemActive: txtItemActiveUpdated && txtItemActiveUpdated.name,
      menuWithScroll: false
    };
  }

  componentDidMount() {
    const menuTabs = document.getElementById('menuTabs');
    const widthMenuTabs = menuTabs.clientWidth;
    if (widthMenuTabs > 1040) {
      this.setState({
        menuWithScroll: true
      });
    } else {
      this.setState({
        posX: -widthMenuTabs
      });
    }
  }

  onMouseEnter = e => {
    const { isOpen } = this.state;
    if (
      e.target.className === '' ||
      !e.target.className.indexOf('item') ||
      isOpen === true
    ) {
      return false;
    }
    this.clickMenu();
    return undefined;
  };

  onMouseLeave = e => {
    const { isOpen } = this.state;
    const delay = 1500; // delay al cerrar el menú: 1,5seg
    if (
      e.target.className === '' ||
      !e.target.className.indexOf('item') ||
      isOpen === false
    ) {
      return false;
    }
    setTimeout(() => {
      this.clickMenu();
      return undefined;
    }, delay);
    return null;
  };

  scrollMobile = x => {
    const menuMobile = document.querySelector('#menuTabsMobile');
    menuMobile.scrollLeft = x;
  };

  // animate menu
  clickMenu = () => {
    const menuTabs = document.getElementById('menuTabs');

    const { isOpen } = this.state;

    if (isOpen) {
      // If is open close the menu
      this.setState(
        {
          isOpen: false,
          posX: -menuTabs.clientWidth
        }
        // this.setItemActive()
      );
    } else {
      // If is close open the menu
      this.setState({
        isOpen: true,
        posX: 0
      });
    }
  };

  render() {
    const {
      className,
      color,
      dataMenu,
      clickHandler,
      currentPath,
      textButtonAction,
      fromExternalMenu
    } = this.props;

    const { posX, isOpen, txtItemActive, menuWithScroll } = this.state;
    return (
      <StyledWrapper
        onMouseEnter={menuWithScroll ? null : e => this.onMouseEnter(e)}
        onMouseLeave={menuWithScroll ? null : e => this.onMouseLeave(e)}
        menuWithScroll={menuWithScroll}
      >
        <BoxGeneral
          className={`item ${className}`}
          id="menuTabsMobile"
          menuWithScroll={menuWithScroll}
        >
          <ButtonActions
            selected={isOpen}
            text={textButtonAction}
            menuWithScroll={menuWithScroll}
          />
          {txtItemActive && (
            <ItemSelected
              selected={isOpen}
              text={textButtonAction}
              menuWithScroll={menuWithScroll}
            >
              {fromExternalMenu ? dataMenu[0].name : txtItemActive}
            </ItemSelected>
          )}
          <StyledArrowLeft color={color} />
          <div id="menuTabs">
            <StyledTabs
              selected={isOpen}
              posX={posX}
              menuWithScroll={menuWithScroll}
            >
              {dataMenu.map(item => (
                <StyledElementsList
                  key={item.id}
                  id={item.id}
                  active={item.route === currentPath}
                  clicked={() => {
                    this.clickMenu();
                    clickHandler(item.route);
                    this.setState({ txtItemActive: item.name });
                  }}
                  menuWithScroll={menuWithScroll}
                >
                  {item.name}
                </StyledElementsList>
              ))}
            </StyledTabs>
            <StyledArrowRight color={color} />
          </div>
        </BoxGeneral>
      </StyledWrapper>
    );
  }
}

MenuTabs.propTypes = {
  className: PropTypes.string,
  color: PropTypes.string,
  dataMenu: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      id: PropTypes.string
    })
  ),
  clickHandler: PropTypes.func,
  textButtonAction: PropTypes.string
};

MenuTabs.defaultProps = {
  color: 'grayDark',
  clickHandler: undefined,
  textButtonAction: 'Mis cosas'
};

export default MenuTabs;
