import styled from 'styled-components';
import { rem } from 'polished';
import Icon from '../../core/Icon';
import mediaqueries from '../../../config/themes/variables/mediaqueries';

// Form del Buscador
export const StyledSearch = styled.div`
  display: flex;
  margin-bottom: 0;
  width: 100%;
  ${mediaqueries.greaterThan('desktop')`
    background-color: ${props => props.theme.colors.grayLighter};
    height:100%;
    max-width: ${rem('945px')};
  `};
`;

// Icono de lupa
export const StyledIconSearch = styled(Icon)`
  height: ${rem('34px')};
  margin-right: ${rem('2px')};
  width: ${rem('34px')};
  ${mediaqueries.lessThan('mobile')`
    height: ${rem('24px')};
    margin-right:0;
    width: ${rem('24px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    fill: ${props => props.theme.colors.light};
    height: ${rem('24px')};
    margin-right:0;
    width: ${rem('24px')};
  `};
`;

// Boton de búsqueda
export const StyledButtonSearch = styled.button`
  background-color: transparent;
  border: 0;
  margin-left: auto;
  ${mediaqueries.greaterThan('desktop')`
    display: block;
    background-color: ${props => props.theme.colors.grayDark};
    margin-top:0;
    width: ${rem('72px')};
  `};
`;

// Input del buscador
export const StyledInput = styled.input`
  background-color: transparent;
  border: 0;
  display: none;
  font-size: ${props => props.theme.fontSize.large};
  padding-left: ${rem('35px')};
  padding-right: ${rem('15px')};
  width: 100%;
  &:focus {
    outline: none;
  }
  &::placeholder {
    font-family: ${props => props.theme.fonts.bookItalic};
  }
  ${mediaqueries.greaterThan('desktop')`
    display:block;
    padding-top:${rem('6px')};
    width: calc(100% - ${rem('72px')});
  `};
`;
