import React from 'react';
import { compose, pure, withStateHandlers } from 'recompose';
import PropTypes from 'prop-types';
import { CommentsModel } from '../../../model';
import {
  StyledWrapperComment,
  StyleAnswer,
  StyleButtons,
  StyleDateFormat,
  StyleFormAnswer,
  StyleInfo,
  StyleRead,
  StyleTextComment,
  StyledComment,
  StyledWrapAvatar
} from './styleComment';
import { StyleTextArea } from './styleNewsComments';
import AvatarComments from '../../core/AvatarComments';

const lengthText = 250;

const Comment = ({
  className,
  comment,
  children,
  reply,
  toggleAnswer,
  updateAnswer,
  answerId,
  sendAnswer,
  secondary,
  answer,
  readMore,
  toggleReadMore,
  userId
}) => (
  <StyledWrapperComment className={className} secondary={secondary}>
    <StyledWrapAvatar>
      <AvatarComments
        name={comment.username}
        lastname={comment.userlastname}
        userId={userId}
        avatarPhoto={comment.avatarPhoto}
      />
    </StyledWrapAvatar>
    <StyledComment secondary={secondary}>
      <StyleInfo>
        <StyleDateFormat format="DD/MM/YY">{comment.date}</StyleDateFormat>
        {`${comment.username} ${comment.userlastname}`}
      </StyleInfo>
      <StyleTextComment
        maxlength={lengthText}
        text={comment.text}
        disabled={readMore}
      />

      {reply && (
        <StyleAnswer
          onClick={() => toggleAnswer(comment.id)}
          answerId={answerId === comment.id}
        >
          Responder
        </StyleAnswer>
      )}

      {comment.text.length >= lengthText && (
        <StyleRead onClick={toggleReadMore} inactive={readMore}>
          {readMore ? 'Leer menos' : 'Leer más'}
        </StyleRead>
      )}

      {reply && answerId === comment.id && (
        <StyleFormAnswer>
          <StyleTextArea
            area
            name="responder"
            rows={2}
            multiline
            value={answer}
            onChange={updateAnswer}
            autoFocus
          />
          <StyleButtons ghost onClick={sendAnswer} disabled={!answer}>
            Responder
          </StyleButtons>
        </StyleFormAnswer>
      )}
    </StyledComment>
    {children}
  </StyledWrapperComment>
);

Comment.propTypes = {
  className: PropTypes.string,
  comment: CommentsModel,
  children: PropTypes.node,
  reply: PropTypes.bool,
  secondary: PropTypes.bool,
  toggleAnswer: PropTypes.func,
  updateAnswer: PropTypes.func,
  answerId: PropTypes.string,
  sendAnswer: PropTypes.func,
  answer: PropTypes.string,
  toggleReadMore: PropTypes.func,
  readMore: PropTypes.bool,
  userId: PropTypes.string
};

export default compose(
  withStateHandlers(
    { readMore: false },
    { toggleReadMore: ({ readMore }) => () => ({ readMore: !readMore }) }
  ),
  pure
)(Comment);
