import React from 'react';
import storiesOf from '../../../../.storybook/util';
import EditionIRPF from '.';
import {
  situation,
  geografic,
  laboral,
  pay
} from '../../../utils/mocks/js/irpf';

storiesOf('core/EditionIRPF', module)
  .add('Situación Familiar', () => (
    <EditionIRPF title="Situación Familiar" dataList={situation} />
  ))
  .add('Movilidad geográfica', () => (
    <EditionIRPF title="Movilidad geográfica" dataList={geografic} />
  ))
  .add('Prolongación de actividad laboral', () => (
    <EditionIRPF title="Prolongación de actividad laboral" dataList={laboral} />
  ))
  .add(
    'Pagos por la adquisición o rehabilitación de la vivienda habitual utilizando financiación ajena, con derecho a deducción en el IRPF',
    () => (
      <EditionIRPF
        title="Pagos por la adquisición o rehabilitación de la vivienda habitual utilizando financiación ajena, con derecho a deducción en el IRPF"
        dataList={pay}
      />
    )
  )
  .add('con InputDate', () => (
    <EditionIRPF
      title="Con InputDate"
      dataList={[{ labelPepe: 'Fecha y hora' }]}
    />
  ));
