import React from 'react';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import { SearchByDate } from '.';

const day = '05';
const month = 'Noviembre';
const weekday = 'Miércoles';

storiesOf('views/SearchByDate', module)
  .addDecorator(withReadme(Readme))
  .add('Default', () => (
    <SearchByDate
      day={day}
      month={month}
      weekday={weekday}
      onClick={() => {}}
    />
  ));
