import React from 'react';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import AvatarComments from '.';

const styleDiv = {
  maxWidth: '75px',
  margin: 'auto'
};

storiesOf('core/Avatar/AvatarComments', module)
  .addDecorator(withReadme(Readme))
  .add('sin imagen', () => (
    <div style={styleDiv}>
      <AvatarComments name="Pedro" lastname="López" />
    </div>
  ));
