import styled from 'styled-components';
import { rem } from 'polished';
import mediaqueries from '../../../config/themes/variables/mediaqueries';
import TextSmall from '../TextSmall';
import Cactus from '../../../../assets/images/png/cactus.png';

const StyledWrapperEmpty = styled.div`
  width: 100%;
  background-color: ${props => props.theme.colors.grayLightest};
  padding: ${rem('15px')};

  ${mediaqueries.greaterThan('tablet')`
    position: relative;
    top: ${rem('2px')};
    height: ${rem('230px')};
    display: flex;
    justify-content: center;
    align-items: center;
    background: ${props =>
      props.theme.colors.grayLightest} url(${Cactus}) no-repeat center ${rem(
    '110px'
  )};    
  )};
  `};

  ${mediaqueries.greaterThan('desktop')`
    width: ${rem('800px')};
  `};
`;

const StyledTextSmall = styled(TextSmall)`
  text-align: center;
  max-width: ${rem('265px')};
  margin: 0 auto;
  ${mediaqueries.greaterThan('tablet')`
    margin-top: ${rem('-80px')};
  `};
`;

export { StyledWrapperEmpty, StyledTextSmall };
