import React from 'react';
import storiesOf from '../../../../.storybook/util';
import BoxName from '.';

import company from '../../../../assets/images/avatars/logoempresa.jpg';

storiesOf('core/BoxName', module)
  .add('Name and Day Paragraph Desktop', () => (
    <div style={{ display: 'flex', height: '100px' }}>
      <BoxName
        user="Cristina González"
        paraGrag="Dale a cada día la posibilidad de ser el mejor día de tu vida."
        titleParag="FRASE DEL DÍA:"
      />
    </div>
  ))
  .add('Name, profession and company ', () => (
    <BoxName
      employee
      user="Cristina González"
      profession="Periodista digital"
      company={company}
    />
  ));
