import styled from 'styled-components';
import { rem } from 'polished';
import Modal from '../Modal';
import Buttons from '../Buttons';
import { Checkbox } from '../Checkbox';
import Icon from '../Icon';

const StyledIcon = styled(Icon)`
  background-color: ${props => props.theme.colors.lightLinkedin};
`;

const StyledModal = styled(Modal)`
  background-color: ${props => props.theme.colors.grayLighter};
  padding-bottom: ${rem('15px')};
`;

const IconWrapper = styled.div`
  background-color: ${props => props.theme.colors.lightLinkedin};
  padding: ${rem('10px')};
  margin-left: ${rem('15px')};
`;

const StyledDescription = styled.p`
  color: ${props => props.theme.colors.dark};
  font-family: ${props => props.theme.fonts.normal};
  font-size: ${rem('14px')};
  line-height: 1.2;
  margin-bottom: ${rem('14px')};
  text-align: center;
`;

const StyledItems = styled.li`
  align-items: center;
  background-color: ${props => props.theme.colors.light};
  border: ${rem('1px')} solid ${props => props.theme.colors.grayLighter};
  border-bottom: 0;
  display: flex;
  list-style-type: none;
  margin: 0 auto;
  padding: ${rem('2px')};
  width: 80%;
  &:last-child {
    border-bottom: ${rem('1px')} solid
      ${props => props.theme.colors.grayLighter};
  }
`;

const StyledSpan = styled.span`
  align-items: flex-start;
  color: ${props => props.theme.colors.dark};
  font-family: ${props => props.theme.fonts.normal};
  font-size: ${rem('14px')};
  padding-left: ${rem('5px')};
  width: 97%;
`;

const StyledCheckbox = styled(Checkbox)`
  align-items: flex-end;
  margin-top: ${rem('5px')};
`;

const StyledButton = styled(Buttons)`
  background-color: ${props => props.theme.colors.blueLinkedin};
  display: flex;
  margin: ${rem('15px')} auto;
  padding: 0 0 0 ${rem('15px')};
`;

const StyledFooter = styled(StyledDescription)`
  color: ${props => props.theme.colors.gray};
  font-size: ${rem('12px')};
  margin: ${rem('15px')} 0 0 0;
  text-align: left;
`;

export {
  StyledModal,
  StyledDescription,
  StyledItems,
  StyledSpan,
  StyledCheckbox,
  StyledButton,
  StyledIcon,
  IconWrapper,
  StyledFooter
};
