import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledEdition,
  StyledTitle,
  StyledLabel,
  StyledBoxColum,
  StyledBoxOne,
  StyledBoxTwo,
  StyledBoxThree,
  StyledBoxFour,
  StyledBoxFive,
  StyledBoxSix,
  StyledBoxSeven,
  StyledBoxEight,
  StyledBoxNine,
  StyledBoxTen,
  StyledBoxEleven,
  StyledBoxTwelve,
  StyleInputShort,
  StyledDrop,
  StyledBoxDown
} from './style';

import Button from '../Buttons';
import Input from '../Input';

const EditionPersonalAddress = ({
  className,
  data,
  listStreet,
  labelStreet,
  selectedStreet,
  listArea,
  labelArea,
  selectedArea,
  listCountry,
  labelCountry,
  selectedCountry,
  title
}) => (
  <StyledEdition className={className}>
    <StyledTitle>{title}</StyledTitle>
    <form>
      {data.map((item, key) => (
        <StyledBoxColum>
          <StyledBoxOne>
            <StyledLabel>{item.labelOne}</StyledLabel>
            <StyledDrop
              list={listStreet}
              label={labelStreet}
              selected={selectedStreet}
            />
          </StyledBoxOne>
          <StyledBoxTwo>
            <StyledLabel htmlFor={`${item.labelTwo}-${key + 1}`}>
              {item.labelTwo}
            </StyledLabel>
            <Input
              id={`${item.labelTwo}-${key + 1}`}
              name={`${item.labelTwo}-${key + 1}`}
              value=""
            />
          </StyledBoxTwo>
          <StyledBoxThree>
            <StyledLabel htmlFor={`${item.labelThree}-${key + 1}`}>
              {item.labelThree}
            </StyledLabel>
            <StyleInputShort
              id={`${item.labelThree}-${key + 1}`}
              name={`${item.labelThree}-${key + 1}`}
              value=""
            />
          </StyledBoxThree>
          <StyledBoxFour>
            <StyledLabel id={`${item.labelFour}-${key + 1}`}>
              {item.labelFour}
            </StyledLabel>
            <StyleInputShort
              id={`${item.labelFour}-${key + 1}`}
              name={`${item.labelFour}-${key + 1}`}
              value=""
            />
          </StyledBoxFour>
          <StyledBoxFive>
            <StyledLabel id={`${item.labelFive}-${key + 1}`}>
              {item.labelFive}
            </StyledLabel>
            <StyleInputShort
              id={`${item.labelFive}-${key + 1}`}
              name={`${item.labelFive}-${key + 1}`}
              value=""
            />
          </StyledBoxFive>
          <StyledBoxSix>
            <StyledLabel id={`${item.labelSix}-${key + 1}`}>
              {item.labelSix}
            </StyledLabel>
            <StyleInputShort
              id={`${item.labelSix}-${key + 1}`}
              name={`${item.labelSix}-${key + 1}`}
            />
          </StyledBoxSix>
          <StyledBoxSeven>
            <StyledLabel id={`${item.labelSeven}-${key + 1}`}>
              {item.labelSeven}
            </StyledLabel>
            <StyleInputShort
              id={`${item.labelSeven}-${key + 1}`}
              name={`${item.labelSeven}-${key + 1}`}
              value=""
            />
          </StyledBoxSeven>
          <StyledBoxEight>
            <StyledLabel id={`${item.labelEight}-${key + 1}`}>
              {item.labelEight}
            </StyledLabel>
            <StyleInputShort
              id={`${item.labelEight}-${key + 1}`}
              name={`${item.labelEight}-${key + 1}`}
              value=""
            />
          </StyledBoxEight>
          <StyledBoxNine>
            <StyledLabel id={`${item.labelNine}-${key + 1}`}>
              {item.labelNine}
            </StyledLabel>
            <Input
              id={`${item.labelNine}-${key + 1}`}
              name={`${item.labelNine}-${key + 1}`}
              value=""
            />
          </StyledBoxNine>
          <StyledBoxTen>
            <StyledLabel>{item.labelTen}</StyledLabel>
            <StyledDrop
              list={listArea}
              label={labelArea}
              selected={selectedArea}
            />
          </StyledBoxTen>
          <StyledBoxEleven>
            <StyledLabel>{item.labelEleven}</StyledLabel>
            <StyledDrop
              list={listCountry}
              label={labelCountry}
              selected={selectedCountry}
            />
          </StyledBoxEleven>
          <StyledBoxTwelve>
            <StyledLabel id={`${item.labelTwelve}-${key + 1}`}>
              {item.labelTwelve}
            </StyledLabel>
            <Input
              id={`${item.labelTwelve}-${key + 1}`}
              name={`${item.labelTwelve}-${key + 1}`}
              value=""
            />
          </StyledBoxTwelve>
        </StyledBoxColum>
      ))}
      <StyledBoxDown>
        <Button secondary>Enviar Solicitud</Button>
      </StyledBoxDown>
    </form>
  </StyledEdition>
);

EditionPersonalAddress.propTypes = {
  title: PropTypes.string,
  data: PropTypes.arrayOf(
    PropTypes.shape({
      labelOne: PropTypes.string,
      labelTwo: PropTypes.string,
      labelThree: PropTypes.string
    })
  ),
  listStreet: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      value: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)],
      id: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)]
    })
  ).isRequired,
  listArea: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      value: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)],
      id: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)]
    })
  ).isRequired,
  listCountry: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      value: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)],
      id: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)]
    })
  ).isRequired
};

export default EditionPersonalAddress;
