import React from 'react';
import storiesOf from '../../../../.storybook/util';
import { LayoutColumn, ItemLayoutColumn } from '.';
import NewsItem from '../NewsItem';
import { dataNews } from '../../../utils/mocks/js';

storiesOf('core/LayoutColumn', module).add('Default', () => (
  <LayoutColumn>
    {dataNews.map(item => (
      <ItemLayoutColumn>
        <NewsItem
          link={item.link}
          title={item.title}
          dateFrom={item.dateFrom}
          author={item.author}
          mainTagName={item.mainTagName}
          mediaType={item.mediaType}
          subtitle={item.subtitle}
          orientation={item.orientation}
          image={item.image}
          videoNews={item.sources}
          onToggleFullSize={undefined}
          onLike={undefined}
          onFavorite={undefined}
          onShare={undefined}
          onComment={undefined}
          favorite={false}
          like={false}
          share={undefined}
          onClickMobile={undefined}
          activeMobile={false}
          numberOfComments={item.numberOfComments}
          readAfter
        />
      </ItemLayoutColumn>
    ))}
  </LayoutColumn>
));
