import React from 'react';
import storiesOf from '../../../../.storybook/util';
import BoxEmployee from '.';

const styleItem = {
  maxWidth: '360px',
  margin: 'auto'
};

storiesOf('core/BoxEmployee', module).add('BoxEmployee', () => (
  <div>
    <div style={styleItem}>
      <h2>Usuario no logado</h2>
      <BoxEmployee title="Un título de prueba">
        <ul>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
        </ul>
      </BoxEmployee>
    </div>
    <br />
    <br />
    <div style={styleItem}>
      <h2>Usuario logado</h2>
      <BoxEmployee title="Un título de prueba" userLogged>
        <ul>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
        </ul>
      </BoxEmployee>
    </div>
    <div style={styleItem}>
      <h2>Con más item</h2>
      <BoxEmployee title="Un título de prueba" more>
        <ul>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
          <li>Opción de lista</li>
        </ul>
      </BoxEmployee>
    </div>
  </div>
));
