import styled from 'styled-components';
import { rem } from 'polished';
import BigCalendar from 'react-big-calendar';
import estilosBase from 'react-big-calendar/lib/css/react-big-calendar.css';

import FoodPaybill from '../../../core/FoodPaybill';

import mediaqueries from '../../../../config/themes/variables/mediaqueries';

const WrapperFlex = styled.div`
  display: flex;
  flex-direction: column;
  max-width: ${rem('1104px')};
  ${mediaqueries.greaterThan('tablet')`
    padding: 0 ${rem('24px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    padding: ${rem('58px')} 0 0;
    flex-direction: row;
    justify-content: space-between;
  `};
`;

const WrapperFoodPayFlex = styled.div`
  display: flex;
  justify-content: space-between;
  ${mediaqueries.greaterThan('tablet')`
    justify-content: flex-start;
  `};
  ${mediaqueries.greaterThan('desktop')`
    flex-direction: column;
    width: ${rem('386px')};
  `};
`;

const WrapperCoupons = styled.div`
  padding: ${rem('20px')} 0;
`;

const WrapperCalendar = styled.div`
  width: auto;
  height: ${rem('456px')};
  overflow: hidden;
  ${mediaqueries.greaterThan('tablet')`
    height: ${rem('658px')};
    flex-grow: 1;    
  `};
  ${mediaqueries.greaterThan('desktop')`
    max-width: ${rem('580px')};
  `};
`;

const TitleFoodPay = styled.h2`
  font-size: ${rem('14px')};
  font-family: ${props => props.theme.fonts.bold};
  text-transform: uppercase;
  letter-spacing: 0;
  color: ${props => props.theme.colors.dark};
`;

const StyledFoodPayAvalaible = styled(FoodPaybill)`
  width: 47%;
  ${mediaqueries.greaterThan('tablet')`
    margin-right: ${rem('35px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    margin-right: 0;
    margin-bottom: ${rem('20px')};
    width: 100%;
  `};
`;

const StyledFoodPayConsumed = styled(FoodPaybill)`
  width: 47%;
  ${mediaqueries.greaterThan('desktop')`
    width: 100%;
  `};
`;

const StyleBigCalendarFood = styled(BigCalendar)`
  ${estilosBase};
  font-family: ${props => props.theme.fonts.medium};
  font-size: ${rem('12px')};
  height: ${rem('347px')};
  transition: transform 2s ease-in-out;
  max-width: ${rem('880px')};

  .rbc-toolbar-label {
    font-family: ${props => props.theme.fonts.medium};
    font-size: ${rem('20px')};
    text-transform: uppercase;
  }
  .rbc-month-header {
    background-color: ${props => props.theme.colors.grayLighter};
  }

  .rbc-header {
    border: none;
    font-size: inherit;
    font-weight: normal;
    padding: 0 0 0 ${rem('10px')};
    text-align: left;
    text-transform: uppercase;

    span {
      color: ${props => props.theme.colors.grayDarkest};
    }
  }

  .rbc-header + .rbc-header {
    border: none;
  }

  .rbc-row-bg {
    background-color: ${props => props.theme.colors.grayLightest};
  }

  .rbc-month-row + .rbc-month-row,
  .rbc-day-bg + .rbc-day-bg {
    border-color: ${props => props.theme.colors.light};
  }

  .rbc-month-row {
    max-height: ${rem('52px')};
    ${mediaqueries.greaterThan('mobile')`
      max-height: inherit;
    `};
  }

  .rbc-date-cell {
    font-size: ${rem('14px')};
    padding: ${rem('5px')} 0 0 0;
    text-align: center;
    ${mediaqueries.greaterThan('mobile')`
      font-size: ${rem('16px')};
      padding: ${rem('10px')} 0 0 ${rem('10px')};
      text-align: left;
    `};
  }

  .rbc-today {
    background-color: ${props => props.theme.colors.grayLight};
  }

  .rbc-event {
    background-color: transparent;
    padding: 0 ${rem('10px')};
  }

  .rbc-event.rbc-selected {
    background-color: ${props => props.theme.colors.grayLight};
    border-radius: 0;
    p {
      color: ${props => props.theme.colors.grayDarkest};
      :before {
        background-color: ${props => props.theme.colors.activeBlue};
        border: ${rem('2px')} solid ${props => props.theme.colors.success};
      }
    }
  }

  .rbc-date-cell.rbc-now {
    font-weight: normal;
  }

  .rbc-show-more {
    background-color: transparent;
    color: ${props => props.theme.colors.grayDarkest};
    font-family: ${props => props.theme.fonts.medium};
    font-size: ${rem('12px')};
    font-weight: normal;
    padding: ${rem('5px')} ${rem('10px')};
    display: none;
    ${mediaqueries.greaterThan('mobile')`
      display: block;
    `};
  }

  .rbc-selected-cell {
    background-color: ${props => props.theme.colors.grayLighter};
  }
  .rbc-off-range-bg {
    background-color: transparent;
  }
  .rbc-off-range {
    color: ${props => props.theme.colors.gray};
  }
  .rbc-current {
    background-color: ${props => props.theme.colors.grayLight};
    color: ${props => props.theme.colors.grayDarkest};
    font-family: ${props => props.theme.fonts.medium};
    position: relative;
    :after {
      background-color: ${props => props.theme.colors.grayLight};
      content: '';
      height: 370%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
      z-index: -1;
    }
  }

  .rbc-event-content {
    text-align: center;
    ${mediaqueries.greaterThan('mobile')`
      text-align: left;
    `};
  }

  ${mediaqueries.greaterThan('tablet')`
   height: ${rem('600px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
   width:100%;
  `};
`;

const StyleEventTitle = styled.p`
  color: ${props => props.theme.colors.dark};
  display: inline;
  font-size: ${rem('12px')};
  font-weight: normal;
  margin: 0;
  max-width: 100%;
  overflow: hidden;
  padding: 10px 0 0 10px;
  position: relative;
  text-overflow: ellipsis;
  white-space: nowrap;

  :before {
    background-color: ${props => props.theme.colors.activeBlue};
    border: ${rem('2px')} solid ${props => props.theme.colors.success};
    border-radius: 50%;
    content: '';
    height: 6px;
    left: 0;
    position: absolute;
    top: 10px;
    width: 6px;
  }

  ${mediaqueries.greaterThan('mobile')`
    display: block;
    padding: 0 0 0 10px;
  `};
`;
const StyleEventText = styled.span`
  display: none;

  ${mediaqueries.greaterThan('mobile')`
    display: inline-block;
    cursor: default;
  `};
`;

export {
  WrapperFlex,
  WrapperFoodPayFlex,
  WrapperCoupons,
  WrapperCalendar,
  TitleFoodPay,
  StyledFoodPayAvalaible,
  StyledFoodPayConsumed,
  StyleBigCalendarFood,
  StyleEventTitle,
  StyleEventText
};
