import React from 'react';
import storiesOf from '../../../../.storybook/util';
import Input from '.';

storiesOf('UI Kit/Input', module)
  .add('Default', () => <Input />)
  .add('Placeholder', () => <Input placeholder="Placeholder..." />)
  .add('Desabled', () => <Input disabled placeholder="Inactivo" />)
  .add('Normal inválido', () => (
    <Input placeholder="Escribe algo..." error="Valor inválido" />
  ))
  .add('Text Area', () => (
    <Input area width="200px" multiline placeholder="Con width" />
  ))
  .add('Text Area disabled', () => (
    <Input area multiline rows={5} placeholder="Inactivo" disabled />
  ))
  .add('Text Area Error', () => (
    <Input
      area
      multiline
      placeholder="Escribe algo..."
      error="Valor inválido"
    />
  ));
