import React from 'react';
import PropTypes from 'prop-types';

import {
  StyledLabel,
  StyledBoxModuleRow,
  StyledRadioButton,
  StyledOrderItem,
  StyledIcon,
  StyledBoxRaLabel
} from './style';
import {
  StyleWrapIcon,
  StyledBoxInputRow,
  StyledWrapInputs,
  StyledBoxTwoComponents,
  StyledBoxInputRowTwoBis
} from './styleAwards';

import Input from '../../../core/Input';
import { LANGUAGE } from '../../../../constants/literals/modals';
import { Trash } from '../../../../../assets/images/index';

const LanguageItem = ({
  certification,
  listNivel,
  name,
  onChangeInputName,
  onChangeInputProficiency,
  onChangeRadio,
  onClickDelete,
  onClickDown,
  onClickUp,
  order,
  proficiency
}) => (
  <StyledBoxModuleRow>
    <StyledOrderItem
      order={order}
      onClickDown={onClickDown}
      onClickUp={onClickUp}
    />
    <StyledWrapInputs>
      <StyledBoxInputRow>
        <StyledLabel>{LANGUAGE.CERTIFICATION}</StyledLabel>
        <Input onChange={onChangeInputProficiency} value={certification} />
      </StyledBoxInputRow>
      <StyledBoxInputRowTwoBis>
        <StyledBoxTwoComponents>
          <StyledLabel>{LANGUAGE.NAME}</StyledLabel>
          <Input onChange={onChangeInputName} value={name} />
        </StyledBoxTwoComponents>
        <StyledBoxTwoComponents>
          <StyledLabel>{LANGUAGE.PROFICIENCY}</StyledLabel>
          <StyledBoxRaLabel>
            {listNivel.map(item => (
              <StyledRadioButton
                key={`${item.value}`}
                id={`${item.value}-${order}`}
                checked={proficiency === item.value}
                label={item.text}
                value={item.value}
                onChange={onChangeRadio}
              />
            ))}
          </StyledBoxRaLabel>
        </StyledBoxTwoComponents>
      </StyledBoxInputRowTwoBis>
    </StyledWrapInputs>
    <StyleWrapIcon>
      <StyledIcon svg={Trash} size="m" hover onClick={onClickDelete} />
    </StyleWrapIcon>
  </StyledBoxModuleRow>
);

LanguageItem.propTypes = {
  certification: PropTypes.string,
  listNivel: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string,
      text: PropTypes.string
    })
  ),
  name: PropTypes.string,
  onChangeInputName: PropTypes.func,
  onChangeInputProficiency: PropTypes.func,
  onChangeRadio: PropTypes.func,
  onClickDelete: PropTypes.func,
  onClickDown: PropTypes.func,
  onClickUp: PropTypes.func,
  order: PropTypes.number,
  proficiency: PropTypes.string
};

LanguageItem.defaultProps = {
  certification: '',
  name: ''
};

export default LanguageItem;
