import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledDocuments,
  StyledDocumentsList,
  StyledDocumentsItem,
  StyledDocumentsDownload,
  IconDocuments,
  StyledDocumentsName
} from './style';
import {
  Doc,
  Xls,
  Ppt,
  Pdf,
  Jpg,
  Folder,
  Gen
} from '../../../../assets/images';

import Breadcrumb from '../../core/Breadcrumb';

const getDocumentIcon = extension => {
  switch (extension) {
    case 'DOC':
      return Doc;
    case 'PDF':
      return Pdf;
    case 'XLS':
      return Xls;
    case 'JPG':
      return Jpg;
    case 'PPT':
      return Ppt;
    case 'FOLDER':
      return Folder;
    default:
      return Gen;
  }
};

const Documents = ({ files, breadcrumb, onBreadcrumbClick, onFileClick }) => (
  <StyledDocuments>
    <Breadcrumb dataList={breadcrumb} onClick={onBreadcrumbClick} />
    <StyledDocumentsList>
      {files &&
        files.map(doc => {
          const { id, fileExtension, title } = doc;
          return (
            <StyledDocumentsItem key={id}>
              <StyledDocumentsDownload onClick={() => onFileClick(doc)}>
                <IconDocuments svg={getDocumentIcon(fileExtension)} />
                <StyledDocumentsName>{title}</StyledDocumentsName>
              </StyledDocumentsDownload>
            </StyledDocumentsItem>
          );
        })}
    </StyledDocumentsList>
  </StyledDocuments>
);

Documents.propTypes = {
  files: PropTypes.arrayOf(
    PropTypes.shape({
      fileName: PropTypes.string,
      fileExtension: PropTypes.string,
      folderName: PropTypes.string,
      clientPath: PropTypes.string
    })
  ),
  breadcrumb: PropTypes.arrayOf(
    PropTypes.shape({
      txt: PropTypes.string,
      id: PropTypes.number
    })
  ),
  onBreadcrumbClick: PropTypes.func,
  onFileClick: PropTypes.func
};

export default Documents;
