import styled from 'styled-components';
import { rem } from 'polished';
import Icon from '../Icon';

const StyledBlockquote = styled.blockquote`
  position: relative;
  margin: ${rem('32px')} 0 ${rem('22px')} 0;
  padding: ${rem('24px')} ${rem('18px')} ${rem('22px')} ${rem('80px')};
  font-size: ${rem('18px')};
  line-height: ${rem('20px')};
  background-color: ${props => props.theme.colors.grayLightest};
`;

const StyledTitleBlockquote = styled.h4`
  font-family: ${props => props.theme.fonts.medium};
  margin-bottom: ${rem('8px')};
  text-transform: uppercase;
`;

const StyledIconBlockquote = styled(Icon)`
  fill: ${props => props.theme.colors.gray};
  left: ${rem('9px')};
  position: absolute;
  top: 0;
  width: ${rem('56px')};
`;

export { StyledBlockquote, StyledTitleBlockquote, StyledIconBlockquote };
