import React from 'react';
import {
  StyledModal,
  StyledDescription,
  StyledItems,
  StyledSpan,
  StyledCheckbox,
  StyledButton,
  StyledIcon,
  IconWrapper,
  StyledFooter
} from './style';
import { Linkedin } from '../../../../assets/images/index';

const LinkedInImport = () => (
  <StyledModal title="selecciona los datos que quieres importar">
    <StyledDescription>
      ¡Enhorabuena!Tus cuentas se han conectado correctamente.El último paso es
      escoger los datos que quieres importar.
    </StyledDescription>
    <StyledItems>
      <StyledSpan>Idiomas</StyledSpan>
      <StyledCheckbox />
    </StyledItems>
    <StyledItems>
      <StyledSpan>Intereses</StyledSpan>
      <StyledCheckbox />
    </StyledItems>
    <StyledItems>
      <StyledSpan>Voluntariados</StyledSpan>
      <StyledCheckbox />
    </StyledItems>
    <StyledItems>
      <StyledSpan>Premios</StyledSpan>
      <StyledCheckbox />
    </StyledItems>
    <StyledItems>
      <StyledSpan>Skills</StyledSpan>
      <StyledCheckbox />
    </StyledItems>
    <StyledItems>
      <StyledSpan>Formación</StyledSpan>
      <StyledCheckbox />
    </StyledItems>
    <StyledButton>
      importar datos
      <IconWrapper>
        <StyledIcon color="light" size="m" svg={Linkedin} />
      </IconWrapper>
    </StyledButton>
    <StyledFooter>
      AVISO LEGAL: Lorem ipsum dolor sit amet, consectetur adipiscing elit.
      Praesent gravida q, quis ultrices felis consectetur adipiscing elit.
    </StyledFooter>
  </StyledModal>
);

export default LinkedInImport;
