import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledBoxNav,
  StyledListNav,
  StyledListTitle,
  StyledBoxList,
  StyledList,
  StyledIconDesplegated,
  StyledListLink,
  StyledBoxLogo,
  StyledIconClose,
  StyledBoxScroll,
  StyledLogoLateral,
  StyledMenu
} from './MenuListStyle';
import { Arrow, Close } from '../../../../assets/images/index';
import LogoLateral from '../../../../assets/images/logolateral.svg';

/** HOC */
import withOutsideClick from '../Common/ClickOutside';

const MenuList = ({
  show,
  showMobileMenuCategory,
  onToggleMenuBurguer,
  onToggleMobileMenuCategory,
  menu
}) => (
  <StyledMenu id="showMenuBurguer" show={show}>
    <StyledBoxNav>
      <StyledIconClose
        id="closeMenuBurguer"
        svg={Close}
        onClick={onToggleMenuBurguer}
      />
      <StyledBoxLogo>
        <StyledLogoLateral svg={LogoLateral} />
      </StyledBoxLogo>
      <StyledBoxScroll>
        {menu.map(({ categories, categoryName, id }) => (
          <StyledListNav key={id}>
            <StyledListTitle
              onClick={() => onToggleMobileMenuCategory(id)}
              open={id === showMobileMenuCategory}
            >
              {categoryName}
              <StyledIconDesplegated size="xs" color="light" svg={Arrow} />
            </StyledListTitle>
            <StyledBoxList>
              {categories.map(({ page, title, url }) => (
                <StyledList key={page}>
                  <StyledListLink href={url} target="_blank">
                    {title}
                  </StyledListLink>
                </StyledList>
              ))}
            </StyledBoxList>
          </StyledListNav>
        ))}
      </StyledBoxScroll>
    </StyledBoxNav>
  </StyledMenu>
);

MenuList.propTypes = {
  onToggleMenuBurguer: PropTypes.func,
  show: PropTypes.bool,
  onToggleMobileMenuCategory: PropTypes.func,
  showMobileMenuCategory: PropTypes.number
};

export default withOutsideClick(MenuList);
