import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { initialsString } from '../../../utils/initialsString';
import { API } from '../../../constants/api';
import {
  StyledHeaderBox,
  StyledBoxName,
  StyledLogo,
  Styledleft,
  StyledLogoMobile,
  StyledList,
  StyledRazor,
  StyledAvatar,
  StyledBoxLateral
} from './BoxLogoAvatarStyle';

import { Logout, Razor, ColapseBorder } from '../../../../assets/images';
import Logo from '../../../../assets/images/logolateral.svg';
import LogoMobile from '../../../../assets/images/logoresponsive.svg';

import company from '../../../../assets/images/avatars/logoempresa.jpg';

/** Components */
import { StyledIcon } from '../../core/MenuBox/style';

import { accentsTidy } from '../../../utils/accents';

import { selectAvatar, selectAvatarAxis } from '../../../constants/avatarIcons';

const options = [
  { name: 'Nóminas', action: 'WIDGET_DOWN' },
  { name: 'Contactos', action: 'WIDGET_DOWN' },
  { name: 'Grupos', action: 'WIDGET_DOWN' },
  { name: 'Agenda', action: 'WIDGET_DOWN' },
  { name: 'Ir al CMS', action: 'WIDGET_DOWN' },
  { name: 'Salir', action: 'WIDGET_DOWN', icon: Logout }
];
class BoxLogoAvatar extends React.Component {
  constructor() {
    super();

    this.state = {
      show: false
    };
  }

  toggleOptions = () => {
    const { show } = this.state;
    this.setState({
      show: !show
    });
  };

  hideList = () => {
    this.setState({
      show: false
    });
  };

  render() {
    const {
      employee,
      open,
      logout,
      goCMS,
      goOffice,
      user,
      showUserList,
      showEditAvatar,
      userProfileView,
      isMe,
      avatarPhoto
    } = this.props;

    const { show } = this.state;
    const { name, lastname, idDepartment, userId } = user;

    const firstLetter = accentsTidy(initialsString(name, 1));
    const secondLetter = accentsTidy(initialsString(lastname, 1));

    return (
      <StyledHeaderBox>
        <Styledleft>
          {!employee && (
            <div>
              <StyledRazor svg={Razor} />
              <Link to="/">
                <StyledLogoMobile svg={LogoMobile} />
              </Link>
            </div>
          )}
          <Link to="/">
            <StyledLogo svg={Logo} />
          </Link>
        </Styledleft>
        <StyledBoxLateral>
          <StyledBoxName
            employee={employee}
            open={open}
            user={name}
            company={company}
            onClick={this.toggleOptions}
            userProfileView={userProfileView && !isMe}
          >
            {(!userProfileView || isMe) && (
              <StyledIcon
                color="main"
                svg={ColapseBorder}
                onClick={e => {
                  e.stopPropagation();
                  this.toggleOptions();
                }}
              />
            )}
            <StyledList
              onClickOption={this.toggleOptions}
              options={options}
              logout={logout}
              goCMS={goCMS}
              goOffice={goOffice}
              onClickOutside={this.hideList}
              show={show && showUserList}
              idDepartment={idDepartment}
              idEmployee={userId}
            />
          </StyledBoxName>
          <StyledAvatar
            type={selectAvatarAxis(user.avatarId)}
            imgAvatar={selectAvatar(user.avatarId)}
            name={`${firstLetter}${secondLetter}`}
            showEditAvatar={showEditAvatar}
            editAvatar={!userProfileView || isMe}
            perfil={`${API}images/avatar/${user && user.userId}/128x128.jpg`}
            avatarPhoto={avatarPhoto}
          />
        </StyledBoxLateral>
      </StyledHeaderBox>
    );
  }
}

BoxLogoAvatar.propTypes = {
  open: PropTypes.bool,
  employee: PropTypes.bool,
  showEditAvatar: PropTypes.func,
  userProfileView: PropTypes.bool,
  avatarPhoto: PropTypes.bool
};

BoxLogoAvatar.defaultProps = {
  open: false,
  employee: false
};

export default BoxLogoAvatar;
