import React from 'react';
import PropTypes from 'prop-types';

import { ElementList, ActionButton, Link } from './styleElementDropdown';

const ElementDropdown = ({ className, text, onClick, href }) => {
  const Action = href ? Link : ActionButton;
  return (
    <ElementList className={className}>
      <Action onClick={onClick}>{text}</Action>
    </ElementList>
  );
};

ElementDropdown.propTypes = {
  className: PropTypes.string,
  text: PropTypes.string,
  onClick: PropTypes.func,
  href: PropTypes.string
};

export default ElementDropdown;
