import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Comentarios, Arrow } from '../../../../assets/images';

import { CommentsModel } from '../../../model';
import Comment from './Comment';
import InfiniteScroller from '../../core/InfiniteScroller';
import {
  StyleButtonArrow,
  StyleButtonsSend,
  StyleForm,
  StyleNumComments,
  StyleTextArea,
  StyleWrapComments,
  StyleWrapCommentsBox,
  StyleWrapTitle,
  StyleWrapTotalComments,
  StyledIcon,
  StyledIconArrowDown,
  StyledIconArrowUp,
  StyledTitle
} from './styleNewsComments';
import { showReplies, hideReplies } from './helpers';

class NewsComments extends Component {
  constructor(props) {
    super(props);

    this.loadReplies = this.loadReplies.bind(this);
  }

  componentDidMount() {
    const {
      location: { hash }
    } = this.props;
    const height = this.totalComments.offsetTop - 70;

    if (hash === '#comments') {
      setTimeout(() => {
        window.scrollTo(0, height);
      }, 300);
    }
  }

  loadReplies(id, e) {
    const { loadAllReplies } = this.props;

    loadAllReplies(id);
    showReplies(e);
  }

  render() {
    const {
      id,
      page,
      comments,
      form,
      updateForm,
      submitForm,
      toggleAnswer,
      answerId,
      answer,
      updateAnswer,
      sendAnswer,
      loadMoreComments,
      numComments
    } = this.props;

    return (
      <StyleWrapTotalComments
        innerRef={data => {
          this.totalComments = data;
        }}
      >
        <StyleWrapComments>
          <StyleWrapCommentsBox>
            <div>
              <StyleWrapTitle>
                <StyledIcon size="l" svg={Comentarios} />
                <StyledTitle>Comentarios</StyledTitle>
              </StyleWrapTitle>
              <StyleForm>
                <StyleTextArea
                  placeholder="Añadir un comentario..."
                  name="comment"
                  onChange={updateForm}
                  value={form.comment}
                  area
                  rows={2}
                  // error=""
                  multiline
                />
                <StyleButtonsSend
                  type="submit"
                  onClick={submitForm}
                  disabled={!form.comment}
                >
                  Enviar
                </StyleButtonsSend>
              </StyleForm>
            </div>
            <div id="comments">
              <StyleNumComments>{numComments} Comentarios</StyleNumComments>
              <InfiniteScroller
                page={page}
                loadMore={() => loadMoreComments(id)}
              >
                {comments.map(comment => (
                  <Comment
                    comment={comment}
                    key={comment.id}
                    answer={answer}
                    answerId={answerId}
                    reply
                    toggleAnswer={toggleAnswer}
                    updateAnswer={updateAnswer}
                    sendAnswer={sendAnswer}
                    userId={String(comment.userId)}
                  >
                    {comment.replies.map(item => (
                      <Comment
                        key={item.replyId || item.id}
                        comment={item}
                        reply={false}
                        secondary
                        className="reply"
                        userId={String(item.userId)}
                      />
                    ))}

                    <StyleButtonArrow
                      onClick={e => {
                        this.loadReplies(comment.id, e);
                      }}
                      className={
                        comment.numReplies < 2 &&
                        comment.numReplies === comment.replies.length
                          ? `btnShow hide`
                          : `btnShow`
                      }
                    >
                      Ver las {comment.numReplies} respuestas
                      <StyledIconArrowDown svg={Arrow} />
                    </StyleButtonArrow>

                    <StyleButtonArrow
                      onClick={e => hideReplies(e)}
                      className="btnHide hide"
                    >
                      Ocultar respuestas
                      <StyledIconArrowUp svg={Arrow} />
                    </StyleButtonArrow>
                  </Comment>
                ))}
              </InfiniteScroller>
            </div>
          </StyleWrapCommentsBox>
        </StyleWrapComments>
      </StyleWrapTotalComments>
    );
  }
}

NewsComments.propTypes = {
  id: PropTypes.string,
  page: PropTypes.number,
  comments: PropTypes.arrayOf(CommentsModel),
  form: PropTypes.shape({
    comment: PropTypes.string
  }),
  updateForm: PropTypes.func,
  submitForm: PropTypes.func,
  toggleAnswer: PropTypes.func,
  answerId: PropTypes.string,
  answer: PropTypes.string,
  updateAnswer: PropTypes.func,
  sendAnswer: PropTypes.func,
  loadAllReplies: PropTypes.func,
  loadMoreComments: PropTypes.func,
  numComments: PropTypes.number
};

export default NewsComments;
