import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledChannelTitle,
  StyledData,
  StyledGoldMinute,
  StyledSpan,
  StyledSpanRate,
  StyledTime
} from './style';
import ContentGrouping from '../ContentGrouping/ContentGrouping';

const GoldMinute = ({ imgSrc, rate, title, audience, time }) => (
  <ContentGrouping title="minuto de oro">
    <StyledGoldMinute>
      <img src={imgSrc} alt="Canal" />
      <StyledData>
        <StyledChannelTitle>{title}</StyledChannelTitle>
        <StyledSpanRate>{rate}</StyledSpanRate>
        <StyledSpan>Espectadores: {audience}</StyledSpan>
        <StyledTime>{time}</StyledTime>
      </StyledData>
    </StyledGoldMinute>
  </ContentGrouping>
);

GoldMinute.propTypes = {
  imgSrc: PropTypes.string,
  title: PropTypes.string,
  rate: PropTypes.string,
  audience: PropTypes.string,
  time: PropTypes.string
};

export default GoldMinute;
