import React from 'react';
import {
  StyledPopupWrap,
  StyledPopupTitle,
  StyledPopupDescription,
  StyledPopupButton
} from './style';
import Modal from '../Modal';
import Input from '../Input';

const PopupPassword = () => (
  <Modal>
    <StyledPopupWrap>
      <StyledPopupTitle>confirma tu contraseña</StyledPopupTitle>
      <StyledPopupDescription>
        Introduce la contraseña para iniciar la descarga
      </StyledPopupDescription>
      <Input type="text" value="Contraseña" />
      <StyledPopupButton secondary>descargar</StyledPopupButton>
    </StyledPopupWrap>
  </Modal>
);

export default PopupPassword;
