import styled, { css } from 'styled-components';
import { rem } from 'polished';

const StyledTooltipBasic = styled.div`
  position: absolute;
  bottom: ${rem('-35px')};
  left: ${rem('-29px')};
  background-color: #000;
  color: #fff;
  padding: ${rem('6px')} ${rem('7px')};
  width: ${rem('145px')};

  &:before {
    content: '';
    width: 0;
    height: 0;
    border-bottom: ${rem('10px')} solid #000;
    border-left: ${rem('8px')} solid transparent;
    border-right: ${rem('8px')} solid transparent;
    position: absolute;
    top: ${rem('-7px')};
    left: ${rem('32px')};
  }

  p {
    margin-bottom: 0;
    font-size: ${rem('14px')};
    font-family: ${props => props.theme.fonts.medium};
  }
`;

const StyledTooltip = styled.div`
  position: relative;

  & ${StyledTooltipBasic} {
    ${props =>
      props.active
        ? css`
            display: block;
          `
        : css`
            display: none;
          `};
  }
`;

export { StyledTooltip, StyledTooltipBasic };
