import styled from 'styled-components';
import { rem } from 'polished';
import mediaqueries from '../../../../config/themes/variables/mediaqueries';
import DropdownInput from '../../../core/DropdownInput';
import Button from '../../../core/Buttons';

const StyledWithholding = styled.div`
  width: 100%;
  display: block;
  background-color: ${props => props.theme.colors.grayLightest};
  padding: ${rem('30px')};
`;

const StyledTitle = styled.h3`
  font-weight: bold;
  font-size: ${rem('14px')};
  margin: 0 0 ${rem('30px')};
`;

const StyledWithholdingContent = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  max-width: ${rem('120px')};
  ${mediaqueries.greaterThan('480px')`
    flex-wrap: nowrap;
  `};
`;

const StyledBtnContent = styled.div`
  margin: ${rem('25px')} 0 0;
`;

const StyledDropdown = styled(DropdownInput)`
  ${mediaqueries.greaterThan('480px')`
    flex: 1;
  `};
`;

const StyledLabel = styled.span`
  font-weight: bold;
  font-size: ${rem('14px')};
  margin-right: ${rem('10px')};
`;

const StyledButton = styled(Button)`
  font-size: ${rem('14px')};
  height: ${rem('32px')};
  line-height: ${rem('8px')};
`;

export {
  StyledWithholding,
  StyledTitle,
  StyledWithholdingContent,
  StyledBtnContent,
  StyledDropdown,
  StyledLabel,
  StyledButton
};
