import React, { useState, useRef } from 'react';
import Circle from './circle';
import { StyledWrapper, BoxImg, Box, StyledWrapperBox } from './style';
import { AvatarEdition } from '../../../core/AvatarEdition';
import Button from '../../../core/Buttons';
import Icon from '../../../core/Icon';
import { ArrowLeft } from '../../../../../assets/images';

import { API } from '../../../../constants/api';

const ChangeImage = props => {
  const imgAt = useRef();
  const inputFile = useRef();
  const box = useRef();
  const [control, setControl] = useState(false);
  const [img, setImg] = useState(
    `${API}images/avatar/${
      props.user.userId
    }/default?update=${new Date().getTime()}`
  );
  const [imgSend, setImgSend] = useState([]);
  const [diametre, setDiametre] = useState(200);
  const [top, setTop] = useState(0);
  const [left, setLeft] = useState(0);
  const [createCropsResponse, setCreateCropsResponse] = useState(null);

  function updateDisplay(event) {
    const ele = box.current;
    setLeft(event.pageX - ele.offsetLeft - diametre / 2);
    setTop(event.pageY - ele.offsetTop - diametre / 2);
  }

  const preview = () => {
    const preview = imgAt.current;
    const file = inputFile.current.files[0];
    const reader = new FileReader();

    reader.onloadend = () => {
      preview.src = reader.result;
    };

    if (file) {
      reader.readAsDataURL(file);
    } else {
      preview.src = '';
    }
  };

  const sendFile = () => {
    const { uploadAvatarImage, createCrops } = props;
    const realTop =
      (imgAt.current.naturalHeight * top) / box.current.offsetHeight;
    const realLeft =
      (imgAt.current.naturalWidth * left) / box.current.offsetWidth;
    const realDiam =
      (imgAt.current.naturalWidth * diametre) / box.current.offsetWidth;

    const formData = new FormData();

    if (imgSend.length > 0) {
      const type = imgSend[0].type.slice(
        imgSend[0].type.indexOf('/') + 1,
        imgSend[0].type.length
      );
      const imageTypes = {
        jpeg: 'jpg',
        png: 'png'
      };

      Array.from(imgSend).forEach(image => {
        formData.append('file', image);
      });

      uploadAvatarImage(formData, imageTypes[type], {
        offsetX: parseInt(realLeft, 0),
        offsetY: parseInt(realTop, 0),
        sizeX: parseInt(realDiam, 0),
        sizeY: parseInt(realDiam, 0)
      });
    } else {
      createCrops({
        offsetX: parseInt(realLeft, 0),
        offsetY: parseInt(realTop, 0),
        sizeX: parseInt(realDiam, 0),
        sizeY: parseInt(realDiam, 0)
      })
        .then(() => {
          setCreateCropsResponse(true);
        })
        .catch(() => {
          setCreateCropsResponse(false);
        });
    }
  };

  return (
    <StyledWrapper>
      <div className="button-back">
        <Button
          transparent
          className="avatar-edit"
          onClick={props.changeToAvatar}
        >
          <Icon dark size="m" svg={ArrowLeft} />
          Cambiar avatar
        </Button>
      </div>
      <AvatarEdition
        dataFormAvatar={props.userAvatarData}
        edit
        changeImg
        avatar={props.user.avatarId}
        getDiametre={setDiametre}
        setImage={sendFile}
        setImageResult={createCropsResponse}
        {...props}
      />
      <div>
        <input
          type="file"
          onChange={e => {
            preview();
            setImg(e.currentTarget.value);
            setImgSend(e.target.files);
          }}
          ref={inputFile}
          id="inputFileImg"
          hidden
        />
      </div>
      <StyledWrapperBox>
        <BoxImg
          className="box_img"
          innerRef={box}
          onClick={e => updateDisplay(e)}
          onMouseDown={() => setControl(true)}
          onMouseMove={e => control && updateDisplay(e)}
          onMouseUp={() => setControl(false)}
        >
          <img className="avatar-image" src={img} alt="avatar" ref={imgAt} />
          <Box style={{ position: 'absolute', top: 0 }}>
            <Circle diametre={diametre} top={top} left={left} />
          </Box>
        </BoxImg>
      </StyledWrapperBox>
    </StyledWrapper>
  );
};

export default ChangeImage;
