import React from 'react';
import BigCalendar from 'react-big-calendar';
import moment from 'moment';
import storiesOf from '../../../../.storybook/util';
import CalendarFood from './CalendarLaunch';
import { StyleEventTitle, StyleEventText, StyledWrapper } from './style';

BigCalendar.momentLocalizer(moment); // or globalizeLocalizer

const events = [
  {
    id: 0,
    title: '14:30',
    start: new Date(new Date().setDate(1)),
    end: new Date(new Date().setDate(1))
  },
  {
    id: 1,
    title: '15:30',
    start: new Date(new Date().setDate(1)),
    end: new Date(new Date().setDate(1))
  },

  {
    id: 2,
    title: '13.30',
    start: new Date(new Date().setDate(2)),
    end: new Date(new Date().setDate(2))
  },

  {
    id: 3,
    title: '14:30',
    start: new Date(new Date().setDate(2)),
    end: new Date(new Date().setDate(2))
  },

  {
    id: 4,
    title: '15:00',
    start: new Date(new Date().setDate(14)),
    end: new Date(new Date().setDate(14))
  },
  {
    id: 5,
    title: '18:30',
    start: new Date(new Date().setDate(14)),
    end: new Date(new Date().setDate(14))
  },
  {
    id: 6,
    title: '10:30',
    start: new Date(new Date().setDate(7)),
    end: new Date(new Date().setDate(7))
  },
  {
    id: 7,
    title: '17:30',
    start: new Date(new Date().setDate(15)),
    end: new Date(new Date().setDate(15))
  },
  {
    id: 8,
    title: '19:30',
    start: new Date(new Date().setDate(27)),
    end: new Date(new Date().setDate(27))
  },
  {
    id: 14,
    title: '13:18',
    start: new Date(new Date().setHours(new Date().getHours() - 3)),
    end: new Date(new Date().setHours(new Date().getHours() + 3))
  },
  {
    id: 15,
    title: '15:30',
    start: new Date(new Date().setHours(new Date().getHours() - 3)),
    end: new Date(new Date().setHours(new Date().getHours() + 3))
  },
  {
    id: 16,
    title: '18:30',
    start: new Date(new Date().setHours(new Date().getHours() - 3)),
    end: new Date(new Date().setHours(new Date().getHours() + 3))
  },
  {
    id: 17,
    title: '19:30',
    start: new Date(new Date().setHours(new Date().getHours() - 3)),
    end: new Date(new Date().setHours(new Date().getHours() + 3))
  }
];

const EventMonth = [
  {
    eventHour: [
      {
        eventHourStart: '13:30'
      }
    ]
  }
];

function Event({ event }) {
  return (
    <StyleEventTitle>
      <StyleEventText>{event.title}</StyleEventText>
    </StyleEventTitle>
  );
}

// BigCalendar tiene una props components para personalizar las views
const CustomToolbar = () => <div>CuestomToolbar</div>;
const components = {
  toolbar: CustomToolbar,
  event: Event
};

storiesOf('core/Calendar Food', module).add('Default', () => (
  <StyledWrapper>
    <CalendarFood
      events={events}
      components={components}
      dateDay="Jun 02 2018"
      dataEvent={EventMonth}
    />
  </StyledWrapper>
));
