import React from 'react';
import storiesOf from '../../../../../.storybook/util';
import {
  LinkedinImport,
  Social,
  Aboutyou,
  Interests,
  Projects,
  Skills,
  Volunteer,
  Awards,
  Language,
  Education,
  Experience,
  ModExperienceActual,
  ModCurrentJob
} from '.';

import {
  linkedin,
  social,
  interests,
  ProjectsInvolved,
  dataListEmployee,
  skills,
  volunteerEdition,
  awards,
  months,
  years,
  languageEdition,
  nivel,
  dataEducation,
  experienceEdition,
  dataActual
} from '../../../../utils/mocks/js';

storiesOf('views/Edition', module)
  .add('Linkedin', () => (
    <LinkedinImport
      title="Importación de datos de Linkedin"
      text="¿Estás seguro que quieres importar los datos de Linkedin? Se sustiruirán
    los datos actuales por los importados:"
      dataLinkedin={linkedin}
      label="Entiendo que los datos importados sustituirán a los actuales"
    />
  ))
  .add('Social', () => <Social title="Redes sociales" dataSocial={social} />)
  .add('Projects', () => (
    <Projects
      title="Proyectos"
      data={ProjectsInvolved}
      dataPending={ProjectsInvolved}
      list={dataListEmployee}
    />
  ))
  .add('Aboutyou', () => <Aboutyou title="Sobre ti" />)
  .add('Skills', () => <Skills title="Skills" dataSkills={skills} />)
  .add('Interests', () => (
    <Interests title="Intereses" dataInterests={interests} />
  ))
  .add('Volunteer', () => (
    <Volunteer title="VOLUNTARIADOS" data={volunteerEdition} />
  ))
  .add('Language', () => (
    <Language
      dataLanguage={languageEdition}
      listNivel={nivel}
      selectedNivel="MEDIO"
    />
  ))
  .add('Awards', () => (
    <Awards
      title="PREMIOS"
      data={awards}
      listMonths={months}
      listYears={years}
      selectedMonths="Mes"
      selectedYears="Año"
    />
  ))
  .add('Education', () => (
    <Education
      dataEducation={dataEducation}
      listMonths={months}
      listYears={years}
      selectedMonths="Mes"
      selectedYears="Año"
    />
  ))
  .add('Add Experience', () => (
    <Experience
      add
      title="Añadir experiencia profesional"
      dataExperience={experienceEdition}
      listMonths={months}
      listYears={years}
    />
  ))
  .add('Modification Experience', () => (
    <Experience
      title="Modificar experiencia profesional"
      dataExperience={experienceEdition}
      listMonths={months}
      listYears={years}
    />
  ))
  .add('Modification Actual Experience', () => (
    <ModExperienceActual
      title="Modificar Experiencia Actual"
      dataEducation={dataActual}
      listMonths={months}
      listYears={years}
    />
  ))
  .add('Modification Actual Experience (End date)', () => (
    <ModExperienceActual
      title="Modificar Experiencia Actual"
      dataEducation={dataActual}
      listMonths={months}
      listYears={years}
      modifyEperience
    />
  ))
  .add('Modification Current Job', () => (
    <ModCurrentJob listMonths={months} listYears={years} />
  ));
