import React from 'react';
import PropTypes from 'prop-types';

import { ArrowLeft, Close, Success } from '../../../../assets/images/index';

import {
  BoxMessage,
  StyledIconClose,
  StyledIconPal,
  ParagraphTitle,
  ParagraphSubtitle,
  ParagraphLink,
  StyledIconLink
} from './style';

const MessageAddressSend = ({ className }) => (
  <BoxMessage className={className}>
    <StyledIconClose svg={Close} />
    <StyledIconPal svg={Success} />
    <ParagraphTitle>
      Tu solicitud de cambio de domicilio personal ha sido enviada
    </ParagraphTitle>
    <ParagraphSubtitle>
      En breve te confirmaremos la actualización de los datos.
    </ParagraphSubtitle>
    <ParagraphLink href="">
      <StyledIconLink svg={ArrowLeft} color="primary" />
      Volver a mi perfil
    </ParagraphLink>
  </BoxMessage>
);

MessageAddressSend.propTypes = {
  className: PropTypes.string
};

export default MessageAddressSend;
