import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { AvatarModal } from './style';

import {
  getUser,
  setAvatar,
  uploadAvatarImage,
  setImageAvatar,
  createCrops
} from '../../../../utils/apiUser';

import {
  selectAvatar,
  selectAvatarAxis
} from '../../../../constants/avatarIcons';

import { API } from '../../../../constants/api';

import ChooseAvatar from './ChooseAvatar';
import ChangeImage from './ChangeImage';

const ModalAvatar = ({ hideEditAvatar }) => {
  const [view, SetView] = useState('chooseAvatar');
  const [user, SetUser] = useState(null);
  const [avatar, SetAvatarPR] = useState(null);

  useEffect(() => {
    getUser().then(data => {
      SetUser(data);
      SetAvatarPR(data.avatarId);
    });
  }, []);

  return (
    <AvatarModal
      clickCloseIcon={() => {
        hideEditAvatar();
      }}
    >
      {user && view === 'chooseAvatar' ? (
        <ChooseAvatar
          userAvatarData={{
            userName: user && `${user.name} ${user.lastname}`,
            imgAvatar: !avatar
              ? selectAvatar(user.avatarId ? user.avatarId : 'koala')
              : selectAvatar(avatar),
            type: !avatar
              ? selectAvatarAxis(user.avatarId ? user.avatarId : 'koala')
              : selectAvatarAxis(avatar),
            perfil: `${API}images/avatar/${user && user.userId}/128x128.jpg`
          }}
          user={user}
          avatar={avatar}
          setAvatar={setAvatar}
          chosseAvatar={SetAvatarPR}
          changeView={vw => SetView(vw)}
        />
      ) : (
        user && (
          <ChangeImage
            changeToAvatar={() => SetView('chooseAvatar')}
            user={user}
            userAvatarData={{
              userName: user && `${user.name} ${user.lastname}`,
              imgAvatar: !avatar
                ? selectAvatar(user.avatarId ? user.avatarId : 'koala')
                : selectAvatar(avatar),
              type: !avatar
                ? selectAvatarAxis(user.avatarId ? user.avatarId : 'koala')
                : selectAvatarAxis(avatar),
              perfil: `${API}images/avatar/${user && user.userId}/128x128.jpg`
            }}
            uploadAvatarImage={uploadAvatarImage}
            setImageAvatar={setImageAvatar}
            createCrops={createCrops}
            changeView={vw => SetView(vw)}
          />
        )
      )}
    </AvatarModal>
  );
};

ModalAvatar.propTypes = {
  hideEditAvatar: PropTypes.func
};

export default ModalAvatar;
