import styled from 'styled-components';
import { rem } from 'polished';
import mediaqueries from '../../../config/themes/variables/mediaqueries';
import Button from '../Buttons';

const StyledEventComplete = styled.div`
  background-color: ${props => props.theme.colors.colorOne};
  color: ${props => props.theme.colors.light};
  font-size: ${props => props.theme.fonts.small};
`;

const StyledEventHeader = styled.div`
  align-items: center;
  background-color: ${props => props.theme.colors.grayDark}33;
  display: flex;
  font-family: ${props => props.theme.fonts.medium};
  font-size: ${props => props.theme.fonts.xsmall};
  justify-content: space-between;
  padding: ${rem('5px')} ${rem('5px')} ${rem('5px')} ${rem('16px')};
  width: 100%;
`;

const StyleEventDay = styled.div`
  ${mediaqueries.greaterThan('desktop')`
  `};
`;
const StyleEventDayWeek = styled.span`
  ${mediaqueries.greaterThan('desktop')`
  `};
`;

const StyleEventDayNum = styled.span`
  ${mediaqueries.greaterThan('desktop')`
  `};
`;

const StyleEventDayMonth = styled.span`
  text-transform: uppercase;
  ${mediaqueries.greaterThan('desktop')`
  `};
`;

const StyleEventButtons = styled(Button)`
  background-color: ${props => props.theme.colors.grayDark}44;
`;

export {
  StyledEventComplete,
  StyledEventHeader,
  StyleEventDay,
  StyleEventDayWeek,
  StyleEventDayNum,
  StyleEventDayMonth,
  StyleEventButtons
};
