import React from 'react';
import PropTypes from 'prop-types';
import {
  StyleList,
  StyleItem,
  StyleOptions,
  StyleBoxEmployee,
  StyleEditIcon
} from './style';
import { Pencil } from '../../../../assets/images/index';

const formatDate = date => {
  const splitDate = date.split('-');
  const updatedDate = `${splitDate[1]}-${splitDate[0]}`;

  return updatedDate;
};

const Resume = (dataAward, limit) => (
  <StyleList>
    {dataAward.slice(0, limit).map(item => (
      <StyleItem key={`${item.title + item.date}`}>
        {item.title}{' '}
        {item.title && <StyleOptions>{formatDate(item.date)}</StyleOptions>}
      </StyleItem>
    ))}
  </StyleList>
);

const Award = ({ limit, dataAward, onEditMode, clickEditIcon }) => (
  <StyleBoxEmployee
    title="Premios"
    more={dataAward && dataAward.length > limit}
    moreInfo={Resume(dataAward, dataAward ? dataAward.length : 0)}
  >
    {onEditMode && (
      <StyleEditIcon
        svg={Pencil}
        size="m"
        color="dark"
        onClick={clickEditIcon}
        hover
      />
    )}
    {Resume(dataAward, limit)}
  </StyleBoxEmployee>
);

Award.propTypes = {
  dataAward: PropTypes.arrayOf(
    PropTypes.shape({
      date: PropTypes.string,
      name: PropTypes.string
    })
  ),
  limit: PropTypes.number,
  onEditMode: PropTypes.bool,
  clickEditIcon: PropTypes.func
};

Award.defaultProps = {
  dataAward: []
};

export default Award;
