import React from 'react';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import OptionsNews from '.';
import ShareSend from './ShareSend';
import WrapperShareSend from './WrapperShareSend';

/* #### STORY STYLES #### */
const capa = {
  position: 'relative',
  width: '600px',
  height: '250px',
  margin: '0 auto',
  textAlign: 'center'
};

/* ###################### */

storiesOf('core/OptionsNews', module)
  .addDecorator(withReadme(Readme))
  .add('Iconos', () => (
    <div style={capa}>
      <OptionsNews numberIcon={58} />
    </div>
  ))
  .add('Opcion Like', () => (
    <div style={capa}>
      <OptionsNews numberIcon={28} activeLike />
    </div>
  ))
  .add('Opcion Read', () => (
    <div style={capa}>
      <OptionsNews numberIcon={158} activeRead />
    </div>
  ))
  .add('Opcion Share', () => (
    <div style={capa}>
      <OptionsNews numberIcon={8} />
      <WrapperShareSend>
        <ShareSend activeShare />
      </WrapperShareSend>
    </div>
  ))
  .add('Opcion Iconos móvil', () => (
    <div style={capa}>
      <OptionsNews numberIcon={2} activeMobile />
    </div>
  ))
  .add('for columns', () => (
    <div style={capa}>
      <OptionsNews numberIcon={2} activeMobile readAfter />
    </div>
  ));
