import request from './api';
import {
  API,
  API_USER,
  API_DEPARTMENT_TEAM,
  API_CMS,
  API_LEGAL
} from '../constants/api';

export const getUser = () => request(`${API}connected`);
export const getUserDetails = () => request(`${API_USER}user/me`);
export const getUserDetailsById = userId =>
  request(`${API_USER}user/${userId}`);
export const getMyTeam = departmentId =>
  request(API_DEPARTMENT_TEAM.replace('0000', departmentId), { method: 'GET' });
export const setUserEditingData = userUpdatedData =>
  request(`${API_USER}user`, {
    method: 'PUT',
    data: userUpdatedData
  });

export const checkPassword = password =>
  request(`${API}passwordVerify`, {
    method: 'POST',
    data: {
      password
    }
  });

export const getToken = () => request(`${API}/token`);

export const setAvatar = id =>
  request(`${API_USER}avatar?avatarId=${id}`, {
    method: 'PUT'
  });

export const createCrops = data =>
  request(`${API_CMS}images/avatar/crop`, {
    method: 'POST',
    data
  });

export const setImageAvatar = (token, type, corps = null) => {
  request(`${API_CMS}images/avatar/default?file=${token}.${type}`, {
    method: 'POST'
  })
    .then(() => createCrops(corps))
    .catch(err => {
      console.warn(err);
    });
};

export const uploadAvatarImage = (data, type, corps = null) => {
  request(`${API_CMS}upload`, {
    method: 'POST',
    headers: { 'Content-Type': 'multipart/form-data' },
    data
  })
    .then(res => {
      if (corps) {
        setImageAvatar(res, type, corps);
      }
      return res;
    })
    .catch(err => {
      console.warn(err);
    });
};

export const setLegalCondition = () => {
  request(`${API_LEGAL}?legalConditions=true`, {
    method: 'POST'
  });
};
