import React from 'react';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import { GeneralList } from '.';
import { list5 } from '../../../utils/mocks/js';

storiesOf('core/GeneralList', module)
  .addDecorator(withReadme(Readme))
  .add('Default', () => (
    <div>
      <h4>
        active = false <em>(Vista móvil)</em>
      </h4>
      <GeneralList dataList={list5} />
      <br />
      <h4>
        active = true <em>(Vista móvil)</em>
      </h4>
      <GeneralList dataList={list5} active />
    </div>
  ));
