import React from 'react';
import storiesOf from '../../../../.storybook/util';
import Alerts from '.';

storiesOf('core/Alerts', module)
  .add('Info alert', () => <Alerts visible>Alerta informativa</Alerts>)
  .add('Success alert', () => (
    <Alerts visible type="success">
      Alerta éxito
    </Alerts>
  ))
  .add('Warning alert', () => (
    <Alerts visible type="warning">
      Alerta advertencia
    </Alerts>
  ))
  .add('Error alert', () => (
    <Alerts visible type="error">
      Alerta error
    </Alerts>
  ));
