import React from 'react';
import storiesOf from '../../../../.storybook/util';
import { Tabs, Tab } from '.';
import { menuDia } from '../../../utils/mocks/js';

storiesOf('core/Tabs Menu del día', module)
  .add('Horizontal', () => (
    <Tabs>
      {menuDia.map(item => (
        <Tab label={item.name} key={item.id} id={item.id}>
          {item.content.map(content => (
            <p key={content.name}>{content.name}</p>
          ))}
        </Tab>
      ))}
    </Tabs>
  ))
  .add('Vertical', () => (
    <Tabs vertical>
      {menuDia.map(item => {
        const body = item.content.map(content => (
          <p key={content.name}>{content.name}</p>
        ));
        return (
          <Tab label={item.name} key={item.id} id={item.id}>
            {body}
          </Tab>
        );
      })}
    </Tabs>
  ));
