import React from 'react';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import { SQUARED_SMALL } from '../../../utils/crops';
import Credit from '.';
import { author, author2 } from '../../../utils/mocks/js';

storiesOf('core/Credit', module)
  .addDecorator(withReadme(Readme))
  .add('Autor y Departamento', () => (
    <Credit
      author={author}
      pathImage="/crops/liberty"
      defaultCrop={SQUARED_SMALL}
      alt="Usuario"
    />
  ))
  .add('Sin imagen', () => (
    <Credit author={author2} name="Alias del autor de la noticia" />
  ));
