import React from 'react';
import storiesOf from '../../../../.storybook/util';
import SkillUser from '.';
import { dataSkill } from '../../../utils/mocks/js';

const styleItem = {
  maxWidth: '650px',
  margin: 'auto'
};

storiesOf('core/SkillUser', module).add('Default', () => (
  <div>
    <div style={styleItem}>
      <h3>No logado</h3>
      <SkillUser dataSkill={dataSkill} />
    </div>
    <br /> <br />
    <div style={styleItem}>
      <h3>Logado</h3>
      <SkillUser dataSkill={dataSkill} userLogged />
    </div>
    <br /> <br />
    <div style={styleItem}>
      <h3>More</h3>
      <SkillUser dataSkill={dataSkill} more />
    </div>
    <br /> <br />
  </div>
));
