import React from 'react';
import storiesOf from '../../../../.storybook/util';
import AvatarPerson from '.';

const styleDiv = {
  width: '200px',
  height: '200px',
  margin: 'auto',
  fontSize: '4px'
};

const avatar = {
  name: 'Estefania',
  lastname: 'Gallego Márquez',
  userId: '1',
  crop: {
    width: 72,
    height: 72
  }
};

storiesOf('core/Avatar/AvatarPerson', module)
  .add('sin imagen', () => (
    <div style={styleDiv}>
      <AvatarPerson {...avatar} />
    </div>
  ))
  .add('con imagen', () => (
    <div style={styleDiv}>
      <AvatarPerson {...avatar} pathImage="/crops/liberty" avatarPhoto />
    </div>
  ));
