import React, { Component } from 'react';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import moment from 'moment';
import MomentUtils from '@date-io/moment';

import { StyledWrapperTimes, StyledDateTimePicker } from './style';

import { convertDateToUTC } from '../../../utils/dates';

class InputDateTime extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedDate: convertDateToUTC(new Date())
    };
  }

  handleDateChange = selectedDate => {
    this.setState({
      selectedDate
    });
  };

  render() {
    const { selectedDate } = this.state;
    const { className } = this.props;

    return (
      <StyledWrapperTimes className={className}>
        <MuiPickersUtilsProvider utils={MomentUtils}>
          <StyledDateTimePicker
            onChange={this.handleDateChange}
            showTodayButton
            okLabel="Aceptar"
            cancelLabel="Cancelar"
            todayLabel="Hoy"
            format={moment(selectedDate).format('DD/MM/YYYY HH:mm')}
            allowKeyboardControl
          />
        </MuiPickersUtilsProvider>
      </StyledWrapperTimes>
    );
  }
}

export default InputDateTime;
