import styled, { css } from 'styled-components';
import { rem } from 'polished';
import { Link } from 'react-router-dom';
import mediaqueries from '../../../config/themes/variables/mediaqueries';
import Icon from '../../core/Icon';
import Notifications from '../../core/Notifications';
import AvatarPerson from '../../core/AvatarPerson';
import OverWriteMyApps from './styleAppsHeader';
import { StyledIconCollapse } from '../../core/MyApps/style';
import { StyledHeaderBox } from './BoxLogoAvatarStyle';

// Usuario
export const StyledUser = styled.figure`
  cursor: pointer;
  ${props =>
    !props.userProfileView
      ? `      
      margin-left: ${rem('26px')};
      margin-top: ${rem('-4px')};
      overflow: hidden;
  `
      : `
    margin-left: ${rem('26px')};
  `}
`;

// Imagen usuario
export const StyledImagen = styled.img`
  border: ${rem('2px')} solid ${props => props.theme.colors.dark};
  border-radius: 50%;
  height: ${rem('42px')};
  width: ${rem('42px')};
`;

// Caja para logo
export const StyledBoxLogo = styled.div`
  ${mediaqueries.greaterThan('desktop')`
    background-color: ${props => props.theme.colors.light};
    height:${rem('71px')};
    margin-top:${rem('-1px')};
    overflow:hidden;
    position:relative;
    transition: width .5s ease-in-out;
    width:${rem('184px')};
    z-index:12;
  `};
`;

// Logo
export const StyledLogo = styled(Icon)`
  cursor: pointer;
  margin-left: ${rem('86px')};
  width: ${rem('99px')};
  ${mediaqueries.lessThan('mobile')`
    height: ${rem('21px')};
    margin-left: ${rem('50px')};
    width: ${rem('70px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    height:${rem('35px')};
    margin-left:${rem('9px')};
    position:relative;
    top:${rem('18px')};
    width:${rem('117px')};
    z-index:12;
  `};
`;

// Zona interior con contenido
export const StyledNav = styled.nav`
  background-color: ${props => props.theme.colors.light};
  border-bottom: ${rem('2px')} solid ${props => props.theme.colors.dark};
  padding-left: ${rem('25px')};
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 30;
  ${mediaqueries.lessThan('mobile')`
    padding-left: ${rem('16px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    padding-left:0;
  `};
`;

// Listado con Buscador, Logo Dashboard, Logo Noticifiaciones, Logo Apps
export const StyledListGeneral = styled.ul`
  align-items: center;
  display: flex;
  margin: 0;
  padding: 0;
  width: 100%;
  z-index: 12;

  ${props =>
    props.active &&
    `
    z-index: 26;
  `};

  ${mediaqueries.greaterThan('desktop')`
    position:relative;
    height:calc(100% + ${rem('1px')});
    top:${rem('-1px')};
    z-index: 12;
  `};
  ${mediaqueries.lessThan('mobile')`
    justify-content: flex-end;
  `};
`;

// Elementos del listado
export const StyledElementsList = styled.li`
  align-items: center;
  display: inline-flex;
  height: 100%;
  list-style: none;
  margin-left: ${rem('40px')};
  padding-left: 0;
  position: relative;
  ${mediaqueries.lessThan('mobile')`
    margin-left: ${rem('28px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    margin-left: ${rem('28px')};
  `};
  &:before {
    content: none;
  }

  &:first-child {
    /* Buscador de header */
    margin-left: auto;
    ${mediaqueries.greaterThan('desktop')`
      margin-right:0;
      margin-left:inherit;
      position:absolute;
      right:${rem('264px')};
      top: 0;
      transition: width 0.5s ease-in-out;
      width: ${rem('735px')};
    `};
  }
  &:nth-child(2) {
    /* Darchboard */
    display: none;
  }
  &:nth-child(3) {
    ${mediaqueries.greaterThan('desktop')`
      display:inline-flex;
      margin-left:auto;
    `};
  }
  /* &:nth-child(3)   Notificaciones */
  /* &:nth-child(4)   Apss  */
  /* &:nth-child(5)   Perfil  */
  &:nth-child(5) {
    display: none;
    ${mediaqueries.greaterThan('desktop')`
      display:inline-flex;
      margin-left:0;
      margin-top:${rem('5px')};
      transition: width .5s ease-in-out;
      width: ${rem('72px')};
    `};
  }
  &:nth-child(2),
  &:nth-child(3),
  &:nth-child(4) {
    /* Corrección centrado vertical iconos */
    > svg {
      ${mediaqueries.greaterThan('desktop')`
        margin-top:${rem('2px')};
      `};
    }
  }
`;

// Header, contenedor total
export const StyledHeader = styled.header`
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
  position: relative;
  z-index: 40;

  ${mediaqueries.lessThan('mobile')`
    padding-top:${rem('49px')};
  `};
  ${mediaqueries.greaterThan('tablet')`
    padding-top:${rem('73px')};    
  `};

  * svg {
    transition: inherit;
  }
  /* props parte avatar y logo que solo aparezcan en home */
  ${props =>
    props.showHeader &&
    css`
      ${StyledHeaderBox} {
        display: flex;
      }
      ${StyledNav} {
        border-bottom: 0;
      }
      ${StyledBoxLogo} {
        width: 0;
      }
      ${StyledElementsList}:first-child {
        width: ${rem('910px')};
      }
      ${StyledElementsList}:nth-child(5) {
        width: ${props => (props.userProfileView ? rem('72px') : 0)};
      }
      ${mediaqueries.greaterThan('desktop')`
        border-bottom: ${rem('5px')} solid ${props.theme.colors.dark};
        z-index: 25;
      `};
    `};
  /* props para logo y usuario al hacer scroll */
  ${props =>
    props.fixed &&
    props.showHeader &&
    css`
      ${mediaqueries.greaterThan('desktop')`
        ${StyledBoxLogo} {
          width:${rem('184px')};
        }
        ${StyledNav} {
          border-bottom: ${rem('2px')} solid ${props.theme.colors.dark};
        }
        ${StyledElementsList}:first-child {
          width: ${rem('735px')};
        }
        ${StyledElementsList}:nth-child(5) {
          width: ${rem('72px')};
        }
      `};
    `};
  ${props =>
    props.fixed &&
    css`
      ${StyledIconCollapse} {
        right: ${rem('75px')};
      }
      ${OverWriteMyApps} {
        &:after {
          right: ${rem('73px')};
        }
      }
    `};
`;

// Div interior nav
export const StyledInterior = styled.div`
  align-items: center;
  display: flex;
  height: ${rem('74px')};
  margin: auto;
  max-width: ${rem('1232px')};
  padding-right: ${rem('26px')};
  position: relative;
  width: 100%;
  ${mediaqueries.lessThan('mobile')`
    height: ${rem('49px')};
    padding-right: ${rem('7px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    height: ${rem('71px')};
    padding-left:${rem('57px')};
    padding-right:0;
    &:before {
      background-color: ${props => props.theme.colors.light};
      content: '';
      left:${rem('56px')};
      height:calc(100% + ${rem('1px')});
      position: absolute;
      top: ${rem('-2px')};
      width:${rem('168px')};
      z-index:12;
    }
  `};
`;

// Hamburguesa
export const StyledHamburguer = styled(Icon)`
  left: 0;
  height: ${rem('36px')};
  position: absolute;
  top: ${rem('20px')};
  width: ${rem('36px')};
  ${mediaqueries.lessThan('mobile')`
    height: ${rem('24px')};
    top: ${rem('13px')};
    width: ${rem('24px')};
  `};
  ${mediaqueries.lessThan('desktop')`
    z-index: 26;
  `};
  ${mediaqueries.greaterThan('desktop')`
    height: ${rem('24px')};
    top: ${rem('22px')};
    width: ${rem('24px')};
    z-index:inherit;
  `};
`;

// Icono Apps Desktop
export const StyledIconAppsDesktop = styled(Icon)`
  display: none;
  ${mediaqueries.greaterThan('desktop')`
    display:block;
  `};
`;

// Icono Campana
export const StyledIconBell = styled(Icon)`
  height: ${rem('36px')};
  width: ${rem('36px')};
  ${mediaqueries.lessThan('mobile')`
    height: ${rem('24px')};
    width: ${rem('24px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    height: ${rem('24px')};
    width: ${rem('24px')};
  `};
`;

// Icono User dispositivos
export const StyledIconUser = styled(Icon)`
  ${mediaqueries.lessThan('mobile')`
    height: ${rem('24px')};
    width: ${rem('24px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    display:none;
  `};
`;

// Icono circulo con numero de notificaciones
export const StyledIconNot = styled(Notifications)`
  left: ${rem('25px')};
  margin-top: ${rem('-20px')};
  position: absolute;
  ${mediaqueries.lessThan('mobile')`
    left: ${rem('17px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    left: ${rem('17px')};
  `};
`;

export const StyledAvatarPerson = styled(AvatarPerson)`
  font-size: ${rem('21px')};
`;

export const StyledLinkMobile = styled(Link)`
  z-index: 14;
`;
