import styled from 'styled-components';
import { rem } from 'polished';
import Icon from '../Icon';
import Avatar from '../Avatar';
import Buttons from '../Buttons';
import Modal from '../Modal';

const StyledModal = styled(Modal)`
  background-color: ${props => props.theme.colors.grayLighter};
`;

const StyledConnectLinkedin = styled.div`
  width: 100%;
`;

const StyledTitle = styled.h1`
  color: ${props => props.theme.colors.dark};
  font-family: ${props => props.theme.fonts.bold};
  font-size: ${rem('16px')};
  margin-bottom: ${rem('10px')};
  text-align: center;
`;

const StyledDescription = styled.p`
  color: ${props => props.theme.colors.dark};
  font-family: ${props => props.theme.fonts.normal};
  font-size: ${rem('14px')};
  line-height: 1.2;
  margin-bottom: ${rem('34px')};
  text-align: center;
`;
const ItemsWrapper = styled.div`
  align-items: center;
  display: flex;
  justify-content: center;
`;

const StyledWrapper = styled.div`
  padding: ${rem('14px')} ${rem('0px')};
`;

const StyledWrapperLinked = styled(StyledWrapper)`
  border: ${rem('1px')} solid ${props => props.theme.colors.dark};
  margin-left: ${rem('34px')};
  padding: ${rem('10px')};
`;
const StyledWrapperPlus = styled(StyledWrapper)`
  padding: ${rem('14px')} ${rem('14px')};
`;

const StyledIcon = styled(Icon)`
  fill: ${props => props.theme.colors.dark};
`;

const StyledLinkedin = styled(Icon)`
  fill: ${props => props.theme.colors.dark};
  height: ${rem('35px')};
  width: ${rem('35px')};
`;

const StyledAvatar = styled(Avatar)`
  height: ${rem('124px')};
  margin: 0 auto;
  width: ${rem('124px')};
`;

const StyledButton = styled(Buttons)`
  background-color: ${props => props.theme.colors.blueLinkedin};
  display: block;
  margin: 0 auto;
  width: 50%;
`;

const StyledFooter = styled(StyledDescription)`
  color: ${props => props.theme.colors.gray};
  font-size: ${rem('14px')};
  margin-top: ${rem('25px')};
`;

export {
  StyledConnectLinkedin,
  StyledDescription,
  StyledTitle,
  StyledWrapper,
  StyledIcon,
  StyledLinkedin,
  StyledAvatar,
  ItemsWrapper,
  StyledWrapperLinked,
  StyledWrapperPlus,
  StyledButton,
  StyledModal,
  StyledFooter
};
