import React from 'react';
import storiesOf from '../../../../.storybook/util';
import { Paginator, ItemPaginator, ItemDots } from '.';

storiesOf('core/Paginator', module)
  .add('Navigation', () => (
    <Paginator>
      <ItemPaginator selected>1</ItemPaginator>
      <ItemPaginator>2</ItemPaginator>
      <ItemPaginator>3</ItemPaginator>
      <ItemPaginator>4</ItemPaginator>
      <ItemDots />
      <ItemPaginator>25</ItemPaginator>
    </Paginator>
  ))
  .add('Item', () => <ItemPaginator />)
  .add('Item selected', () => <ItemPaginator selected />);
