import React from 'react';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import { Languages, Interests, Volunteer, Award } from '.';
import {
  languageEmployee,
  interestInfo,
  volunteerInfo,
  awardInfo
} from '../../../utils/mocks/js';

const styleItem = {
  maxWidth: '350px',
  margin: 'auto'
};

storiesOf('core/Employee Info', module)
  .addDecorator(withReadme(Readme))
  .add('Idiomas', () => (
    <div style={styleItem}>
      <Languages dataLanguage={languageEmployee} more userLogged />
    </div>
  ))
  .add('Intereses', () => (
    <div style={styleItem}>
      <Interests dataInterests={interestInfo} more userLogged />
    </div>
  ))
  .add('Voluntariado', () => (
    <div style={styleItem}>
      <Volunteer dataVolunteer={volunteerInfo} more userLogged />
    </div>
  ))
  .add('Premio', () => (
    <div style={styleItem}>
      <Award dataAward={awardInfo} more userLogged />
    </div>
  ));
