import styled, { css } from 'styled-components';
import { rem } from 'polished';
import Icon from '../Icon';

const MainDropdown = styled.div`
  position: relative;
  width: ${rem('136px')};
`;

const Selector = styled.div`
  align-items: flex-end;
  display: flex;
  justify-content: flex-end;
  cursor: pointer;
`;

const IconDropdown = styled(Icon)`
  left: ${rem('0.5px')};
  position: relative;
  top: ${rem('2px')};
  transition: top ease 0.2s;
  z-index: 2;
  ${props =>
    !props.show &&
    css`
      top: ${rem('-7px')};
    `};
`;

const LabelDropdown = styled.span`
  font-family: ${props => props.theme.fonts.medium};
  font-size: ${props => props.theme.fontSize.xsmall};
  margin-right: 0.3rem;
  position: relative;
  top: ${rem('-3px')};
`;

const ListDropdown = styled.ul`
  background-color: ${props => props.theme.colors.dark};
  display: none;
  margin: 1rem 0 0 0;
  padding: ${rem('15px')} ${rem('20px')};
  position: absolute;
  right: ${rem('-4px')};
  top: 0;
  width: calc(100% + ${rem('4px')});
  z-index: 1;

  &:after {
    border-left: ${rem('10px')} solid transparent;
    border-right: ${rem('10px')} solid transparent;
    border-top: ${rem('12px')} solid ${props => props.theme.colors.light};
    content: '';
    height: 0;
    position: absolute;
    right: ${rem('2px')};
    top: ${rem('-2px')};
    width: 0;
  }

  ${props =>
    props.show &&
    css`
      display: block;
    `};
`;

export { MainDropdown, LabelDropdown, IconDropdown, ListDropdown, Selector };
