import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';

import {
  StyledBoxContent,
  StyledBoxDown,
  StyledButton,
  StyledButtonPlus
} from './style';
import LanguageItem from './LanguageItem';
import Modal from '../../../core/Modal';
import { LANGUAGE } from '../../../../constants/literals/modals';
import { moveElementPosition } from '../../../../utils/splices';

class Language extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.dataLanguage,
      btnIsDisabled: true
    };
  }

  componentDidMount() {
    const { data } = this.state;

    if (data.length === 0) {
      this.handlePlus();
    }
  }

  onClickDelete = index => {
    const { data } = this.state;
    const updatedData = data.filter((ele, i) => i !== index);

    this.setState({
      data: updatedData,
      btnIsDisabled: this.getBtnStatus(updatedData)
    });
  };

  onClickOrderArrow = (index, isDown) => {
    const { data } = this.state;
    const move = isDown ? index + 1 : index - 1;
    const updatedData = moveElementPosition([...data], index, move);

    this.setState({
      data: updatedData,
      btnIsDisabled: this.getBtnStatus(updatedData)
    });
  };

  getBtnStatus = data => {
    const { dataLanguage } = this.props;

    return _.isEqual(dataLanguage, data);
  };

  handleAccept = () => {
    const { onAcceptModal } = this.props;
    const { data } = this.state;
    const updatedData = data.reduce((acc, ele) => {
      if (ele.certification && ele.name) {
        acc.push({
          name: ele.name,
          certification: ele.certification,
          proficiency: ele.proficiency
        });
      }
      return acc;
    }, []);

    onAcceptModal(updatedData);
  };

  handleChange = (value, index, key) => {
    const { data } = this.state;
    const clonedData = [...data];

    clonedData[index] = { ...clonedData[index], [key]: value };

    this.setState({
      data: clonedData,
      btnIsDisabled: this.getBtnStatus(clonedData)
    });
  };

  handleChangeRadio = (index, key, event) => {
    const { data } = this.state;
    const clonedData = [...data];

    clonedData[index] = { ...clonedData[index], [key]: event.target.value };

    this.setState({
      data: clonedData,
      btnIsDisabled: this.getBtnStatus(clonedData)
    });
  };

  handlePlus = () => {
    const { selectedNivel } = this.props;
    const { data } = this.state;
    const clonedData = [...data];

    clonedData.push({
      name: '',
      certification: '',
      proficiency: selectedNivel
    });

    this.setState({
      data: clonedData,
      btnIsDisabled: this.getBtnStatus(clonedData)
    });
  };

  render() {
    const { onCloseModal, listNivel } = this.props;
    const { data, btnIsDisabled } = this.state;

    return (
      <Modal title={LANGUAGE.TITLE} clickCloseIcon={onCloseModal}>
        <StyledBoxContent>
          {data.map((item, index) => (
            <LanguageItem
              key={`Language${index + 1}`}
              certification={item.certification}
              listNivel={listNivel}
              name={item.name}
              onChangeInputName={e => this.handleChange(e, index, 'name')}
              onChangeInputProficiency={e =>
                this.handleChange(e, index, 'certification')
              }
              onChangeRadio={e =>
                this.handleChangeRadio(index, 'proficiency', e)
              }
              onClickDelete={() => this.onClickDelete(index)}
              onClickDown={() => this.onClickOrderArrow(index, true)}
              onClickUp={() => this.onClickOrderArrow(index, false)}
              order={index + 1}
              proficiency={item.proficiency}
            />
          ))}
          <StyledButtonPlus onClick={this.handlePlus} dark />
          <StyledBoxDown>
            <StyledButton onClick={onCloseModal} modal>
              {LANGUAGE.CANCEL}
            </StyledButton>
            <StyledButton
              onClick={this.handleAccept}
              secondary
              disabled={btnIsDisabled}
            >
              {LANGUAGE.ACCEPT}
            </StyledButton>
          </StyledBoxDown>
        </StyledBoxContent>
      </Modal>
    );
  }
}

Language.propTypes = {
  dataLanguage: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      certification: PropTypes.string,
      proficiency: PropTypes.string,
      id: PropTypes.string
    })
  ),
  listNivel: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string,
      text: PropTypes.string
    })
  ),
  onAcceptModal: PropTypes.func,
  onCloseModal: PropTypes.func,
  selectedNivel: PropTypes.string
};

Language.defaultProps = {
  dataLanguage: [],
  selectedNivel: 'MEDIO',
  listNivel: [
    { value: 'BAJO', text: 'Bajo' },
    { value: 'MEDIO', text: 'Medio' },
    { value: 'ALTO', text: 'Alto' }
  ]
};

export default Language;
