import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledList,
  StyledElement,
  StyledIcon,
  StyledButtonLeft
} from './style';

import { ArrowLeft } from '../../../../assets/images/index';

const Breadcrumb = ({ className, dataList, onClick }) => (
  <nav className={className}>
    {dataList.map(
      (item, index) =>
        index > 0 && (
          <StyledButtonLeft onClick={() => onClick(dataList[index - 1])}>
            <StyledIcon svg={ArrowLeft} />
          </StyledButtonLeft>
        )
    )}
    <StyledList>
      {dataList.map((item, index) => (
        <React.Fragment key={item.id}>
          <StyledElement tabindex="0" onClick={() => onClick(item)}>
            {index > 0 && <StyledIcon svg={ArrowLeft} />}
            {item.txt}
          </StyledElement>
        </React.Fragment>
      ))}
    </StyledList>
  </nav>
);

Breadcrumb.propTypes = {
  className: PropTypes.string,
  onClick: PropTypes.func,
  dataList: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      dataList: PropTypes.arrayOf(
        PropTypes.shape({
          txt: PropTypes.string,
          id: PropTypes.string
        })
      )
    })
  )
};

export default Breadcrumb;
