import React from 'react';
import StoryRouter from 'storybook-react-router';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import { createStore, applyMiddleware } from 'redux';
import rootReducer from '../../../reducers';
import storiesOf from '../../../../.storybook/util';
import Header from '.';
import { menu } from '../../../utils/mocks/js';

const header = {
  fixed: false,
  widget: true,
  showSearch: false,
  headerMenu: [],
  user: {
    userId: 6,
    email: 'jose.ocana@i3television.es',
    name: 'José María',
    lastname: 'Ocaña Tofiño',
    idDepartment: 2361,
    profile: 'TECNICO',
    typology: 'EXTERNAL',
    location: 'MADRID',
    imageName: null,
    username: 'jot74343',
    roles: '["ROLE_ADMIN_CMS", "ROLE_ADMIN_CMS_ASSOCIATION", "R…]',
    extension: null,
    avatarId: 'donuts',
    legalConditions: true
  },
  query: '',
  userId: 6,
  userProfile: null
};

const initialState = {
  notifications: {
    notificationsList: [
      {
        typeAppNotification: 'PARCEL',
        count: '6'
      },
      {
        typeAppNotification: 'CMS',
        count: '1'
      }
    ]
  }
};

const store = createStore(rootReducer, initialState, applyMiddleware(thunk));

storiesOf('views/Header', module)
  .addDecorator(StoryRouter())
  .addDecorator(getStory => <Provider store={store}>{getStory()}</Provider>)
  .add('Big Header', () => <Header headerMenu={menu} {...header} showHeader />)
  .add('Small Header', () => <Header {...header} />);
