import React from 'react';
import PropTypes from 'prop-types';
import { StyledTitle, StyledIcon } from './style';
import { Usuario } from '../../../../assets/images/index';

const TitleWidget = ({ title, type, icon, className, small, size }) => (
  <StyledTitle type={type} className={className} small={small}>
    <StyledIcon size={size} svg={icon} type={type} small={small} />
    {title}
  </StyledTitle>
);

TitleWidget.propTypes = {
  title: PropTypes.string,
  type: PropTypes.string,
  icon: PropTypes.shape({
    id: PropTypes.string
  }),
  className: PropTypes.string,
  small: PropTypes.bool,
  size: PropTypes.oneOf(['xxs', 'xs', 's', 'sm', 'smm', 'm', 'l', 'xl'])
};

TitleWidget.defaultProps = {
  type: 'colorOne',
  icon: Usuario,
  small: false,
  size: 'xl'
};

export default TitleWidget;
