import React from 'react';
import StoryRouter from 'storybook-react-router';
import storiesOf from '../../../../.storybook/util';
import AvatarModule from '.';

const styleDiv = {
  maxWidth: '150px',
  margin: 'auto'
};

storiesOf('core/Avatar/AvatarModule', module)
  .addDecorator(StoryRouter())
  .add('sin imagen', () => (
    <div style={styleDiv}>
      <AvatarModule
        person="J. Pedro González"
        lastname="González"
        job="Programador"
      />
    </div>
  ));
