import React from 'react';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import { ContentGrouping } from '.';
import { formatTxt } from '../../../utils/mocks/js/formats';

storiesOf('core/ContentGrouping', module)
  .addDecorator(withReadme(Readme))
  .add('Agrupador con formatos', () => (
    <ContentGrouping title="Titulo del agrupador" formats={formatTxt}>
      <p style={{ paddingTop: '20px' }}>
        Aquí componente o nodo a cargar en el agrupador...
      </p>
    </ContentGrouping>
  ))
  .add('Agrupador sin formatos', () => (
    <ContentGrouping title="Titulo del agrupador">
      <p style={{ paddingTop: '20px' }}>
        Aquí componente o nodo a cargar en el agrupador...
      </p>
    </ContentGrouping>
  ));
