import React from 'react';
import PropTypes from 'prop-types';
import { Blockquote as ImgBlockquote } from '../../../../assets/images';
import {
  StyledBlockquote,
  StyledTitleBlockquote,
  StyledIconBlockquote
} from './style';

const Blockquote = ({ className, children, title }) => (
  <StyledBlockquote className={className}>
    <StyledIconBlockquote svg={ImgBlockquote} />
    <StyledTitleBlockquote>{title}</StyledTitleBlockquote>
    {children}
  </StyledBlockquote>
);

Blockquote.propTypes = {
  className: PropTypes.string,
  title: PropTypes.string
};

Blockquote.defaultProps = {
  title: 'Default title'
};

export default Blockquote;
