import React from 'react';
import { rem } from 'polished';
import PropTypes from 'prop-types';
import Icon from '../../../core/Icon';
import { Download } from '../../../../../assets/images/index';
import {
  StyledLatestPayrolls,
  StyledLatestPayrollsButton,
  StyledLatestPayrollsItem,
  StyledLatestPayrollsList,
  StyledLatestPayrollsText,
  StyledTitleGivens
} from './style';

const LatestPayrolls = ({ payRolls, onSelect }) => (
  <StyledLatestPayrolls>
    <StyledTitleGivens title="MIS ÚLTIMAS NÓMINAS" />
    <StyledLatestPayrollsList>
      {payRolls.map(item => (
        <StyledLatestPayrollsItem
          key={`${item.month}-${item.year}`}
          onClick={() => {
            if (onSelect) {
              onSelect(item);
            }
          }}
        >
          <StyledLatestPayrollsText>
            Nómina {item.month} {item.year}
          </StyledLatestPayrollsText>
          <StyledLatestPayrollsButton
            role="link"
            title={`Descargar nómina de ${item.month} ${item.year}`}
            download
          >
            <Icon
              color="dark"
              svg={Download}
              width={rem('18px')}
              height={rem('18px')}
              hover
            />
          </StyledLatestPayrollsButton>
        </StyledLatestPayrollsItem>
      ))}
    </StyledLatestPayrollsList>
  </StyledLatestPayrolls>
);

LatestPayrolls.propTypes = {
  payRolls: PropTypes.arrayOf(
    PropTypes.shape({
      month: PropTypes.string,
      year: PropTypes.number,
      href: PropTypes.string
    })
  ).isRequired,
  onSelect: PropTypes.func
};

export default LatestPayrolls;
