import React from 'react';
import PropTypes from 'prop-types';

import {
  StyledSearch,
  StyledInput,
  StyledButtonSearch,
  StyledIconSearch
} from './SearchHeaderTopStyle';

import { Search } from '../../../../assets/images';

const SearchHeaderTop = ({
  onChangeSearch,
  value,
  onFocus,
  onToggleSearch,
  name
}) => {
  const txtPlaceholder = `¿Cómo podemos ayudarte ${name}?`;

  return (
    <StyledSearch>
      <StyledInput
        placeholder={txtPlaceholder}
        type="text"
        name="search"
        value={value}
        onChange={onChangeSearch}
        onFocus={onFocus}
      />
      <StyledButtonSearch onClick={onToggleSearch}>
        <StyledIconSearch color="dark" svg={Search} hover />
      </StyledButtonSearch>
    </StyledSearch>
  );
};

SearchHeaderTop.propTypes = {
  onChangeSearch: PropTypes.func,
  onFocus: PropTypes.func,
  value: PropTypes.string,
  onToggleSearch: PropTypes.func,
  name: PropTypes.string
};

export default SearchHeaderTop;
