import styled from 'styled-components';
import { rem } from 'polished';
import mediaqueries from '../../../config/themes/variables/mediaqueries';

const StyledBoxApps = styled.div`
  background-color: ${props => props.theme.colors.light};
  width: 100%;

  ${mediaqueries.greaterThan('desktop')`
    height:auto;
  `};

  .viewport {
    align-content: flex-start;
    display: flex;
    flex-flow: wrap;
  }
`;

const StyledBoxAppsDark = StyledBoxApps.extend`
  background-color: ${props => props.theme.colors.dark};
  height: 100%;

  ${mediaqueries.greaterThan('mobile')`
  .viewport {
    margin-right: ${rem('-100px')};
    padding-right: ${rem('100px')};
    max-height: ${rem('375px')};
    overflow-x: hidden;
    overflow-y-auto;
  }
  `};
`;

const StyledListApps = styled.div`
  align-items: center;
  color: ${props => props.theme.colors.dark};
  display: flex;
  font-family: ${props => props.theme.fonts.medium};
  justify-content: center;
  margin-bottom: ${rem('35px')};
  min-height: ${rem('45px')};
  width: calc(100% / 3);

  &:hover {
    cursor: pointer;
  }

  ${mediaqueries.greaterThan('mobile')`
    margin-bottom: ${rem('30px')};
  `};

  ${mediaqueries.greaterThan('desktop')`
    width: calc(100% / 4);
  `};
`;

const StyledListAppsWidget = StyledListApps.extend`
  ${mediaqueries.between('mobile', 'desktop')`
    width: calc(100% / 5);
  `};
`;

export {
  StyledBoxApps,
  StyledBoxAppsDark,
  StyledListApps,
  StyledListAppsWidget
};
