import React from 'react';
import StoryRouter from 'storybook-react-router';
import storiesOf from '../../../../.storybook/util';
import SearchModal from '.';
import { dataTabs, dataResult, pagesResult } from '../../../utils/mocks/js';

const refs = {
  store: () => {}
};

storiesOf('views/SearchModal', module)
  .addDecorator(StoryRouter())
  .add('Result Search', () => (
    <SearchModal
      refs={refs}
      tabs={dataTabs}
      results={dataResult}
      paginator={pagesResult}
    />
  ))
  .add('Error Search', () => (
    <SearchModal
      refs={refs}
      tabs={dataTabs}
      results={dataResult}
      paginator={pagesResult}
      error
    />
  ));
