import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { setAvatar2Id } from '../../../actions/user';
import { initialsString } from '../../../utils/initialsString';
import { accentsTidy } from '../../../utils/accents';

import Avatar from '../Avatar';

import {
  FormUpload,
  NameUser,
  WrapperAvatar,
  ZoomPhoto,
  WrapperButtons,
  FormButtons,
  FormButtonsInvested,
  StyledAlerts
} from './styleFormAvatar';

const saveAvatar = (setAvatar, avatar, SetSaved, setAvatar2) => {
  setAvatar(avatar)
    .then(() => {
      SetSaved(true);
      setTimeout(() => SetSaved(false), 5000);
      setAvatar2(avatar);
    })
    .catch(SetSaved(false));
};

const saveImage = (setTimestamp, setImage, setSaving) => {
  setImage();
  setSaving(true);
  setTimestamp(new Date().getTime());
};

const FormAvatar = ({
  className,
  edit,
  dataFormAvatar,
  changeView,
  changeImg,
  setAvatar,
  getDiametre,
  setImage,
  avatar,
  setAvatar2,
  avatarPhoto,
  setImageResult,
  user
}) => {
  const [saved, SetSaved] = useState(null);
  const [timestamp, setTimestamp] = useState(new Date().getTime());
  const [saving, setSaving] = useState(false);
  let firstLetter;
  let secondLetter;

  if (user.name) {
    firstLetter = accentsTidy(initialsString(user.name, 1));
  }
  if (user.lastname) {
    secondLetter = accentsTidy(initialsString(user.lastname, 1));
  }

  useEffect(() => {
    SetSaved(setImageResult);
    setSaving(false);
  }, [setImageResult]);

  return (
    <div>
      <FormUpload className={className}>
        {dataFormAvatar && (
          <React.Fragment>
            <NameUser>{dataFormAvatar.userName}</NameUser>
            <WrapperAvatar>
              <Avatar
                name={`${firstLetter}${secondLetter}`}
                imgAvatar={dataFormAvatar.imgAvatar}
                type={dataFormAvatar.type}
                perfil={`${dataFormAvatar.perfil}?update=${timestamp}`}
                editAvatar={false}
                avatarPhoto={avatarPhoto}
              />
            </WrapperAvatar>
            {changeImg && (
              <ZoomPhoto max={400} min={0} onChange={val => getDiametre(val)} />
            )}
            <WrapperButtons>
              {!edit && dataFormAvatar.perfil && (
                <FormButtonsInvested secondary>Editar</FormButtonsInvested>
              )}

              {changeImg && (
                <React.Fragment>
                  <FormButtonsInvested
                    secondary
                    type="button"
                    onClick={() => {
                      document.getElementById('inputFileImg').click();
                    }}
                  >
                    Subir foto
                  </FormButtonsInvested>
                  <FormButtons
                    type="button"
                    secondary
                    onClick={() => {
                      saveImage(setTimestamp, setImage, setSaving);
                    }}
                  >
                    Guardar
                  </FormButtons>
                </React.Fragment>
              )}

              {!changeImg && (
                <React.Fragment>
                  <FormButtonsInvested
                    type="button"
                    onClick={() => changeView('changeImage')}
                    ghost
                    dark
                  >
                    Cambiar foto
                  </FormButtonsInvested>
                  <FormButtons
                    type="button"
                    secondary
                    onClick={() => {
                      saveAvatar(setAvatar, avatar, SetSaved, setAvatar2);
                    }}
                  >
                    Guardar
                  </FormButtons>
                </React.Fragment>
              )}
              {saved !== null && (
                <>
                  {saved ? (
                    <StyledAlerts visible={saved} type="success">
                      Nuevo avatar guardado
                    </StyledAlerts>
                  ) : (
                    <StyledAlerts visible={saved} type="error">
                      Error al guardar la imagen
                    </StyledAlerts>
                  )}
                </>
              )}
              <StyledAlerts visible={saving} type="success">
                Guardando foto...
              </StyledAlerts>
            </WrapperButtons>
          </React.Fragment>
        )}
      </FormUpload>
    </div>
  );
};

FormAvatar.propTypes = {
  setAvatar: PropTypes.func,
  avatar: PropTypes.string,
  setImage: PropTypes.func,
  changeView: PropTypes.func,
  getDiametre: PropTypes.func,
  changeImg: PropTypes.bool,
  className: PropTypes.string,
  edit: PropTypes.bool,
  setImageResult: PropTypes.bool,
  dataFormAvatar: PropTypes.shape({
    userName: PropTypes.string,
    imgAvatar: PropTypes.object,
    type: PropTypes.string,
    perfil: PropTypes.string
  }),
  avatarPhoto: PropTypes.bool
};

const mapDispatchToProps = dispatch => ({
  setAvatar2: idAvatar => dispatch(setAvatar2Id(idAvatar))
});

export default connect(
  null,
  mapDispatchToProps
)(FormAvatar);
