import React from 'react';
import styled from 'styled-components';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import { CalendarEventDay } from './index';
import mediaqueries from '../../../config/themes/variables/mediaqueries';

const StyledRecurs = styled.div`
  height: 100vh;
  position: relative;
  ${mediaqueries.greaterThan('desktop')`
    width: 360px;
    margin: 0 auto;
  `};
`;

const EventMonth = [
  {
    eventHour: [
      {
        eventHourStart: '13:30'
      }
    ]
  }
];

storiesOf('core/CalendarEventLunch', module)
  .addDecorator(withReadme(Readme))
  .add('Not Event Day', () => (
    <StyledRecurs>
      <CalendarEventDay dateDay="Jun 02 2018" />
    </StyledRecurs>
  ))
  .add('Event Day', () => (
    <StyledRecurs>
      <CalendarEventDay dataEvent={EventMonth} dateDay="May 21 2018" />
    </StyledRecurs>
  ));
