import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledPreviousPayrolls,
  StyledTitle,
  StyledCols,
  StyledCol,
  StyledButtonsWrapper,
  StyledButtonSecondary,
  StyledDropdownInput,
  StyledLabel
} from './style';

class PreviousPayrolls extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      yearSelected: props.years[0],
      monthSelected: props.months[0]
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.months !== this.props.months) {
      this.setState({
        monthSelected: nextProps.months[0]
      });
    }
  }

  render() {
    const { months, years, onChangeYears, onClickBtn } = this.props;
    const { yearSelected, monthSelected } = this.state;

    return (
      <StyledPreviousPayrolls>
        <StyledTitle>DESCARGAR NÓMINAS ANTERIORES</StyledTitle>
        <StyledCols>
          <StyledCol>
            <StyledLabel label="Mes" />
            <StyledDropdownInput
              list={months}
              label={monthSelected.text}
              onSelect={month => {
                this.setState({
                  monthSelected: month
                });
              }}
            />
          </StyledCol>
          <StyledCol>
            <StyledLabel label="Año" />
            <StyledDropdownInput
              list={years}
              label={yearSelected.text}
              onSelect={year => {
                this.setState({
                  yearSelected: year
                });
                onChangeYears(year);
              }}
            />
          </StyledCol>
        </StyledCols>
        <StyledButtonsWrapper>
          {/* <StyledButtonGhost ghost>VER</StyledButtonGhost> */}
          <StyledButtonSecondary
            secondary
            onClick={() => {
              onClickBtn(yearSelected, monthSelected);
            }}
          >
            DESCARGAR
          </StyledButtonSecondary>
        </StyledButtonsWrapper>
      </StyledPreviousPayrolls>
    );
  }
}

PreviousPayrolls.propTypes = {
  months: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.number,
      text: PropTypes.string,
      id: PropTypes.number
    })
  ),
  years: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.number,
      text: PropTypes.string,
      id: PropTypes.string
    })
  ),
  onChangeYears: PropTypes.func,
  onClickBtn: PropTypes.func
};

export default PreviousPayrolls;
