import React from 'react';
import PropTypes from 'prop-types';

import {
  StyledBoxApps,
  StyledTitleApps,
  StyledIconCloseApps,
  StyledIconCollapse,
  StyledIconArrow
} from './style';
import { Apps } from '../Apps';
import { Back, Close, Colapse } from '../../../../assets/images/index';

/** HOC */
import withOutsideClick from '../../views/Common/ClickOutside';

const MyApps = ({ className, onClickBack }) => (
  <StyledBoxApps className={className}>
    <StyledIconCollapse color="main" size="s" svg={Colapse} />
    <StyledIconCloseApps size="m" svg={Close} onClick={onClickBack} />
    <StyledTitleApps>
      <StyledIconArrow
        onClick={onClickBack}
        size="xs"
        color="light"
        svg={Back}
      />{' '}
      Accesos directos
    </StyledTitleApps>
    <Apps onClickBack={onClickBack} dark />
  </StyledBoxApps>
);

MyApps.propTypes = {
  className: PropTypes.string,
  onClickBack: PropTypes.func
};

export default withOutsideClick(MyApps);
