import {
  compose,
  setDisplayName,
  pure,
  branch,
  renderComponent,
  renderNothing
} from 'recompose';
import Widget from '../../views/WidgetList/Widget';
import MenuDay from '../Menu/MenuDay';
import PressResume from '../PressResume/home';
import AudiencesHome from '../Audiences/audiencesHome';
import RankingHome from '../Audiences/rankingHome';
import PressRelease from '../PressRelease/PressReleaseHome';
import { Apps } from '../../core/Apps';

export default compose(
  setDisplayName('WidgetContainer'),
  branch(
    props => props.type === 'menuofday',
    renderComponent(MenuDay),
    branch(
      props => props.type === 'resumen',
      renderComponent(PressResume),
      branch(
        props => props.type === 'audiences',
        renderComponent(AudiencesHome),
        branch(
          props => props.type === 'pressrelease',
          renderComponent(PressRelease),
          branch(
            props => props.type === 'ranking',
            renderComponent(RankingHome),
            branch(
              props =>
                props.type === 'shortcut' || props.type === 'shortcutWidget',
              renderComponent(Apps),
              renderNothing
            )
          )
        )
      )
    )
  ),
  pure
)(Widget);
