import React from 'react';
import styled from 'styled-components';
import storiesOf from '../../../../.storybook/util';
import Buttons from '.';
import Icon from '../Icon';
import { Download } from '../../../../assets/images/index';

const StyledDiv = styled.div`
  background-color: ${props => props.theme.colors.primary};
  height: 100px;
  padding: 20px;
`;

storiesOf('UI Kit/Buttons', module)
  .add('Primary', () => <Buttons primary>Primary</Buttons>)
  .add('Secondary', () => <Buttons secondary>Secondary</Buttons>)
  .add('Dark', () => <Buttons dark>Dark</Buttons>)
  .add('Modal', () => <Buttons modal>Modal</Buttons>)
  .add('Ghost', () => (
    <StyledDiv>
      <Buttons ghost>Ghost</Buttons>{' '}
    </StyledDiv>
  ))
  .add('Default', () => <Buttons>Primary</Buttons>)
  .add('Fixed', () => (
    <Buttons secondary fixed>
      Solicitar cambio de domicilio
    </Buttons>
  ))
  .add('Primary Icon', () => (
    <Buttons>
      <Icon color="light" size="m" svg={Download} />
      Boton width icon
    </Buttons>
  ))
  .add('Transparent', () => (
    <Buttons transparent>
      Button with icon
      <Icon color="dark" size="m" svg={Download} />
    </Buttons>
  ))
  .add('Small', () => (
    <Buttons small primary>
      Small
    </Buttons>
  ));
