import { css } from 'styled-components';
import { rem } from 'polished';

import book from '../../../../assets/fonts/gotham-book.woff';
import bookItalic from '../../../../assets/fonts/gotham-book-italic.woff';
import medium from '../../../../assets/fonts/gotham-medium.woff';
import mediumItalic from '../../../../assets/fonts/gotham-medium-italic.woff';
import bold from '../../../../assets/fonts/gotham-bold.woff';
import boldItalic from '../../../../assets/fonts/gotham-bold-italic.woff';

const fonts = {
  book: '"Gotham Book", Helvetica, Arial, sans-serif',
  bookItalic: '"Gotham Book Italic", Helvetica, Arial, sans-serif',
  medium: '"Gotham Medium", Helvetica, Arial, sans-serif',
  mediumItalic: '"Gotham Medium Italic", Helvetica, Arial, sans-serif',
  bold: '"Gotham Bold", Helvetica, Arial, sans-serif',
  boldItalic: '"Gotham Bold Italic", Helvetica, Arial, sans-serif'
};

const fontSize = {
  xsmall: rem('12px'),
  small: rem('14px'),
  medium: rem('15px'),
  large: rem('18px'),
  xlarge: rem('20px')
};

// Carga de las fuentes
const fontsFace = css` 
  @font-face { 
      font-family: 'Gotham Book'; 
      src:  url('${book}');
      font-weight: 400;
  }
  @font-face { 
      font-family: 'Gotham Book Italic'; 
      src:  url('${bookItalic}');
      font-weight: 400;
  }
  @font-face { 
      font-family: 'Gotham Medium'; 
      src:  url('${medium}');
      font-weight: 500;
  }
  @font-face { 
      font-family: 'Gotham Medium Italic'; 
      src:  url('${mediumItalic}');
      font-weight: 500;
  }
  @font-face { 
      font-family: 'Gotham Bold'; 
      src:  url('${bold}');
      font-weight: 700;
  }
  @font-face { 
      font-family: 'Gotham Bold Italic'; 
      src:  url('${boldItalic}');
      font-weight: 700;
  }
`;

export { fonts, fontsFace, fontSize };
