import styled from 'styled-components';
import { rem } from 'polished';
import Button from '../Buttons';

const StyledPopupWrap = styled.div`
  padding: ${rem('20px')};
  width: 90%;
`;

const StyledPopupTitle = styled.h3`
  color: ${props => props.theme.colors.dark};
  font-family: ${props => props.theme.fonts.bold};
  font-size: ${rem('16px')};
  text-transform: uppercase;
  text-align: center;
`;

const StyledPopupDescription = styled.p`
  color: ${props => props.theme.colors.dark};
  font-size: ${rem('14px')};
  padding-top: ${rem('10px')};
  text-align: center;
`;

const StyledPopupButton = styled(Button)`
  display: block;
  margin-top: ${rem('22px')};
  text-align: center;
`;

export {
  StyledPopupWrap,
  StyledPopupTitle,
  StyledPopupDescription,
  StyledPopupButton
};
