import React from 'react';
import { storiesOf } from '@storybook/react';

import { Table, THead, TBody, TFoot, Row, Cell } from '.';

storiesOf('UI Kit/Table', module).add('Tabla simple', () => (
  <div style={{ overflowX: 'auto' }}>
    <Table summary="Tabla vacía" style={{ width: '506px' }}>
      <THead>
        <Row>
          <Cell />
          <Cell tagName="th" scope="col">
            Columna 1
          </Cell>
          <Cell tagName="th" scope="col">
            Columna 2
          </Cell>
          <Cell tagName="th" scope="col">
            Columna 3
          </Cell>
        </Row>
      </THead>
      <TBody>
        <Row>
          <Cell tagName="th" scope="row">
            Row 1
          </Cell>
          <Cell>dato 1</Cell>
          <Cell>dato 2</Cell>
          <Cell>dato 3</Cell>
        </Row>
        <Row selected>
          <Cell tagName="th" scope="row">
            Row 2
          </Cell>
          <Cell>dato 1</Cell>
          <Cell>dato 2</Cell>
          <Cell>dato 3</Cell>
        </Row>
        <Row>
          <Cell tagName="th" scope="row">
            Row 3
          </Cell>
          <Cell>dato 1</Cell>
          <Cell>dato 2</Cell>
          <Cell>dato 3</Cell>
        </Row>
      </TBody>
      <TFoot>
        <Row>
          <Cell tagName="th" scope="row">
            Row 3
          </Cell>
          <Cell>dato 1</Cell>
          <Cell>dato 2</Cell>
          <Cell>dato 3</Cell>
        </Row>
      </TFoot>
    </Table>
  </div>
));
