import React from 'react';
import PropTypes from 'prop-types';

import {
  StyledBoxNewsTab,
  StyledMultimedia,
  StyledBoxNewText,
  DeleteItemReadAfter,
  StyledTagCategory,
  StyledDateNews,
  OverwriteDateFormat,
  OverwriteHeadline,
  LinkHeader,
  Summary,
  StyleLinkRead,
  StyledBoxOptions,
  StyledOptionsNews
} from './style';

import ShareSend from '../OptionsNews/ShareSend';
import { CloseBg } from '../../../../assets/images/index';
import Multimedia from '../../views/Multimedia';

const NewsItem = ({
  className,
  baseContentId,
  title,
  dateFrom,
  mainTagName,
  mediaType,
  onToggleFullSize,
  image,
  videoNews,
  subtitle,
  orientation,
  onLike,
  onFavorite,
  onShare,
  onComment,
  favorite,
  like,
  share,
  numberOfComments,
  readAfter,
  onClick
}) => (
  <StyledBoxNewsTab className={className}>
    {(image || videoNews) && (
      <StyledMultimedia orientation={orientation}>
        <Multimedia
          media={mediaType}
          src={image || videoNews}
          onToggleFullSize={onToggleFullSize}
          home
        />
      </StyledMultimedia>
    )}

    {readAfter && (
      <DeleteItemReadAfter
        svg={CloseBg}
        color="dark"
        size="m"
        onClick={onClick}
      />
    )}

    <StyledBoxNewText orientation={orientation}>
      {mainTagName && <StyledTagCategory category={mainTagName} />}

      {dateFrom && (
        <StyledDateNews>
          Publicado{' '}
          <OverwriteDateFormat fromNow>
            {new Date(dateFrom)}
          </OverwriteDateFormat>
        </StyledDateNews>
      )}
      <OverwriteHeadline>
        <LinkHeader to={`/noticia/${baseContentId}`}>{title}</LinkHeader>
      </OverwriteHeadline>
      {subtitle && <Summary text={subtitle} maxlength={180} />}
      {/* Debería usar LINK. Habría que cambiarlo en los estilos */}
      <StyleLinkRead to={`/noticia/${baseContentId}`} title="leer más">
        + LEER MÁS
      </StyleLinkRead>
      <StyledBoxOptions>
        <StyledOptionsNews
          numberIcon={numberOfComments}
          onLike={onLike}
          onFavorite={onFavorite}
          onShare={onShare}
          onComment={onComment}
          activeLike={like}
          activeFavorite={favorite}
          readAfter
          column
        />
      </StyledBoxOptions>
      <ShareSend activeShare={share} />
    </StyledBoxNewText>
  </StyledBoxNewsTab>
);

NewsItem.propTypes = {
  className: PropTypes.string,
  orientation: PropTypes.string,
  title: PropTypes.string,
  subtitle: PropTypes.string,
  mainTagName: PropTypes.string,
  numberOfComments: PropTypes.number,
  share: PropTypes.func,
  onShare: PropTypes.func,
  onLike: PropTypes.func,
  onFavorite: PropTypes.func,
  onComment: PropTypes.func,
  onClick: PropTypes.func,
  like: PropTypes.bool,
  favorite: PropTypes.bool,
  readAfter: PropTypes.bool,
  author: PropTypes.shape({
    user: PropTypes.shape({ name: PropTypes.string }),
    association: PropTypes.shape({ name: PropTypes.string })
  }),
  dateFrom: PropTypes.instanceOf(Date),
  mediaType: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.string,
    PropTypes.string
  ])
};

export default NewsItem;
