import styled from 'styled-components';
import { rem } from 'polished';
import Icon from '../Icon';
import mediaqueries from '../../../config/themes/variables/mediaqueries';

export const StyledButtonArrowRight = styled.button`
  align-items: center;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  display: flex;
  height: ${rem('40px')};
  right: 0;
  justify-content: center;
  outline: none;
  position: absolute;
  top: 50%;
  transition: all 0.3s ease-in 0s;
  width: ${rem('40px')};
  z-index: 12;
  ${mediaqueries.greaterThan('desktop')`
    background-color: ${props => props.theme.colors.dark}B0;
  `};
`;

export const StyledButtonArrowLeft = StyledButtonArrowRight.extend`
  left: 0;
`;

export const StyledIcon = styled(Icon)`
  height: ${rem('16px')};
  width: ${rem('8px')};
`;

export const StyledImage = styled.img`
  height: auto;
  width: 100%;
`;
