import React from 'react';
import StoryRouter from 'storybook-react-router';
import storiesOf from '../../../../.storybook/util';
import ContentItem from './ContentItem';
import DocumentItem from './DocumentItem';
import GroupItem from './GroupItem';
import PersonItem from './PersonItem';
import AvatarPez from '../../../../assets/images/avatars/axis3/pez.svg';

const styleDiv = {
  maxWidth: '900px',
  margin: 'auto'
};

storiesOf('core/ResultItem', module)
  .addDecorator(StoryRouter())
  .add('Content with image', () => (
    <div style={styleDiv}>
      <ContentItem
        imgSrc="http://via.placeholder.com"
        title="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt"
        date="12/01/1990"
        text="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
            minim veniam, quis nostrud exercitation ullamco laboris nisi ut
            aliquip ex ea commodo consequat."
      />
    </div>
  ))
  .add('Content without image', () => (
    <div style={styleDiv}>
      <ContentItem
        title="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt"
        date="12/01/1990"
        text="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
        eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
        minim veniam, quis nostrud exercitation ullamco laboris nisi ut
        aliquip ex ea commodo consequat."
      />
    </div>
  ))
  .add('Document', () => (
    <div style={styleDiv}>
      <DocumentItem
        title="Lorem_ipsum_dolor_sit_amet_consectetur_adipiscing_elit"
        date="12/01/1990"
        path="doc"
      />
    </div>
  ))
  .add('Group', () => (
    <div style={styleDiv}>
      <GroupItem title="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt" />
    </div>
  ))
  .add('Person', () => (
    <div style={styleDiv}>
      <PersonItem
        title="Lorem ipsum dolor sit"
        subtitle="Periodista digital"
        imgSrc="http://via.placeholder.com/150x150"
        avatar={AvatarPez}
        text="Redacción y contenidos"
        name="Sandra"
        lastname="López"
      />
    </div>
  ));
