import React from 'react';
import PropTypes from 'prop-types';
import { StyledImage } from './style';

import ImageDesktop from '../../../../assets/images/gallery/gallery-desktop.jpg';
import ImageTablet from '../../../../assets/images/gallery/gallery-tablet.jpg';

const ImageGalleries = ({ className, children }) => (
  <picture className={className}>
    <source
      type="image/jpeg"
      media="(min-width:1280px)"
      srcSet={ImageDesktop}
    />
    <source type="image/jpeg" media="(min-width:720px)" srcSet={ImageTablet} />
    {children}
  </picture>
);

ImageGalleries.propTypes = {
  className: PropTypes.string,
  children: PropTypes.node
};

export default ImageGalleries;

const Imagebase = ({ className, src }) => (
  <StyledImage className={className} alt="" src={src} />
);

Imagebase.propTypes = {
  className: PropTypes.string,
  src: PropTypes.string
};

export { Imagebase, ImageGalleries };
