import React from 'react';
import PropTypes from 'prop-types';
import {
  StyleList,
  StyleInterestItem,
  StyleBoxEmployee,
  StyleEditIcon
} from './style';
import { Pencil } from '../../../../assets/images/index';

const Resume = (dataInterests, limit) => (
  <StyleList>
    {dataInterests.slice(0, limit).map(item => (
      <StyleInterestItem key={item}>{item}</StyleInterestItem>
    ))}
  </StyleList>
);

const Interests = ({ limit, dataInterests, onEditMode, clickEditIcon }) => (
  <StyleBoxEmployee
    title="Intereses"
    more={dataInterests && dataInterests.length > limit}
    moreInfo={Resume(dataInterests, dataInterests ? dataInterests.length : 0)}
  >
    {onEditMode && (
      <StyleEditIcon
        svg={Pencil}
        size="m"
        color="dark"
        onClick={clickEditIcon}
        hover
      />
    )}
    {Resume(dataInterests, limit)}
  </StyleBoxEmployee>
);

Interests.propTypes = {
  dataInterests: PropTypes.arrayOf(PropTypes.string),
  limit: PropTypes.number,
  onEditMode: PropTypes.bool,
  clickEditIcon: PropTypes.func
};

export default Interests;
