import React from 'react';
import { AvatarEdition } from '../../../core/AvatarEdition';

const ChooseAvatar = props => (
  <AvatarEdition
    dataFormAvatar={props.userAvatarData}
    edit
    getAvatar={props.chosseAvatar}
    avatar={props.avatar}
    setAvatar={props.setAvatar}
    {...props}
  />
);

export default ChooseAvatar;
