import styled from 'styled-components';
import { rem } from 'polished';
import Icon from '../Icon';
import mediaqueries from '../../../config/themes/variables/mediaqueries';

export const StyledModal = styled.div`
  background-color: ${props => props.theme.colors.light};
  display: flex;
  height: 100%;
  left: 0;
  overflow: auto;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;

  ${mediaqueries.greaterThan('desktop')`
    background-color: rgba(0, 0, 0, 0.9);
    top: 0;
  `};
`;

export const StyledModalContent = styled.div`
  margin: auto;
  padding: 0 ${rem('15px')} ${rem('10px')} ${rem('15px')};
  width: 100%;
  ${mediaqueries.greaterThan('mobile')`
    margin: 0 auto auto;
    min-width: ${rem('720px')};
    padding: ${rem('150px')} ${rem('85px')} ${rem('10px')} ${rem('85px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    background-color: ${props => props.theme.colors.light};
    margin: auto;
    max-width: ${rem('794px')};
    max-height: 80vh;
    /*min-height: 343px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;*/
    overflow: auto;
    padding: ${rem('50px')} ${rem('100px')};
    position: relative;
  `};
`;

export const StyledIconClose = styled(Icon)`
  height: ${rem('19px')};
  position: absolute;
  right: ${rem('32px')};
  top: ${rem('33px')};
  width: ${rem('19px')};
  ${mediaqueries.greaterThan('desktop')`
    height: ${rem('14px')};
    right: ${rem('21px')};
    top: ${rem('21px')};
    width: ${rem('14px')};
  `};
`;

export const StyledTitle = styled.h2`
  font-family: ${props => props.theme.fonts.bold};
  font-size: ${rem('16px')};
  margin: ${rem('35px')} 0 ${rem('18px')} 0;
  padding: 0 ${rem('26px')};
  text-transform: uppercase;
  ${mediaqueries.greaterThan('mobile')`
    padding: 0;
    text-align: center;   
    margin-top: 0;
  `};
`;
