import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledSummaryProfile,
  StyledBoxCentral,
  StyledExperience,
  StyledBoxExperienceInfo,
  StyledBoxInfo,
  StyledBoxDual,
  StyledEducation,
  StyledSkillUser,
  StyledWrapEmployeeInfoInterest,
  StyledWrapEmployeeInfoLanguages,
  StyledWrapEmployeeInfoVolunteer,
  StyledWrapEmployeeInfoAwards,
  StyledBoxProjects,
  StyledProjects,
  StyledProfileName
} from './style';

import {
  SOCIAL,
  ABOUT_YOU,
  INTERESTS,
  SKILLS,
  VOLUNTEER,
  AWARDS,
  LANGUAGE,
  EDUCATION,
  EXPERIENCE,
  MOD_EXPERIENCE_ACTUAL,
  MOD_EXPERIENCE_PAST,
  DELETE_EXPERIENCE_PAST
} from '../../../../constants/modals';

import { SAVE } from '../../../../constants/literals/profile';

import OurTeam from '../../../core/OurTeam/OurTeam';
import Languages from '../../../core/BoxEmployeeInfo/Languages';
import Interests from '../../../core/BoxEmployeeInfo/Interests';
import Volunteer from '../../../core/BoxEmployeeInfo/Volunteer';
import Award from '../../../core/BoxEmployeeInfo/Award';

const Profile = ({
  title,
  children,
  dataExperience,
  dataLanguage,
  dataInterest,
  dataVolunteer,
  dataAward,
  dataEducations,
  dataSkill,
  dataTeam,
  dataUser,
  dataSocial,
  editMode,
  clickAddIconHandler,
  clickEditIconHandler,
  clickEditExperiencePastIconHandler,
  clickDeleteExperienceIconHandler,
  dataProjects
}) => (
  <React.Fragment>
    <StyledProfileName>{`${dataUser.name} ${dataUser.lastname}`}</StyledProfileName>
    <StyledSummaryProfile
      title={title}
      dataUser={dataUser}
      dataSocial={dataSocial}
      onEditMode={editMode}
      clickEditIcon={() => clickEditIconHandler(ABOUT_YOU)}
      clickSocialEditIcon={() => clickEditIconHandler(SOCIAL)}
    >
      {children}
    </StyledSummaryProfile>
    {dataProjects && (
      <StyledBoxProjects>
        <StyledProjects dataProjects={dataProjects} onEditMode={editMode} />
      </StyledBoxProjects>
    )}
    <StyledBoxCentral>
      <StyledBoxExperienceInfo>
        <StyledExperience
          dataExperience={dataExperience}
          onEditMode={editMode}
          clickAddIcon={() => clickAddIconHandler(EXPERIENCE)}
          clickEditIcon={() => clickEditIconHandler(MOD_EXPERIENCE_ACTUAL)}
          clickEditExperiencePastIcon={experience =>
            clickEditExperiencePastIconHandler(MOD_EXPERIENCE_PAST, experience)
          }
          clickDeleteIcon={experience =>
            clickDeleteExperienceIconHandler(DELETE_EXPERIENCE_PAST, experience)
          }
        />

        <StyledBoxInfo>
          {(dataLanguage.length !== 0 || editMode) && (
            <StyledWrapEmployeeInfoInterest>
              <Languages
                clickEditIcon={() => clickEditIconHandler(LANGUAGE)}
                dataLanguage={dataLanguage}
                limit={3}
                onEditMode={editMode}
              />
            </StyledWrapEmployeeInfoInterest>
          )}
          {(dataInterest.length !== 0 || editMode) && (
            <StyledWrapEmployeeInfoLanguages>
              <Interests
                clickEditIcon={() => clickEditIconHandler(INTERESTS)}
                dataInterests={dataInterest}
                limit={5}
                onEditMode={editMode}
              />
            </StyledWrapEmployeeInfoLanguages>
          )}
          {(dataVolunteer.length !== 0 || editMode) && (
            <StyledWrapEmployeeInfoVolunteer>
              <Volunteer
                clickEditIcon={() => clickEditIconHandler(VOLUNTEER)}
                dataVolunteer={dataVolunteer}
                limit={3}
                onEditMode={editMode}
              />
            </StyledWrapEmployeeInfoVolunteer>
          )}
          {(dataAward.length !== 0 || editMode) && (
            <StyledWrapEmployeeInfoAwards>
              <Award
                dataAward={dataAward}
                limit={3}
                onEditMode={editMode}
                clickEditIcon={() => clickEditIconHandler(AWARDS)}
              />
            </StyledWrapEmployeeInfoAwards>
          )}
        </StyledBoxInfo>
      </StyledBoxExperienceInfo>
      <StyledBoxDual>
        <StyledEducation
          dataEducations={dataEducations}
          limit={2}
          onEditMode={editMode}
          clickEditIcon={() => clickEditIconHandler(EDUCATION)}
        />
        <StyledSkillUser
          dataSkill={dataSkill}
          clickEditIcon={() => clickEditIconHandler(SKILLS)}
          limit={3}
          onEditMode={editMode}
        />
      </StyledBoxDual>
    </StyledBoxCentral>
    <OurTeam title="Nuestro equipo" dataTeam={dataTeam} />
  </React.Fragment>
);

Profile.propTypes = {
  title: PropTypes.string,
  children: PropTypes.node,
  dataUser: PropTypes.shape({
    extension: PropTypes.number,
    mobile: PropTypes.string,
    address: PropTypes.string
  }),
  dataSocial: PropTypes.shape({
    facebook: PropTypes.string,
    instagram: PropTypes.string,
    twitter: PropTypes.string,
    vimeo: PropTypes.string,
    linkedin: PropTypes.string
  }),
  dataExperience: PropTypes.arrayOf(
    PropTypes.shape({
      company: PropTypes.string,
      job: PropTypes.string,
      paraGraph: PropTypes.string,
      dateFrom: PropTypes.string,
      dateThrough: PropTypes.string
    })
  ),
  dataLanguage: PropTypes.arrayOf(
    PropTypes.shape({
      language: PropTypes.string,
      level: PropTypes.string,
      certificate: PropTypes.string
    })
  ),
  dataInterest: PropTypes.arrayOf(PropTypes.string),
  dataVolunteer: PropTypes.arrayOf(
    PropTypes.shape({
      organization: PropTypes.string,
      role: PropTypes.string
    })
  ),
  dataAward: PropTypes.arrayOf(
    PropTypes.shape({
      date: PropTypes.string,
      name: PropTypes.string
    })
  ),
  dataEducations: PropTypes.arrayOf(
    PropTypes.shape({
      degree: PropTypes.string,
      endDate: PropTypes.string,
      schoolName: PropTypes.string,
      startDate: PropTypes.string
    })
  ),
  dataSkill: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      skillLevel: PropTypes.string
    })
  ),
  editMode: PropTypes.bool,
  clickAddIconHandler: PropTypes.func,
  clickEditIconHandler: PropTypes.func,
  clickEditExperiencePastIconHandler: PropTypes.func,
  clickDeleteExperienceIconHandler: PropTypes.func
};

Profile.defaultProps = {
  title: 'conóceme un poco mejor',
  dataLanguage: [],
  dataInterest: [],
  dataVolunteer: [],
  dataAward: []
};

export default Profile;
