import request from './api';
import {
  API_DEPARTMENT_ID,
  API_DEPARTMENT_DATA,
  API_DEPARTMENT_DOCS,
  API_DEPARTMENT_TEAM
} from '../constants/api';

/** Search department */
/**
 * Retrieves the id of the department.
 * @param {string} name
 * @param {string} orderby    NAME
 * @param {string} size         10
 * @param {string} page          0
 * @param {string} direction   ASC
 */
// orderby, name,lastname,idDepartment,extension,phones,size,page,direction
export const getDepartmentId = ({ name }) =>
  request(API_DEPARTMENT_ID, {
    method: 'GET',
    params: {
      orderby: 'NAME',
      name,
      size: 10,
      page: 0,
      direction: 'ASC'
    }
  });

export const getDepartmentDocs = departmentId =>
  request(API_DEPARTMENT_DOCS, {
    method: 'GET',
    params: {
      associationId: departmentId
    }
  });

export const getDepartmentData = departmentId =>
  request(`${API_DEPARTMENT_DATA}/${departmentId}`, {
    method: 'GET'
  });

export const getDepartmentTeam = departmentId =>
  request(API_DEPARTMENT_TEAM.replace('0000', departmentId), { method: 'GET' });

export const getDepartmentFolder = folderId =>
  request(`${API_DEPARTMENT_DOCS}/folders`, {
    method: 'GET',
    params: {
      folderId
    }
  });
