import styled, { css } from 'styled-components';
import { rem } from 'polished';

const StyledPillText = styled.h2`
  font-size: ${rem('16px')};
  font-family: ${props => props.theme.fonts.medium};
  color: ${props => props.theme.colors.dark_2};
  margin: 0;
  font-weight: initial;
  @media (min-width: ${props => props.theme.mediaqueries.desktop}) {
    margin: 0 ${rem('20px')} ${rem('25px')};
    font-size: ${rem('18px')};
    font-family: ${props => props.theme.fonts.regular};
  }
`;

const StyledPill = styled.div`
  padding: ${rem('25px')};
  background-color: ${props => props.theme.colors.light};
  border: ${rem('1px')} solid ${props => props.theme.colors.grayLight};
  text-align: center;
  position: relative;
  width: ${rem('180px')};
  height: ${rem('200px')};
  display: flex;
  align-items: center;
  margin-bottom: ${rem('22px')};
  transition: all 0.3s ease-in-out;
  &:hover {
    background-color: ${props => props.theme.colors.primary};
    cursor: pointer;
    ${StyledPillText} {
      color: ${props => props.theme.colors.light};
    }
  }

  ${props =>
    props.featured &&
    css`
      background-color: ${props => props.theme.colors.dark_8};
      border: ${rem('1px')} solid ${props => props.theme.colors.dark_4};
    `};
  @media (min-width: ${props => props.theme.mediaqueries.desktop}) {
    padding: ${rem('40px')} ${rem('30px')};
  }
`;

export { StyledPill, StyledPillText };
