import React from 'react';
import StoryRouter from 'storybook-react-router';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import { createStore, applyMiddleware } from 'redux';
import PropTypes from 'prop-types';
import rootReducer from '../../../../reducers';
import storiesOf from '../../../../../.storybook/util';
import BoxInside from '../../../core/BoxInside';
import CalendarFood from '../../../core/CalendarLunch/CalendarLaunch';
import EventsCalendarHeader from '../../../core/CalendarLunch/EventsCalendarHeader';
import { StyledMonthHeader } from '../../../core/Calendar/style';

import {
  WrapperFlex,
  WrapperFoodPayFlex,
  WrapperCoupons,
  WrapperCalendar,
  TitleFoodPay,
  StyledFoodPayAvalaible,
  StyledFoodPayConsumed,
  StyleEventTitle,
  StyleEventText
} from './style';

const events = [
  {
    id: 0,
    title: '14:30',
    start: new Date(new Date().setDate(1)),
    end: new Date(new Date().setDate(1))
  },
  {
    id: 1,
    title: '15:30',
    start: new Date(new Date().setDate(1)),
    end: new Date(new Date().setDate(1))
  },

  {
    id: 2,
    title: '13.30',
    start: new Date(new Date().setDate(2)),
    end: new Date(new Date().setDate(2))
  },

  {
    id: 3,
    title: '14:30',
    start: new Date(new Date().setDate(2)),
    end: new Date(new Date().setDate(2))
  },

  {
    id: 4,
    title: '15:00',
    start: new Date(new Date().setDate(14)),
    end: new Date(new Date().setDate(14))
  },
  {
    id: 5,
    title: '18:30',
    start: new Date(new Date().setDate(14)),
    end: new Date(new Date().setDate(14))
  },
  {
    id: 6,
    title: '10:30',
    start: new Date(new Date().setDate(7)),
    end: new Date(new Date().setDate(7))
  },
  {
    id: 7,
    title: '17:30',
    start: new Date(new Date().setDate(15)),
    end: new Date(new Date().setDate(15))
  },
  {
    id: 8,
    title: '19:30',
    start: new Date(new Date().setDate(27)),
    end: new Date(new Date().setDate(27))
  },
  {
    id: 14,
    title: '13:18',
    start: new Date(new Date().setHours(new Date().getHours() - 3)),
    end: new Date(new Date().setHours(new Date().getHours() + 3))
  },
  {
    id: 15,
    title: '15:30',
    start: new Date(new Date().setHours(new Date().getHours() - 3)),
    end: new Date(new Date().setHours(new Date().getHours() + 3))
  },
  {
    id: 16,
    title: '18:30',
    start: new Date(new Date().setHours(new Date().getHours() - 3)),
    end: new Date(new Date().setHours(new Date().getHours() + 3))
  },
  {
    id: 17,
    title: '19:30',
    start: new Date(new Date().setHours(new Date().getHours() - 3)),
    end: new Date(new Date().setHours(new Date().getHours() + 3))
  }
];

const EventMonth = [
  {
    eventHour: [
      {
        eventHourStart: '13:30'
      }
    ]
  }
];

function Event({ event }) {
  return (
    <StyleEventTitle>
      <StyleEventText>{event.title}</StyleEventText>
    </StyleEventTitle>
  );
}

const FormattedMonthHeader = ({ label, date }) => {
  const newLabel = label.slice(0, -1);
  if (date.getDay() === 6 || date.getDay() === 0) {
    return <StyledMonthHeader weekend>{newLabel}</StyledMonthHeader>;
  }
  return <StyledMonthHeader>{newLabel}</StyledMonthHeader>;
};

FormattedMonthHeader.propTypes = {
  label: PropTypes.string,
  date: PropTypes.instanceOf(Date)
};

const components = {
  toolbar: EventsCalendarHeader,
  event: Event,
  month: {
    header: FormattedMonthHeader
  }
};

const initialState = {};

const store = createStore(rootReducer, initialState, applyMiddleware(thunk));

storiesOf('views/Empleados/Internos/Cupones comida', module)
  .addDecorator(StoryRouter())
  .addDecorator(getStory => <Provider store={store}>{getStory()}</Provider>)
  .add('default', () => (
    <div className="pressResume_container" style={{ backgroundColor: '#FFF' }}>
      <BoxInside>
        <WrapperFlex>
          <WrapperCoupons>
            <TitleFoodPay>Tus vales de comida para este mes</TitleFoodPay>
            <WrapperFoodPayFlex>
              <StyledFoodPayAvalaible type="Disponibles" quantity={12} />
              <StyledFoodPayConsumed type="Consumidos" quantity={10} />
            </WrapperFoodPayFlex>
          </WrapperCoupons>
          <WrapperCalendar>
            <CalendarFood
              events={events}
              components={components}
              dateDay="Jun 02 2018"
              dataEvent={EventMonth}
            />
          </WrapperCalendar>
        </WrapperFlex>
      </BoxInside>
    </div>
  ));
