import React from 'react';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import { PressResumeDate } from '.';

storiesOf('views/PressResume Date', module)
  .addDecorator(withReadme(Readme))
  .add('Default', () => (
    <div style={{ maxWidth: '562px', margin: '0 auto' }}>
      <PressResumeDate dayWeek="Mie" numberWeek="05" text="05_septiembre.pdf" />
    </div>
  ));
