import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import {
  MainDropdown,
  Selector,
  LabelDropdown,
  IconDropdown,
  ListDropdown
} from './styleMainDropdown';
import { Colapse } from '../../../../assets/images/index';

class Dropdown extends Component {
  constructor(props) {
    super(props);
    this.onClick = this.onClick.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);
    this.state = { show: false };
    document.addEventListener('click', this.handleClickOutside);
  }

  onClick() {
    const { show } = this.state;
    this.setState({ show: !show });
  }

  handleKeyPress(e) {
    if (e.key === 'Enter') {
      this.onClick();
    }
  }

  handleClickOutside(e) {
    const el = ReactDOM.findDOMNode(this.svgButton);
    const isInside = el.contains(e.target) || el === e.target;
    if (!isInside) {
      this.setState({ show: false });
    }
  }

  render() {
    const { className, optionSelected, subname, children } = this.props;

    return (
      <MainDropdown
        className={className}
        ref={svg => {
          this.svgButton = svg;
        }}
      >
        <Selector onClick={this.onClick}>
          <LabelDropdown>{optionSelected}</LabelDropdown>
          <IconDropdown
            id={`dropdown${subname}`}
            color="main"
            size="s"
            svg={Colapse}
            onKeyPress={this.handleKeyPress}
            show={this.state.show}
            expanded={this.state.show}
            haspopup
            hover
          />
        </Selector>
        <ListDropdown
          show={this.state.show}
          aria-labelledby={`dropdown${subname}`}
        >
          {children}
        </ListDropdown>
      </MainDropdown>
    );
  }
}

Dropdown.propTypes = {
  className: PropTypes.string,
  optionSelected: PropTypes.string,
  subname: PropTypes.string
};

Dropdown.defaultProps = {
  subname: ''
};

export default Dropdown;
