import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Button from '../../core/Buttons';

import { StyledModal, StyledInput } from './style';

class ModalPassword extends Component {
  constructor(props) {
    super(props);

    this.state = {
      currentPassword: '',
      isPasswordCorrect: !props.errorPassword
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errorPassword !== this.props.errorPassword) {
      this.setState({
        isPasswordCorrect: !nextProps.errorPassword
      });
    }
  }

  render() {
    const { clickCloseIcon, action } = this.props;
    const { currentPassword, isPasswordCorrect } = this.state;

    return (
      <StyledModal
        title="CONFIRMA TU CONTRASEÑA"
        clickCloseIcon={clickCloseIcon}
      >
        <p>Introduce tu contraseña para iniciar la descarga</p>
        <StyledInput
          autoComplete="new-password"
          type="password"
          placeholder="Contraseña"
          error={!isPasswordCorrect && `Contraseña incorrecta`}
          onChange={value => {
            this.setState({
              currentPassword: value
            });
          }}
        />
        <Button
          secondary
          onClick={() => {
            action(currentPassword);
          }}
        >
          DESCARGAR
        </Button>
      </StyledModal>
    );
  }
}

ModalPassword.propTypes = {
  action: PropTypes.func,
  clickCloseIcon: PropTypes.func,
  errorPassword: PropTypes.bool
};

export default ModalPassword;
