import React from 'react';
import storiesOf from '../../../../.storybook/util';
import Education from '.';
import { dataEducations } from '../../../utils/mocks/js/education';

const styleItem = {
  maxWidth: '580px',
  margin: 'auto'
};

storiesOf('core/Education', module).add('Default', () => (
  <div>
    <div style={styleItem}>
      <Education title="Mi formación" dataEducations={dataEducations} />
    </div>
    <br />
    <br />
    <div style={styleItem}>
      <Education title="Mi formación" dataEducations={dataEducations} more />
    </div>
    <br />
    <br />
  </div>
));
