import React from 'react';
import PropTypes from 'prop-types';

import {
  WrapperResultItem,
  WrapperHeader,
  WrapperBody,
  ResultTitle,
  StyledDateFormat,
  WrapperIcon
} from './style';

import Icon from '../Icon';

import { Documents } from '../../../../assets/images';

const ContentItem = ({ className, title, date, urlLink, onClick }) => (
  <WrapperResultItem className={className} to={urlLink} onClick={onClick}>
    <WrapperBody>
      <WrapperHeader>
        <WrapperIcon>
          <Icon svg={Documents} size="m" hover />
        </WrapperIcon>
        <div>
          <ResultTitle>{title}</ResultTitle>
          <StyledDateFormat format="DD/MM/YY">{date}</StyledDateFormat>
        </div>
      </WrapperHeader>
    </WrapperBody>
  </WrapperResultItem>
);

ContentItem.propTypes = {
  className: PropTypes.string,
  urlLink: PropTypes.string,
  title: PropTypes.string,
  date: PropTypes.string,
  onClick: PropTypes.func
};

ContentItem.defaultProps = {
  urlLink: '/'
};

export default ContentItem;
