import React from 'react';
import PropTypes from 'prop-types';

import BoxEmpty from '../BoxEmpty';

import {
  MainWrapper,
  WrapperBox,
  BoxSummary,
  Title,
  Contact,
  StyleEditIcon,
  StyledContact
} from './style';
import { Pencil } from '../../../../assets/images/index';

const validateEmptyChildren = child => {
  if (child && child.props && child.props.dangerouslySetInnerHTML) {
    if (
      child.props.dangerouslySetInnerHTML.__html === undefined ||
      child.props.dangerouslySetInnerHTML.__html === '<p><br></p>' ||
      child.props.dangerouslySetInnerHTML.__html === '<h1><br></h1>' ||
      child.props.dangerouslySetInnerHTML.__html === '<h2><br></h2>' ||
      child.props.dangerouslySetInnerHTML.__html === '<h3><br></h3>'
    ) {
      return true;
    }
    return false;
  }
  return true;
};

const SummaryProfile = ({
  className,
  title,
  children,
  dataUser,
  dataSocial,
  onEditMode,
  clickEditIcon,
  clickSocialEditIcon
}) => (
  <MainWrapper className={className}>
    <WrapperBox>
      <BoxSummary>
        {title && (
          <Title>
            {title}
            {onEditMode && (
              <StyleEditIcon
                svg={Pencil}
                size="m"
                color="dark"
                hover
                onClick={clickEditIcon}
              />
            )}
          </Title>
        )}
        {validateEmptyChildren(children) ? <BoxEmpty /> : children}
      </BoxSummary>
      {(dataUser || dataSocial) && (
        <StyledContact className="contact">
          <Contact
            dataUser={dataUser}
            dataSocial={dataSocial}
            onEditMode={onEditMode}
            onClick={clickSocialEditIcon}
          />
        </StyledContact>
      )}
    </WrapperBox>
  </MainWrapper>
);

SummaryProfile.propTypes = {
  className: PropTypes.string,
  title: PropTypes.string,
  children: PropTypes.node,
  dataUser: PropTypes.shape({
    extension: PropTypes.number,
    mobile: PropTypes.string,
    address: PropTypes.string
  }),
  dataSocial: PropTypes.shape({
    facebook: PropTypes.string,
    instagram: PropTypes.string,
    twitter: PropTypes.string,
    vimeo: PropTypes.string,
    linkedin: PropTypes.string
  }),
  onEditMode: PropTypes.bool,
  clickEditIcon: PropTypes.func,
  clickSocialEditIcon: PropTypes.func
};

export default SummaryProfile;
