import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import uuid from 'uuid';

import {
  StyledBoxContent,
  StyledBoxDown,
  StyledButton,
  StyledButtonPlus
} from './style';

import SkillItem from './SkillItem';
import Modal from '../../../core/Modal';
import { SKILLS } from '../../../../constants/literals/modals';
import { moveElementPosition } from '../../../../utils/splices';

class Skills extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.dataSkills,
      btnIsDisabled: true
    };
  }

  componentDidMount() {
    const { data } = this.state;

    if (data.length === 0) {
      this.handlePlus();
    }
  }

  onClickDelete = index => {
    const { data } = this.state;
    const updatedData = data.filter((ele, i) => i !== index);

    this.setState({
      data: updatedData,
      btnIsDisabled: this.getBtnStatus(updatedData)
    });
  };

  onClickOrderArrow = (index, isDown) => {
    const { data } = this.state;
    const move = isDown ? index + 1 : index - 1;
    const updatedData = moveElementPosition([...data], index, move);

    this.setState({
      data: updatedData,
      btnIsDisabled: this.getBtnStatus(updatedData)
    });
  };

  getBtnStatus = data => {
    const { dataSkills } = this.props;

    return _.isEqual(dataSkills, data);
  };

  handleAccept = () => {
    const { onAcceptModal } = this.props;
    const { data } = this.state;
    const updatedData = data.filter(ele => ele.name && ele.skillLevel);

    onAcceptModal(updatedData);
  };

  handleChange = (value, index, key) => {
    const { data } = this.state;
    const clonedData = [...data];

    clonedData[index] = { ...clonedData[index], [key]: value };

    this.setState({
      data: clonedData,
      btnIsDisabled: this.getBtnStatus(clonedData)
    });
  };

  handleChangeRadio = (index, key, event) => {
    const { data } = this.state;
    const clonedData = [...data];

    clonedData[index] = { ...clonedData[index], [key]: event.target.value };

    this.setState({
      data: clonedData,
      btnIsDisabled: this.getBtnStatus(clonedData)
    });
  };

  handlePlus = () => {
    const { data } = this.state;
    const clonedData = [...data];

    clonedData.push({
      name: '',
      skillLevel: 'MEDIUM'
    });

    this.setState({ data: clonedData });
  };

  render() {
    const { onCloseModal } = this.props;
    const { data, btnIsDisabled } = this.state;

    return (
      <Modal title={SKILLS.TITLE} clickCloseIcon={onCloseModal}>
        <StyledBoxContent>
          {data.map((item, index) => (
            <SkillItem
              key={uuid()}
              name={item.name}
              skillLevel={item.skillLevel}
              order={index + 1}
              onClickDown={() => this.onClickOrderArrow(index, true)}
              onClickUp={() => this.onClickOrderArrow(index, false)}
              onClickDelete={() => this.onClickDelete(index)}
              onChangeInput={e => this.handleChange(e, index, 'name')}
              onChangeRadio={e =>
                this.handleChangeRadio(index, 'skillLevel', e)
              }
            />
          ))}
          <StyledButtonPlus dark onClick={this.handlePlus} />
          <StyledBoxDown>
            <StyledButton onClick={onCloseModal} modal>
              {SKILLS.CANCEL}
            </StyledButton>
            <StyledButton
              onClick={this.handleAccept}
              secondary
              disabled={btnIsDisabled}
            >
              {SKILLS.ACCEPT}
            </StyledButton>
          </StyledBoxDown>
        </StyledBoxContent>
      </Modal>
    );
  }
}

Skills.propTypes = {
  dataSkills: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      skillLevel: PropTypes.string
    })
  ),
  onCloseModal: PropTypes.func,
  onAcceptModal: PropTypes.func
};

Skills.defaultProps = {
  dataSkills: []
};

export default Skills;
