import styled from 'styled-components';
import { rem } from 'polished';
import Icon from '../Icon';
import mediaqueries from '../../../config/themes/variables/mediaqueries';

export const BoxMessage = styled.div`
  background-color: ${props => props.theme.colors.grayLightest};
  height: 100%;
  padding: ${rem('117px')} 6%;
  position: fixed;
  text-align: center;
  width: 100%;
  ${mediaqueries.greaterThan('desktop')`
    height: auto;
    padding: ${rem('68px')} 6%;
    position: relative;
  `};
`;

export const StyledIconClose = styled(Icon)`
  right: ${rem('18px')};
  height: ${rem('19px')};
  position: absolute;
  top: ${rem('32px')};
  width: ${rem('19px')};
  ${mediaqueries.greaterThan('desktop')`
    display: none;
  `};
`;

export const StyledIconPal = styled(Icon)`
  height: ${rem('54px')};
  margin-bottom: ${rem('20px')};
  width: ${rem('54px')};
  ${mediaqueries.greaterThan('desktop')`
    margin-bottom: ${rem('26px')};
  `};
`;

export const ParagraphTitle = styled.p`
  font-family: ${props => props.theme.fonts.medium};
  font-size: ${rem('15px')};
  line-height: ${rem('18px')};
  margin-bottom: ${rem('9px')};
  text-transform: uppercase;
  ${mediaqueries.greaterThan('desktop')`
    margin-bottom: ${rem('4px')};
  `};
`;

export const ParagraphSubtitle = styled.p`
  font-family: ${props => props.theme.fonts.medium};
  font-size: ${rem('15px')};
  line-height: ${rem('16px')};
  margin: 0 0 ${rem('50px')} 0;
`;

export const ParagraphLink = styled.a`
  font-family: ${props => props.theme.fonts.medium};
  font-size: ${rem('14px')};
  display: none;
  margin: 0;
  ${mediaqueries.greaterThan('desktop')`
    display: block;
  `};
`;

export const StyledIconLink = styled(Icon)`
  height: ${rem('8px')};
  margin-right: ${rem('6px')};
  width: ${rem('4px')};
`;
