import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';

import { API } from '../../../constants/api';
import { StyledImgAvatar, StyledFirstChar, StyledImg } from './style';
import { initialsString } from '../../../utils/initialsString';
import { SQUARED_SMALL } from '../../../utils/crops';
import { accentsTidy } from '../../../utils/accents';

const AvatarPerson = ({
  className,
  name,
  lastname,
  userId,
  crop,
  avatarPhoto
}) => {
  let firstLetter;
  let secondLetter;
  const [timestamp, setTimestamp] = useState(undefined);

  if (name) {
    firstLetter = accentsTidy(initialsString(name, 1));
  }
  if (lastname) {
    secondLetter = accentsTidy(initialsString(lastname, 1));
  }

  useEffect(() => {
    setTimestamp(new Date().getTime());
  }, []);

  return (
    <StyledImgAvatar className={className} defaultCrop={SQUARED_SMALL}>
      {!avatarPhoto ? (
        <StyledFirstChar className="avatar-char">{`${firstLetter}${secondLetter}`}</StyledFirstChar>
      ) : (
        <StyledImg
          src={
            timestamp
              ? `${API}images/avatar/${userId}/${crop.width}x${crop.height}.jpg?update=${timestamp}`
              : `${API}images/avatar/${userId}/${crop.width}x${crop.height}.jpg`
          }
          alt="Foto del usuario"
        />
      )}
    </StyledImgAvatar>
  );
};

AvatarPerson.propTypes = {
  className: PropTypes.string,
  name: PropTypes.string,
  lastname: PropTypes.string,
  crop: PropTypes.shape({
    width: PropTypes.number,
    height: PropTypes.number
  }),
  userId: PropTypes.string,
  avatarPhoto: PropTypes.bool
};

AvatarPerson.defaultProps = {
  name: ''
};

export default AvatarPerson;
