import styled from 'styled-components';
import { rem } from 'polished';
import { getCrop } from '../../../utils/crops';

const StyledImgAvatar = styled.span`
  background-color: ${props => props.theme.colors.grayLightest};
  background-size: cover;
  border-radius: 50%;
  color: ${props => props.theme.colors.grayDark};
  display: block;
  font-size: 400%;
  font-family: ${props => props.theme.fonts.medium};
  height: ${rem('45px')};
  left: 0;
  overflow: hidden;
  width: ${rem('45px')};
  border: ${rem('3px')} ${props => props.theme.colors.dark} solid;

  @supports (clip-path: circle()) or (-webkit-clip-path: circle()) {
    clip-path: circle();
    overflow: initial;
  }
`;

const StyledFirstChar = styled.span`
  align-items: center;
  background-color: inherit;
  color: inherit;
  display: flex;
  font-size: inherit;
  font-family: inherit;
  height: 100%;
  justify-content: center;
  border-radius: 50%;
  text-transform: uppercase;
`;

const StyledImg = styled.img`
  max-width: 100%;
  border-radius: 50%;
`;

export { StyledImgAvatar, StyledFirstChar, StyledImg };
