import axios from 'axios';
import request from './api';
import {
  API_SOCIAL,
  API_PRESS_RESUME,
  API_NEWS_REVIEWS,
  API_EMPLOYEE_LIST
} from '../constants/api';

/** EMPLOYEE LIST */
/**
 * Retrieves the data of the employees.
 * @param {string} orderby NAME
 * @param {string} name
 * @param {string} lastname
 * @param {string} idDepartment
 * @param {string} extension
 * @param {string} phones
 * @param {string} size
 * @param {string} page
 * @param {string} direction  ASC|DESC
 */
// orderby, name,lastname,idDepartment,extension,phones,size,page,direction
export const getEmployeeList = ({
  orderby = 'NAME',
  name,
  lastname,
  idDepartment,
  extension,
  phones,
  size = 10,
  page = 0,
  direction = 'ASC'
}) =>
  request(API_EMPLOYEE_LIST, {
    method: 'GET',
    params: {
      orderby,
      name,
      lastname,
      idDepartment,
      extension,
      phones,
      size,
      page,
      direction
    }
  });

/** TRAFFIC */
/**
 * Retrieves the data of the traffic. If lat and long are undefined, the office location will be used
 * @param {Number} lat Latitude
 * @param {Number} long Longitude
 */
export const getTraffic = (lat, long) =>
  request(`${API_SOCIAL}widget/googlemaps`, {
    method: 'GET',
    params: {
      latitude: lat,
      longitude: long
    }
  });

/**
 * Set the config for the user.
 * @param {String} method 'POST' if the user has id or 'PUT' if not
 * @param {String} userAddress New address of this user
 * @param {Number} id User id in the database for using the Google services
 */
export const setTrafficConfig = (method, userAddress, id = null) =>
  request(`${API_SOCIAL}widget/googlemaps/config`, {
    method,
    data: {
      active: true,
      userAddress,
      id
    }
  });

export const getPressResume = fromDate =>
  request(API_PRESS_RESUME, {
    method: 'GET',
    params: {
      date: fromDate
    }
  });

export const getNewsReview = () =>
  request(API_NEWS_REVIEWS, {
    method: 'GET',
    params: {
      size: 5
    }
  });

/**
 * Get info of the traffic widget for the current user
 */
export const getTrafficConfig = () =>
  request(`${API_SOCIAL}widget/googlemaps/config`, {
    method: 'GET'
  });

/** WEATHER */
export const getWeather = (lat = '40.5682479', lon = '-3.6204052') =>
  axios('https://api.openweathermap.org/data/2.5/weather', {
    method: 'GET',
    params: {
      id: '3110040',
      appid: 'c1d69b3094886faf8501125e56e17c29',
      lang: 'es',
      units: 'metric',
      lat,
      lon
    }
  });
