import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledBox,
  StyledList,
  StyledElements,
  StyledTitle,
  StyledInfo
} from './style';

const Givens = ({ className, dataDates }) => (
  <StyledBox className={className}>
    {dataDates.map(item => (
      <StyledList>
        {item.datamap.map(aux => (
          <StyledElements>
            <StyledTitle>{aux.title}</StyledTitle>
            <StyledInfo>{aux.info}</StyledInfo>
          </StyledElements>
        ))}
      </StyledList>
    ))}
  </StyledBox>
);

Givens.propTypes = {
  className: PropTypes.string,
  dataDates: PropTypes.arrayOf(
    PropTypes.shape({
      datamap: PropTypes.arrayOf(
        PropTypes.shape({
          title: PropTypes.string,
          info: PropTypes.string
        })
      )
    })
  )
};

export default Givens;
