import React from 'react';
import styled from 'styled-components';
import withReadme from 'storybook-readme/with-readme';
import storiesOf from '../../../../.storybook/util';
import Readme from './README.md';
import Image from '.';
import { LANDSCAPE_MEDIUM } from '../../../utils/crops';

/* #### STORY STYLES #### */
const StyledImage = styled(Image)`
  border-radius: 50%;
`;

/* ###################### */

storiesOf('UI Kit/Image', module)
  .addDecorator(withReadme(Readme))
  .add('Image Crop', () => (
    <div>
      <h2>- Image con Crops</h2>
      <Image
        path="/crops/liberty"
        defaultCrop={LANDSCAPE_MEDIUM}
        placeholder={({ error }) => (
          <div style={{ color: 'red', fontFamily: 'Gotham Book Italic' }}>
            {error ? 'Error on load' : 'Loading'}
          </div>
        )}
      />
      <h2>- Image con error</h2>
      <Image
        path="/fake_placeholder"
        defaultCrop={{ height: 166, width: 295 }}
        placeholder={({ error }) => (
          <div style={{ color: 'red', fontFamily: 'Gotham Book Italic' }}>
            {error ? 'Error on load' : 'Loading'}
          </div>
        )}
      />
      <h2>- Image con media queries</h2>
      <Image
        path="/crops/rabbit"
        defaultCrop={{ height: 166, width: 295 }}
        crops={[
          { media: '(min-width: 800px)', crop: { height: 166, width: 295 } },
          { media: '(min-width: 500px)', crop: { height: 166, width: 295 } }
        ]}
        placeholder={({ error }) => (
          <div style={{ color: 'red', fontFamily: 'Gotham Book Italic' }}>
            {error ? 'Error on load' : 'Loading'}
          </div>
        )}
      />
    </div>
  ))
  .add('Extended image (with custom styles)', () => (
    <div style={{ display: 'flex' }}>
      <StyledImage
        path="/crops/placeholder"
        defaultCrop={{ height: 166, width: 295 }}
        crops={[
          { media: '(min-width: 800px)', crop: { height: 166, width: 295 } },
          { media: '(min-width: 500px)', crop: { height: 166, width: 295 } }
        ]}
        placeholder={({ error }) => (
          <div style={{ color: 'red', fontFamily: 'Gotham Book Italic' }}>
            {error ? 'Error on load' : 'Loading'}
          </div>
        )}
      />
      <StyledImage
        path="/crops/placeholder"
        defaultCrop={{ height: 166, width: 295 }}
        crops={[
          { media: '(min-width: 800px)', crop: { height: 166, width: 295 } },
          { media: '(min-width: 500px)', crop: { height: 166, width: 295 } }
        ]}
        placeholder={({ error }) => (
          <div style={{ color: 'red', fontFamily: 'Gotham Book Italic' }}>
            {error ? 'Error on load' : 'Loading'}
          </div>
        )}
      />
    </div>
  ));
