import request from './api';
import {
  API_INTRANET,
  API_NOTIFICATIONS,
  API_SHIPMENT,
  API_CMS
} from '../constants/api';
import { normalizeHeader } from './normalizeResponse';

export const getHeader = () =>
  request(`${API_INTRANET}menu/home?associationId=405`).then(response =>
    normalizeHeader(response)
  );

/** NOTIFICATIONS */

export const getNotificationNewsByApp = (typeApp = '') =>
  request(`${API_NOTIFICATIONS}notificationNewsByApp`, {
    method: 'GET',
    params: {
      typeApp
    }
  });

export const getNotificationNoViewed = (typeApp, page = 0, size = 10) =>
  request(`${API_NOTIFICATIONS}noViewedByApp`, {
    method: 'GET',
    params: {
      typeApp,
      page,
      size
    }
  });

export const checkViewedByContent = contentId =>
  request(`${API_NOTIFICATIONS}checkViewedByContentId`, {
    method: 'PUT',
    params: {
      contentId
    }
  });

/** PARCEL NOTIFICATIONS */

export const setParcelAction = (actionUser, stateContent, contentId) =>
  request(`${API_SHIPMENT}request/status`, {
    method: 'PUT',
    params: {
      actionUser,
      stateContent,
      contentId
    }
  });

/** CMS NOTIFICATIONS */

export const setCMSAction = (contentType, action, contentId) =>
  request(`${API_CMS}${contentType}/cmscontent/${action}/${contentId}`, {
    method: 'PUT'
  });
