import styled from 'styled-components';
import { rem } from 'polished';
import BigCalendar from 'react-big-calendar';
import estilosBase from 'react-big-calendar/lib/css/react-big-calendar.css';
import mediaqueries from '../../../config/themes/variables/mediaqueries';

const StyledWrapper = styled.div`
  width: 100%;
  height: ${rem('332px')};
  ${mediaqueries.greaterThan('mobile')`
    height: ${rem('520px')};
  `};
`;

const StyledRecurs = styled.div`
  height: 100vh;
  position: relative;
  ${mediaqueries.greaterThan('desktop')`
    width: 360px;
    margin: 0 auto;
  `};
`;

const StyleBigCalendarFood = styled(BigCalendar)`
  ${estilosBase};

  font-family: ${props => props.theme.fonts.medium};
  font-size: ${rem('12px')};

  .rbc-toolbar-label {
    font-family: ${props => props.theme.fonts.medium};
    font-size: ${rem('20px')};
    text-transform: uppercase;
  }
  .rbc-month-header {
    background-color: ${props => props.theme.colors.grayLighter};
  }

  .rbc-header {
    border: none;
    font-size: inherit;
    font-weight: normal;
    padding: 0 0 0 ${rem('10px')};
    text-align: left;
    text-transform: uppercase;

    span {
      font-family: ${props => props.theme.fonts.medium};
    }

    &:nth-child(6) > span,
    &:nth-child(7) > span {
      color: #a3a3a3;
    }
  }

  .rbc-header + .rbc-header {
    border: none;
  }

  .rbc-row-bg {
    background-color: ${props => props.theme.colors.grayLightest};
  }

  .rbc-month-row + .rbc-month-row,
  .rbc-day-bg + .rbc-day-bg {
    border-color: ${props => props.theme.colors.light};
  }

  .rbc-month-row {
    max-height: ${rem('52px')};
    ${mediaqueries.greaterThan('mobile')`
      max-height: inherit;
    `};
  }

  .rbc-date-cell {
    position: relative;
    font-size: ${rem('14px')};
    padding: ${rem('5px')} 0 0 0;
    text-align: center;
    height: 31px;
    ${mediaqueries.greaterThan('mobile')`
      font-size: ${rem('12px')};
      padding: ${rem('10px')} 0 0 ${rem('10px')};
      text-align: left;
      height: auto;
    `};

    &:nth-child(6) > a,
    &:nth-child(7) > a {
      color: #a3a3a3;
    }
  }

  .rbc-date-cell > a {
    cursor: default;
    font-family: ${props => props.theme.fonts.medium};
    width: 100%;
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    padding-top: 7px;
    height: 51px;
    z-index: 5;
    ${mediaqueries.greaterThan('mobile')`
      position: static;
      padding-top: 0;
      height: auto;
    `};
  }

  .rbc-today {
    background-color: ${props => props.theme.colors.grayLighter};
  }

  .rbc-event {
    background-color: transparent;
    padding: 0 ${rem('10px')};
    cursor: default;
  }

  .rbc-event.rbc-selected {
    background-color: transparent;
    border-radius: 0;
    p {
      color: ${props => props.theme.colors.grayDarkest};
      :before {
        background-color: ${props => props.theme.colors.activeBlue};
        border: ${rem('2px')} solid ${props => props.theme.colors.success};
      }
    }
    ${mediaqueries.greaterThan('mobile')`
      background-color: transparent;
    `};
  }

  .rbc-date-cell.rbc-now {
    font-weight: normal;
  }

  .rbc-show-more {
    background-color: transparent;
    color: ${props => props.theme.colors.grayDarkest};
    font-family: ${props => props.theme.fonts.medium};
    font-size: ${rem('12px')};
    font-weight: normal;
    padding: ${rem('5px')} ${rem('10px')};
    display: none;
    ${mediaqueries.greaterThan('mobile')`
      display: block;
    `};
  }

  .rbc-selected-cell {
    background-color: ${props => props.theme.colors.grayLighter};
  }
  .rbc-off-range-bg {
    background-color: transparent;
  }
  .rbc-off-range {
    color: ${props => props.theme.colors.gray};
  }
  .rbc-current {
    background-color: ${props => props.theme.colors.grayLight};
    color: ${props => props.theme.colors.grayDarkest};
    font-family: ${props => props.theme.fonts.medium};
    position: relative;
    :after {
      background-color: ${props => props.theme.colors.grayLight};
      content: '';
      height: 370%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
      z-index: -1;
    }
    ${mediaqueries.greaterThan('mobile')`
      background-color: transparent;
      :after {
        background-color: transparent;
      }  
    `};
  }
  .rbc-now {
    background-color: ${props => props.theme.colors.grayLighter};

    :after {
      background-color: ${props => props.theme.colors.grayLighter};
    }
  }

  .rbc-event-content {
    text-align: center;
    ${mediaqueries.greaterThan('mobile')`
      text-align: left;
    `};
  }
`;

const StyleEventTitle = styled.p`
  color: ${props => props.theme.colors.dark};
  display: inline;
  font-size: ${rem('12px')};
  font-weight: normal;
  margin: 0;
  max-width: 100%;
  overflow: hidden;
  padding: 10px 0 0 10px;
  position: relative;
  text-overflow: ellipsis;
  white-space: nowrap;

  :before {
    background-color: ${props => props.theme.colors.activeBlue};
    border: ${rem('2px')} solid ${props => props.theme.colors.success};
    border-radius: 50%;
    content: '';
    height: 6px;
    left: 0;
    position: absolute;
    top: 10px;
    width: 6px;
  }

  ${mediaqueries.greaterThan('mobile')`
    display: block;
    padding: 0 0 0 10px;
  `};
`;
const StyleEventText = styled.span`
  display: none;

  ${mediaqueries.greaterThan('mobile')`
    display: inline-block;
  `};
`;

export {
  StyleBigCalendarFood,
  StyleEventTitle,
  StyleEventText,
  StyledWrapper,
  StyledRecurs
};
