function Event({ event }) {
  return (
    <StyleEventTitle>
      <StyleEventText>{event.title}</StyleEventText>
    </StyleEventTitle>
  );
}

function CustomDayEvent({ event }) {
  return (
    <span>
      <em style={{ color: 'magenta' }}>{event.title}</em>
      <p>{event.desc && `:  ${event.desc}`}</p>
    </span>
  );
}

const customDayCalendar = date => {
  if (date.getDate() === 7 || date.getDate() === 15) {
    return {
      className: 'special-day',
      style: {
        background: `${date.getDate() === 7 ? '#d0d0d0' : '#d0d0d0'}`
      }
    };
  }
  return {};
};

// BigCalendar tiene una props components para personalizar las views
const CustomToolbar = () => <div>CuestomToolbar</div>;
const components = {
  toolbar: CustomToolbar,
  event: Event,
  day: { event: CustomDayEvent }
};

storiesOf('core/Calendar', module).add('Default', () => (
  <StyledWrapper style={{ height: '600px' }}>
    <StyleBigCalendar
      events={events}
      popup
      selectable
      views={{
        month: true,
        day: true
      }}
      dayPropGetter={customDayCalendar}
      // eventPropGetter= // personalizar seleccionado
      components={components}
      defaultDate={new Date(new Date().setHours(new Date().getHours() - 3))}
      // eslint-disable-next-line
      onSelectEvent={event => alert(JSON.stringify(event))}
    />
  </StyledWrapper>
));
