import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledEventComplete,
  StyledEventHeader,
  StyleEventButtons,
  StyleEventDay,
  StyleEventDayWeek,
  StyleEventDayNum,
  StyleEventDayMonth
} from './style';

const EventAllDay = props => (
  <StyledEventComplete>
    <StyledEventHeader>
      <StyleEventDay>
        <StyleEventDayWeek> {props.DayWeek} </StyleEventDayWeek>
        <StyleEventDayNum> {props.DayNumber}</StyleEventDayNum>
        <StyleEventDayMonth> {props.Month} </StyleEventDayMonth>
      </StyleEventDay>
      <StyleEventButtons secondary>HOY</StyleEventButtons>
    </StyledEventHeader>
    <p> evento </p>
  </StyledEventComplete>
);

EventAllDay.propTypes = {
  DayWeek: PropTypes.string,
  DayNumber: PropTypes.string,
  Month: PropTypes.string
};

EventAllDay.defaultProps = {};

export default EventAllDay;
