import React from 'react';
import storiesOf from '../../../../.storybook/util';
import EditionPersonalAddress from '.';

import { language, street, area, country } from '../../../utils/mocks/js';

storiesOf('core/EditionPersonalAddress', module).add('Default', () => (
  <EditionPersonalAddress
    title="Solicitud de cambio de domicilio personal"
    data={language}
    listStreet={street}
    selectedStreet="Calle"
    listArea={area}
    selectedArea="Madrid"
    listCountry={country}
    selectedCountry="España"
  />
));
