import styled, { css } from 'styled-components';
import { rem } from 'polished';
import mediaqueries from '../../../config/themes/variables/mediaqueries';
import Icon from '../../core/Icon';

// Box general Menu
export const StyledBoxNav = styled.div`
  background-color: ${props => props.theme.colors.dark};
  color: ${props => props.theme.colors.light};
  display: block;
  height: 100%;
  left: 0;
  position: fixed;
  transform: translate(-100%, 0);
  transition-delay: initial;
  transition-duration: 0.4s;
  transition-property: initial;
  transition-timing-function: ease-in-out;
  top: 0;
  width: ${rem('310px')};
  z-index: 30;
  /* velo translúcido */
  &:after {
    background-color: ${props => props.theme.colors.dark};
    content: '';
    display: none;
    height: 100%;
    opacity: 0.7;
    position: fixed;
    top: 0;
    width: 600%;
    z-index: -1;
  }
  ${mediaqueries.lessThan('mobile')`
    width: ${rem('263px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    height: auto;
    top: ${rem('15px')};
    transform: translate(0, -180%);
    opacity:0;
    padding-bottom:${rem('46px')};
    padding-top:${rem('46px')};
    position: absolute;
    transition-duration: 0.27s;
    width: ${rem('977px')};
    z-index: 1;
    /* Parte negra en desktop para X */
    &:before {
      background-color: ${props => props.theme.colors.dark};
      content: '';
      height: calc(100% + ${rem('73px')});
      position: fixed;
      left:${rem('-11px')};
      top:${rem('-73px')};
      width:${rem('76px')};
      z-index: -1;
    }
  `};
`;

/* capa para menu */
export const StyledMenu = styled.div`
  left: ${rem('13px')};
  position: absolute;
  top: ${rem('56px')};
  ${mediaqueries.greaterThan('desktop')`
    left: ${rem('-9px')};
  `};
  ${props =>
    props.show &&
    css`
      ${StyledBoxNav}{
        opacity:1;
        transform: translate(0);
        ${mediaqueries.lessThan('1280px')`
          &:after {
            display:block;
          }
        `};
        ${mediaqueries.greaterThan('desktop')`
          &:after {
            display:none;
          }
        `};
    `};
  }
  }
`;

/* listado menu */
export const StyledBoxList = styled.ul`
  display: none;
  margin: 0;
  padding: 0;
  ${mediaqueries.lessThan('mobile')`
      margin-top:${rem('23px')};
  `};
  ${mediaqueries.greaterThan('tablet')`
      margin-bottom:${rem('6px')};
      margin-top:${rem('27px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    display:block;
    margin-right:${rem('18px')};
    margin-top:0;
  `};
`;

/* capa para distintas columnas del listado */
export const StyledListNav = styled.div`
  background-color: ${props => props.theme.colors.dark};
  ${mediaqueries.greaterThan('desktop')`
    display:inline-block;
    vertical-align: top;
    width:${rem('281px')};
  `};
`;

/* icono desplegar opciones del menu */
export const StyledIconDesplegated = styled(Icon)`
  margin-top: ${rem('4px')};
  transform: rotate(90deg);
  ${mediaqueries.lessThan('mobile')`
    margin-top: ${rem('6px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    display:none;
  `};
`;

export const StyledListTitle = styled.button`
  background-color: ${props => props.theme.colors.grayDarkest};
  border: 0;
  color: ${props => props.theme.colors.light};
  display: flex;
  font-family: ${props => props.theme.fonts.medium};
  height: ${rem('50px')};
  justify-content: space-between;
  margin-bottom: ${rem('4px')};
  padding-left: ${rem('50px')};
  padding-right: ${rem('24px')};
  transition: 0.4s;
  text-align: left;
  width: 100%;
  &:focus {
    outline: none;
  }
  ${mediaqueries.lessThan('1279px')`
    ${props =>
      props.open &&
      `> svg {
          fill:${props.theme.colors.primary};
          transform: rotate(-90deg);
        }
        +ul { display:block};
    `};
  `};

  ${mediaqueries.lessThan('mobile')`
    height:${rem('44px')};
    margin-bottom:${rem('2px')};
    padding-left:${rem('40px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    background-color: transparent;
    border-bottom:${rem('1px')} solid ${props => props.theme.colors.primary};
    cursor: default;
    margin-bottom: ${rem('13px')};
    outline:none;
    padding-bottom: 0;
    padding-left:0;
    padding-right:${rem('5px')};
    width:auto;
  `};
`;

/* elementos del listado del menu */
export const StyledList = styled.li`
  font-family: ${props => props.theme.fonts.medium};
  font-size: ${props => props.theme.fontSize.small};
  list-style: none;
  margin: 0;
  padding-bottom: ${rem('25px')};
  padding-left: ${rem('60px')};

  &:before {
    content: none;
  }

  ${mediaqueries.lessThan('mobile')`
    padding-bottom: ${rem('18px')};
    padding-left:${rem('50px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    padding-bottom: ${rem('7px')};
    padding-left:0;
  `};
`;

/* enlaces menu */
export const StyledListLink = styled.a`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: ${props => props.theme.fontSize.small};
`;

/* capa para logo navaja e icono cerrar en dispositivos */
export const StyledBoxLogo = styled.div`
  background-color: ${props => props.theme.colors.light};
  display: flex;
  height: ${rem('166px')};
  padding-left: ${rem('50px')};
  padding-top: ${rem('25px')};
  ${mediaqueries.lessThan('mobile')`
    height: ${rem('151px')};
    padding-left:${rem('40px')};
    padding-top:${rem('50px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    display:none;
  `};
`;

/* icono de cerrar menu */
export const StyledIconClose = styled(Icon)`
  height: ${rem('19px')};
  position: absolute;
  right: ${rem('13px')};
  top: ${rem('13px')};
  width: ${rem('19px')};
  ${mediaqueries.greaterThan('desktop')`
    fill:${props => props.theme.colors.light};
    height: ${rem('18px')};
    left: ${rem('18px')};
    right:inherit;
    top:${rem('-45px')};
    width: ${rem('18px')};
  `};
`;

/* capa para menu con scroll en dispositivos */
export const StyledBoxScroll = styled.div`
  height: calc(100% - ${rem('166px')});
  overflow-y: auto;
  ${mediaqueries.lessThan('mobile')`
    height: calc(100% - ${rem('151px')});
  `};
  ${mediaqueries.greaterThan('desktop')`
    padding-left:${rem('66px')};
    padding-right:${rem('66px')};
  `};
`;

/* logo navaja en lateral desplegable */
export const StyledLogoLateral = styled(Icon)`
  width: ${rem('200px')};
  ${mediaqueries.lessThan('mobile')`
    width: ${rem('168px')};
    height: ${rem('70px')};
  `};
`;
