import React from 'react';
import { Link } from 'react-router-dom';

import {
  StyledHeader,
  StyledWidgetBox,
  StyledTitle,
  StyledList
} from './style';
import Widget from '../../containers/Widget';
import {
  Audiencias,
  MenuDia,
  AppsWidget,
  Note,
  Resume,
  Apps
} from '../../../../assets/images/index';

import { WIDGET_UP, WIDGET_DOWN } from '../../../constants/actionTypes';

const options = [
  { name: 'Subir', action: WIDGET_UP },
  { name: 'Bajar', action: WIDGET_DOWN }
];

const colors = {
  shortcut: 'colorOne',
  menuofday: 'colorThree',
  audiences: 'colorTwo',
  pressNote: 'colorOne'
};

const icons = {
  shortcut: AppsWidget,
  shortcutWidget: Apps,
  menuofday: MenuDia,
  audiences: Audiencias,
  ranking: Audiencias,
  resumen: Resume,
  pressrelease: Note
};

const WidgetBox = ({
  index,
  name,
  type,
  link,
  onClickOption,
  id,
  className
}) => (
  <StyledWidgetBox className={className}>
    <StyledHeader>
      {link ? (
        <Link to={link}>
          <StyledTitle title={name} type={type} icon={icons[type]} />
        </Link>
      ) : (
        <StyledTitle
          title={name}
          type={type}
          icon={icons[type]}
          className="WidgetBox-title"
        />
      )}
      <StyledList
        type={colors[type]}
        onClickOption={action => onClickOption(index, action)}
        options={options}
        id={id}
      />
    </StyledHeader>
    {type && <Widget type={type} />}
  </StyledWidgetBox>
);

export default WidgetBox;
