import React from 'react';
import storiesOf from '../../../../.storybook/util';
import Experience from '.';
import { experience } from '../../../utils/mocks/js';

storiesOf('core/Experience', module).add('Item', () => (
  <div>
    <h3>No editable</h3>
    <Experience dataExperience={experience} />
    <br /> <br />
    <h3>Editable</h3>
    <Experience dataExperience={experience} editable />
  </div>
));
