import {
  SET_USER,
  SET_USER_DETAILS,
  SET_USER_PROFILE,
  SET_USER_TEAM,
  SET_EDITED_PROFILE,
  SET_USER_AVATAR
} from '../constants/actionTypes';

import { getUser, setUserEditingData } from '../utils/apiUser';

/**
 * Get user data and user details data from backend services using a promise
 * and the results retrieved will be dispatched for the reducer
 * @param {function} dispatch - Dispatcher for the reducer
 */

export const loadUser = (dispatch, history) => {
  getUser()
    .then(user => {
      dispatch({ type: SET_USER, user });
    })
    .catch(error => {
      const { code } = error;

      if (code === 401) {
        if (window && window.process && window.process.type) {
          history.push('/login_widget');
        } else {
          history.push('/login');
        }
      }
    });
};

export const setUserDetail = userDetail => ({
  type: SET_USER_DETAILS,
  userDetail
});

export const setUserProfile = userProfile => ({
  type: SET_USER_PROFILE,
  userProfile
});

export const setUserTeam = userTeam => ({
  type: SET_USER_TEAM,
  userTeam
});

export const sendEditedUserData = data => {
  setUserEditingData(data);
};

export const setEditedProfile = state => ({
  type: SET_EDITED_PROFILE,
  editedProfile: state
});

export const setAvatar2Id = avatarId => ({
  type: SET_USER_AVATAR,
  avatarId
});
