import React, { Component } from 'react';
import {
  StyledContainer,
  StyledTabsContainer,
  StyledTabsHorizontal,
  StyledTabHorizontal,
  StyledWrapper
} from './style';
import BankAccountSection from './bankAccount';
import IRPFDataSection from './IRPFData';
import AddressSection from './addressSection';

import { fiscalDataMenu } from '../../../../constants/menuTabs';

class FiscalData extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentSection: 'DomicilioPersonal'
    };
  }

  render() {
    const { currentSection } = this.state;

    const getContent = section => {
      switch (section) {
        case 'DomicilioPersonal':
        default:
          return <AddressSection />;
        case 'DatosDomBancaria':
          return <BankAccountSection />;
        case 'DatosIRPF':
          return <IRPFDataSection />;
      }
    };

    const changeSection = section => {
      this.setState({ currentSection: section.id });
    };
    return (
      <StyledContainer>
        <StyledWrapper>
          <StyledTabsContainer>
            <StyledTabsHorizontal>
              {fiscalDataMenu.map(tab => (
                <StyledTabHorizontal
                  key={tab.id}
                  active={tab.id === currentSection}
                  clicked={() => changeSection(tab)}
                >
                  {tab.name}
                </StyledTabHorizontal>
              ))}
            </StyledTabsHorizontal>
          </StyledTabsContainer>
          <div>{getContent(currentSection)}</div>
        </StyledWrapper>
      </StyledContainer>
    );
  }
}

export default FiscalData;
