import React from 'react';
import { withKnobs, text, boolean } from '@storybook/addon-knobs/react';
import storiesOf from '../../../../.storybook/util';
import MenuTabs from '.';
import { menuTabs } from '../../../utils/mocks/js';

storiesOf('core/MenuTabs', module)
  .addDecorator(withKnobs)
  .add('MenuTabs', () => <MenuTabs dataMenu={menuTabs} />)
  .add('MenuTabs animado', () => {
    const posX = text('posX', '0');
    return (
      <MenuTabs
        dataMenu={menuTabs}
        posX={posX}
        itemSelected="Retenciones"
        selected={boolean('Selected', true)}
        clickHandler={() => {}}
      />
    );
  });
