import styled from 'styled-components';
import { rem } from 'polished';
import mediaqueries from '../../../config/themes/variables/mediaqueries';
import DropdownInput from '../DropdownInput';
import Input from '../Input';

export const StyledEdition = styled.div`
  padding: ${rem('25px')};
  ${mediaqueries.greaterThan('mobile')`
    padding: 0 10%;
  `};
  ${mediaqueries.greaterThan('desktop')`
    background-color: ${props => props.theme.colors.grayLightest};
    padding: ${rem('25px')} 10%  ${rem('40px')}  ${rem('25px')};
  `};
`;

export const StyledBoxColum = styled.div`
  display: grid;
  grid-gap: 0 ${rem('28px')};
  grid-template-columns: 50% 50%;
  width: calc(100% - ${rem('28px')});
  ${mediaqueries.greaterThan('mobile')`
    grid-gap: 0 ${rem('35px')};
    grid-template-columns: 16.66% 16.66% 16.66% 16.66% 16.66% 16.66%;
    width: calc(100% - ${rem('175px')});
  `};
  ${mediaqueries.greaterThan('desktop')`
    grid-gap: 0 12%;
    grid-template-columns: 50% 50%;
    width: 88%;
  `};
`;

export const StyledBoxBase = styled.div`
  margin-bottom: ${rem('12px')};
  ${mediaqueries.greaterThan('mobile')`
    margin-bottom: ${rem('15px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    align-items: center;
    display: flex;
    margin-bottom: ${rem('9px')};
  `};
`;

export const StyledBoxOne = StyledBoxBase.extend`
  grid-column: 1 /3;
  grid-row: 1;
  ${mediaqueries.greaterThan('mobile')`
    grid-column: 1 /7;
  `};
  ${mediaqueries.greaterThan('desktop')`
    grid-column: 1 /2;
  `};
`;

export const StyledBoxTwo = StyledBoxBase.extend`
  grid-column: 1 /3;
  grid-row: 2;
  ${mediaqueries.greaterThan('mobile')`
    grid-column: 1 /7;
  `};
  ${mediaqueries.greaterThan('desktop')`
    grid-column: 1 /2;
  `};
`;

export const StyledBoxThree = StyledBoxBase.extend`
  ${mediaqueries.greaterThan('mobile')`
    grid-column: 1 /3;
  `};
  ${mediaqueries.greaterThan('desktop')`
    grid-column: 1 /2;
    grid-row: 3;
  `};
`;

export const StyledBoxFour = StyledBoxBase.extend`
  ${mediaqueries.greaterThan('mobile')`
    grid-column: 3 /5;
  `};
  ${mediaqueries.greaterThan('desktop')`
    grid-column: 1 /2;
    grid-row: 4;
  `};
`;

