export const StyledTitle = styled.h2`
  display: none;
  font-size: ${props => props.theme.fontSize.small};
  margin-bottom: ${rem('27px')};
  text-transform: uppercase;
  ${mediaqueries.greaterThan('desktop')`
    display: block;
  `};
`;

export const StyledLabel = styled.label`
  font-family: ${props => props.theme.fonts.medium};
  font-size: ${props => props.theme.fontSize.small};
  ${mediaqueries.greaterThan('desktop')`
    flex-basis: ${rem('114px')};
    flex-shrink: 0;
  `};
`;

export const StyleInputShort = styled(Input)`
  ${mediaqueries.greaterThan('desktop')`
    width: ${rem('114px')};
  `};
`;

export const StyledDrop = styled(DropdownInput)`
  width: 100%;
`;

export const StyledBoxDown = styled.div`
  margin: ${rem('19px')} 0 0 0;
  text-align: right;
  ${mediaqueries.greaterThan('desktop')`
    margin: ${rem('7px')} ${rem('18px')} 0 0;
  `};
`;
