import React from 'react';
import PropTypes from 'prop-types';

import Input from '../../../core/Input';

import { Trash, ArrowLeft } from '../../../../../assets/images/index';

import {
  StyledLabel,
  LabelBlock,
  StyledBoxInputEducation,
  StyledBoxModuleAlignTop,
  StyledOrderItem,
  StyledIcon
} from './style';

import {
  WrapperCollapsed,
  StyledIconDrop,
  StyledDropdownInputMonths,
  StyledDropdownInputYears,
  RangeDates,
  ItemRangeDates
} from './styleEducation';

import { EDUCATION } from '../../../../constants/literals/modals';

const EducationItem = ({
  expanded,
  labelStartMonth,
  labelEndMonth,
  labelStartYear,
  labelEndYear,
  listMonths,
  listYears,
  onChangeDegreeInput,
  onChangeSchoolInput,
  onClickExpand,
  onClickDelete,
  onClickDown,
  onClickUp,
  onSelectStartMonth,
  onSelectStartYear,
  onSelectEndMonth,
  onSelectEndYear,
  order,
  selectedStartMonth,
  selectedEndMonth,
  selectedStartYear,
  selectedEndYear,
  degree,
  school,
  errorTitle,
  errorCenter
}) => (
  <StyledBoxModuleAlignTop>
    <StyledOrderItem
      order={order}
      onClickDown={onClickDown}
      onClickUp={onClickUp}
    />
    <StyledBoxInputEducation>
      <StyledIconDrop
        svg={ArrowLeft}
        size="s"
        hover
        onClick={onClickExpand}
        expanded={expanded}
      />
      <StyledLabel>{EDUCATION.TITLE_LABEL}</StyledLabel>
      <Input value={degree} onChange={onChangeDegreeInput} error={errorTitle} />
      <WrapperCollapsed expanded={expanded}>
        <StyledLabel>{EDUCATION.CENTER_LABEL}</StyledLabel>
        <Input
          value={school}
          onChange={onChangeSchoolInput}
          error={errorCenter}
        />
        <RangeDates>
          <ItemRangeDates>
            <LabelBlock>{EDUCATION.DATE_FROM_LABEL}</LabelBlock>
            <StyledDropdownInputMonths
              list={listMonths}
              selected={selectedStartMonth}
              label={labelStartMonth}
              onSelect={onSelectStartMonth}
            />
            <StyledDropdownInputYears
              list={listYears}
              label={labelStartYear}
              selected={selectedStartYear}
              onSelect={onSelectStartYear}
            />
          </ItemRangeDates>
          <ItemRangeDates>
            <LabelBlock>{EDUCATION.DATE_UNTIL_LABEL}</LabelBlock>
            <StyledDropdownInputMonths
              list={listMonths}
              selected={selectedEndMonth}
              label={labelEndMonth}
              onSelect={onSelectEndMonth}
            />
            <StyledDropdownInputYears
              list={listYears}
              label={labelEndYear}
              selected={selectedEndYear}
              onSelect={onSelectEndYear}
            />
          </ItemRangeDates>
        </RangeDates>
      </WrapperCollapsed>
    </StyledBoxInputEducation>
    <StyledIcon svg={Trash} hover onClick={onClickDelete} />
  </StyledBoxModuleAlignTop>
);

EducationItem.propTypes = {
  expanded: PropTypes.bool,
  labelStartMonth: PropTypes.string,
  labelEndMonth: PropTypes.string,
  labelStartYear: PropTypes.string,
  labelEndYear: PropTypes.string,
  listMonths: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      value: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)],
      id: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)]
    })
  ).isRequired,
  listYears: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      value: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)],
      id: PropTypes.oneOfType[(PropTypes.string, PropTypes.number)]
    })
  ).isRequired,
  onChangeDegreeInput: PropTypes.func,
  onChangeSchoolInput: PropTypes.func,
  onClickExpand: PropTypes.func,
  onClickDelete: PropTypes.func,
  onClickDown: PropTypes.func,
  onClickUp: PropTypes.func,
  onSelectStartMonth: PropTypes.func,
  onSelectStartYear: PropTypes.func,
  onSelectEndMonth: PropTypes.func,
  onSelectEndYear: PropTypes.func,
  order: PropTypes.number,
  selectedStartMonth: PropTypes.string,
  selectedEndMonth: PropTypes.string,
  selectedStartYear: PropTypes.string,
  selectedEndYear: PropTypes.string,
  degree: PropTypes.string,
  school: PropTypes.string,
  errorTitle: PropTypes.bool,
  errorCenter: PropTypes.bool
};

EducationItem.defaultProps = {
  expanded: true,
  labelStartMonth: '',
  labelEndMonth: '',
  labelStartYear: '',
  labelEndYear: '',
  errorTitle: false,
  errorCenter: false
};

export default EducationItem;
