import React from 'react';
import PropTypes from 'prop-types';
import {
  StyleList,
  StyleVolunteerItem,
  StyleBoxEmployee,
  StyleEditIcon
} from './style';
import { Pencil } from '../../../../assets/images/index';

const Resume = (dataVolunteer, limit) => (
  <StyleList>
    {dataVolunteer &&
      dataVolunteer.slice(0, limit).map(item => (
        <StyleVolunteerItem key={item.organization + item.role}>
          {item.role}
          {item.organization && <strong>{` (${item.organization})`}</strong>}
        </StyleVolunteerItem>
      ))}
  </StyleList>
);

const Volunteer = ({ limit, dataVolunteer, onEditMode, clickEditIcon }) => (
  <StyleBoxEmployee
    title="Voluntariados"
    more={dataVolunteer && dataVolunteer.length > limit}
    moreInfo={Resume(dataVolunteer, dataVolunteer ? dataVolunteer.length : 0)}
  >
    {onEditMode && (
      <StyleEditIcon
        svg={Pencil}
        size="m"
        color="dark"
        onClick={clickEditIcon}
        hover
      />
    )}
    {Resume(dataVolunteer, limit)}
  </StyleBoxEmployee>
);

Volunteer.propTypes = {
  dataVolunteer: PropTypes.arrayOf(
    PropTypes.shape({
      organization: PropTypes.string,
      role: PropTypes.string
    })
  ),
  limit: PropTypes.number,
  onEditMode: PropTypes.bool,
  clickEditIcon: PropTypes.func
};

export default Volunteer;
