import React from 'react';
import storiesOf from '../../../../.storybook/util';
import { TabsHorizontal, TabHorizontal } from '.';
import { dataDailyMenu, dataUser2, dataSearch } from '../../../utils/mocks/js';

const styleDiv = {
  maxWidth: '1280px',
  margin: 'auto'
};

storiesOf('UI Kit/TabsHorizontal', module)
  .add('Search', () => (
    <div style={styleDiv}>
      <TabsHorizontal>
        {dataSearch.map(item => (
          <TabHorizontal
            key={item.id}
            id={item.id}
            active={item.active}
            icon={item.icon}
          >
            {item.name} {item.occurrences && `(${item.occurrences})`}
          </TabHorizontal>
        ))}
      </TabsHorizontal>
    </div>
  ))
  .add('User', () => (
    <div style={styleDiv}>
      <TabsHorizontal>
        {dataUser2.map(item => (
          <TabHorizontal key={item.id} id={item.id} active={item.active}>
            {item.name} {item.occurrences && `(${item.occurrences})`}
          </TabHorizontal>
        ))}
      </TabsHorizontal>
    </div>
  ))
  .add('Daily Menu', () => (
    <div style={styleDiv}>
      <TabsHorizontal withButtons>
        {dataDailyMenu.map(item => (
          <TabHorizontal
            key={item.id}
            id={item.id}
            active={item.active}
            withButtons
          >
            {item.name} {item.occurrences && `(${item.occurrences})`}
          </TabHorizontal>
        ))}
      </TabsHorizontal>
    </div>
  ));
