import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledList,
  StyledElements,
  StyleInput,
  StyledBox,
  StyleInputDate
} from './style';

import TitleGivens from '../TitleGivens';

import BoxForm from './BoxForm';

import CheckboxReverse from '../Checkbox/CheckboxReverse';

const EditionIRPF = ({ className, title, dataList }) => (
  <StyledBox className={className}>
    <TitleGivens title={title} />
    <StyledList>
      {dataList.map((item, key) => {
        let content;
        if (item.labelCheckbox) {
          content = <CheckboxReverse label={item.labelCheckbox} />;
        } else if (item.labelInput) {
          content = (
            <BoxForm
              label={item.labelInput}
              htmlFor={`${item.labelInput}-${key + 1}`}
            >
              <StyleInput
                id={`${item.labelInput}-${key + 1}`}
                name={`${item.labelInput}-${key + 1}`}
                value=""
              />
            </BoxForm>
          );
        } else {
          content = (
            <BoxForm
              label={item.labelDate}
              htmlFor={`${item.labelInput}-${key + 1}`}
            >
              <StyleInputDate
                id={`${item.labelDate}-${key + 1}`}
                name={`${item.labelDate}-${key + 1}`}
                value=""
              />
            </BoxForm>
          );
        }
        return <StyledElements>{content}</StyledElements>;
      })}
    </StyledList>
  </StyledBox>
);

EditionIRPF.propTypes = {
  className: PropTypes.string,
  title: PropTypes.string,
  dataList: PropTypes.arrayOf(
    PropTypes.shape({
      labelCheckbox: PropTypes.string,
      labelInput: PropTypes.string,
      labelDate: PropTypes.string
    })
  )
};

export default EditionIRPF;
