import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import App from './App';
import {
  StyledBoxApps,
  StyledBoxAppsDark,
  StyledListApps,
  StyledListAppsWidget
} from './style';

import {
  AudienciasAlambre,
  Documentos,
  Envios,
  MenuDiaAlambre,
  Agenda,
  Workflow,
  Ondacero,
  ControlAcceso,
  Persona,
  Directorio,
  Incidencias,
  Prisma,
  SGLCajas,
  Azure,
  Woffu,
  OfertasComerciales,
  Partes,
  SubIntranetComercial,
  PortalClente,
  ImagenCorporativa,
  PrevencionServiciosMedicos
} from '../../../../assets/images/index';

const Apps = ({ className, dark, onClickBack, userId, roles }) => {
  const BoxApps = dark ? StyledBoxAppsDark : StyledBoxApps;
  const ListApps = dark ? StyledListApps : StyledListAppsWidget;
  return (
    <BoxApps className={`${className} BoxApps`}>
      <div className="viewport">
        <ListApps className="Apps-item">
          <App
            onClickBack={onClickBack}
            title="Agenda corporativa"
            icon={Agenda}
            dark={dark}
            goTo="/agenda"
          />
        </ListApps>
        {roles.includes('ROLE_ACCESS_CMS_MENUOFDAY') && (
          <ListApps className="Apps-item">
            <App
              onClickBack={onClickBack}
              title="Menú del día"
              icon={MenuDiaAlambre}
              dark={dark}
              goTo="/menu/semanal"
            />
          </ListApps>
        )}
        <ListApps className="Apps-item">
          <App
            onClickBack={onClickBack}
            title="Directorio"
            icon={Documentos}
            dark={dark}
            goTo="/directorio_empleados"
          />
        </ListApps>{' '}
        <ListApps className="Apps-item">
          <a
            href="http://mail.atresmedia.com"
            target="_blank"
            rel="noopener noreferrer"
          >
            <App
              onClickBack={onClickBack}
              title="Correo electrónico"
              icon={Envios}
              dark={dark}
            />
          </a>
        </ListApps>
        <ListApps className="Apps-item">
          <App
            onClickBack={onClickBack}
            title="Audiencias"
            icon={AudienciasAlambre}
            dark={dark}
            goTo="/audiencias"
          />
        </ListApps>
        {roles.includes('ROLE_ACCESS_OC') && (
          <ListApps className="Apps-item">
            <App
              onClickBack={onClickBack}
              title="Onda Cero"
              icon={Ondacero}
              dark={dark}
              goTo="/servicios-onda-cero"
            />
          </ListApps>
        )}
        <ListApps className="Apps-item">
          <App
            onClickBack={onClickBack}
            title="Control de acceso"
            icon={ControlAcceso}
            dark={dark}
            goTo={`/perfil/${userId}/control-acceso`}
          />
        </ListApps>
        <ListApps className="Apps-item">
          <a
            href="http://a3netpre/a3wf/nuevaSolicitud.jsf?idServicio=94"
            target="_blank"
            rel="noopener noreferrer"
          >
            <App
              onClickBack={onClickBack}
              title="CAU Incidencias"
              icon={Incidencias}
              dark={dark}
            />
          </a>
        </ListApps>
        <ListApps className="Apps-item">
          <a
            href="http://a3netpre/a3wf/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <App
              onClickBack={onClickBack}
              title="Workflow"
              icon={Workflow}
              dark={dark}
            />
          </a>
        </ListApps>
        <ListApps className="Apps-item">
          <App
            onClickBack={onClickBack}
            title="Portal del empleado"
            icon={Persona}
            dark={dark}
            goTo={`/perfil/${userId}/mi_perfil`}
          />
        </ListApps>
        <ListApps className="Apps-item">
          <App
            onClickBack={onClickBack}
            title="Documentación relevante"
            icon={Directorio}
            dark={dark}
            goTo="/documentacion-relevante"
          />
        </ListApps>
        {roles.includes('ROLE_ACCESS_PRISMA') && (
          <ListApps className="Apps-item">
            <a
              href="http://atresnet/prisma/login.jsf?e=1"
              target="_blank"
              rel="noopener noreferrer"
            >
              <App
                onClickBack={onClickBack}
                title="Prisma"
                icon={Prisma}
                dark={dark}
              />
            </a>
          </ListApps>
        )}
        {roles.includes('ROLE_ACCESS_SGL') && (
          <ListApps className="Apps-item">
            <a
              href="http://atresnet/sgl_caja/login.jsf"
              target="_blank"
              rel="noopener noreferrer"
            >
              <App
                onClickBack={onClickBack}
                title="SGL-Cajas"
                icon={SGLCajas}
                dark={dark}
              />
            </a>
          </ListApps>
        )}
        {roles.includes('ROLE_ACCESS_AZURE') && (
          <ListApps className="Apps-item">
            <a
              href="https://i3television.visualstudio.com/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <App
                onClickBack={onClickBack}
                title="Azure"
                icon={Azure}
                dark={dark}
              />
            </a>
          </ListApps>
        )}
        {roles.includes('ROLE_ACCESS_WOFFU') && (
          <ListApps className="Apps-item">
            <a
              href="https://i3television.woffu.com"
              target="_blank"
              rel="noopener noreferrer"
            >
              <App
                onClickBack={onClickBack}
                title="Woffu"
                icon={Woffu}
                dark={dark}
              />
            </a>
          </ListApps>
        )}
        {roles.includes('ROLE_ACCESS_COMMERCIALOFFERS') && (
          <ListApps className="Apps-item">
            <App
              onClickBack={onClickBack}
              title="Ofertas comerciales"
              icon={OfertasComerciales}
              dark={dark}
              goTo="/ofertas-comerciales"
            />
          </ListApps>
        )}
        {roles.includes('ROLE_ACCESS_ACTIVITYPARTS') && (
          <ListApps className="Apps-item">
            <a
              href="http://atresnet/PartesActividad/Login.jsp"
              target="_blank"
              rel="noopener noreferrer"
            >
              <App
                onClickBack={onClickBack}
                title="Partes de actividad"
                icon={Partes}
                dark={dark}
              />
            </a>
          </ListApps>
        )}
        {roles.includes('ROLE_ACCESS_SUBCOMMERCIAL') && (
          <ListApps className="Apps-item">
            <a
              href="http://atresnet/new_intranet/faces/homeComercial.jsf"
              target="_blank"
              rel="noopener noreferrer"
            >
              <App
                onClickBack={onClickBack}
                title="Sub intranet comercial"
                icon={SubIntranetComercial}
                dark={dark}
              />
            </a>
          </ListApps>
        )}
        {roles.includes('ROLE_ACCESS_CUSTOMERPORTAL') && (
          <ListApps className="Apps-item">
            <a
              href=" https://safeaccess.atresmedia.com/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <App
                onClickBack={onClickBack}
                title="Portal cliente"
                icon={PortalClente}
                dark={dark}
                goTo="/documentacion-relevante"
              />
            </a>
          </ListApps>
        )}
        {roles.includes('ROLE_ACCESS_CORPORATEIMAGE') && (
          <ListApps className="Apps-item">
            <App
              onClickBack={onClickBack}
              title="Imagen Corporativa"
              icon={ImagenCorporativa}
              dark={dark}
              goTo="/imagen-corporativa"
            />
          </ListApps>
        )}
        {roles.includes('ROLE_ACCESS_MEDICALSERVICES') && (
          <ListApps className="Apps-item">
            <App
              onClickBack={onClickBack}
              title="Prevención y servicios médicos."
              icon={PrevencionServiciosMedicos}
              dark={dark}
              goTo="/servicios-medicos"
            />
          </ListApps>
        )}
      </div>
    </BoxApps>
  );
};

Apps.propsType = {
  className: PropTypes.string,
  dark: PropTypes.bool
};

const mapStateToProps = state => ({
  userId: state.user.user.userId,
  roles: state.user.user.roles
});

export default connect(mapStateToProps)(Apps);
