import React from 'react';
import styled from 'styled-components';
import withReadme from 'storybook-readme/with-readme';
import Readme from './README.md';
import storiesOf from '../../../../.storybook/util';
import { CalendarEventDay, CalendarEventHour } from './index';
import mediaqueries from '../../../config/themes/variables/mediaqueries';

const StyledRecurs = styled.div`
  height: 100vh;
  position: relative;
  ${mediaqueries.greaterThan('desktop')`
    width: 360px;
    margin: 0 auto;
  `};
`;

const EventMonth = [
  {
    eventName: 'Campaña donación de alimentos',
    eventHour: [
      {
        eventHourStart: '10:30',
        eventHourEnd: '13:30'
      }
    ]
  },
  {
    eventName: 'Mercadillo de la tele',
    eventHour: [
      {
        eventHourStart: '10:30',
        eventHourEnd: '13:30'
      },
      {
        eventHourStart: '15:30',
        eventHourEnd: '17:30'
      }
    ]
  }
];

const eventHour = {
  eventName: 'Mercadillo de la tele',
  eventDate: '2018-09-21',
  eventConvener: 'RR.PP.',
  eventLocation: 'Estudio 7',
  docEvent: 'mercadillo-2018.pdf',
  descriptionEvent:
    'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation.',
  eventDays: [
    {
      eventDay: '2018-09-21',
      eventHour: '10:30 - 13:30 / 15:30 - 17:30'
    },
    {
      eventDay: '2018-09-22',
      eventHour: '10:30 - 13:30'
    }
  ]
};

storiesOf('core/CalendarEvent', module)
  .addDecorator(withReadme(Readme))
  .add('Not Event Day', () => (
    <StyledRecurs>
      <CalendarEventDay dateDay="Jun 02 2018" />
    </StyledRecurs>
  ))
  .add('Event Day', () => (
    <StyledRecurs>
      <CalendarEventDay dataEvent={EventMonth} dateDay="May 21 2018" />
    </StyledRecurs>
  ))
  .add('Event Day 2', () => (
    <StyledRecurs>
      <CalendarEventDay dataEvent={EventMonth} />
    </StyledRecurs>
  ))
  .add('Event Hour', () => (
    <StyledRecurs>
      <CalendarEventHour dataEvent={eventHour} />
    </StyledRecurs>
  ));
