import React from 'react';
import { rem } from 'polished';
import storiesOf from '../../../../.storybook/util';

/* ##### COMPONENTS ##### */
import Carousel from '.';

/* ###################### */

const CarouselWrapper = ({ children }) => (
  <div
    style={{
      height: rem('598px'),
      position: 'relative',
      width: '100%',
      overflow: 'hidden'
    }}
  >
    {children}
  </div>
);

const PreviousButton = props => (
  <button
    type="button"
    style={{ position: 'fixed', left: 0, top: 10 }}
    {...props}
  >
    Anterior
  </button>
);
const NextButton = props => (
  <button
    type="button"
    style={{ position: 'fixed', right: 0, top: 10 }}
    {...props}
  >
    Siguiente
  </button>
);
const Paginator = props => <div {...props} />;

storiesOf('core/Carousel', module).add('Default', () => (
  <CarouselWrapper>
    <Carousel
      previousButton={PreviousButton}
      nextButton={NextButton}
      paginator={Paginator}
    >
      <div>1</div>
      <div>2</div>
      <div>3</div>
    </Carousel>
  </CarouselWrapper>
));
