import React from 'react';
import storiesOf from '../../../../.storybook/util';
import Day from '.';

const styleDiv = {
  maxWidth: '1280px',
  margin: 'auto',
  display: 'flex',
  justifyContent: 'center',
  textAlign: 'center',
  flexWrap: 'wrap'
};

const styleItem = {
  maxWidth: '300px',
  margin: '0 20px 20px 20px'
};

storiesOf('core/Day', module).add('different formats', () => (
  <div style={styleDiv}>
    <div style={styleItem}>
      <Day date="2015/02/28" />
      <p>Short Date 2015/02/28</p>
    </div>
    <div style={styleItem}>
      <Day date="2015-02-28" />
      <p>ISO Date 2015-02-28</p>
    </div>
    <div style={styleItem}>
      <Day date="2015, 02, 28" />
      <p>2015, 02, 28</p>
    </div>
    <div style={styleItem}>
      <Day date="25 Mar 2015" />
      <p>Long Date 25 Mar 2015</p>
    </div>
    <div style={styleItem}>
      <Day date="Mar 25 2015" />
      <p>Long Date Mar 25 2015</p>
    </div>
    <div style={styleItem}>
      <Day date="Wednesday March 25 2015" />
      <p>Full Date Wednesday March 25 2015</p>
    </div>
  </div>
));
