import request from './api';
import { API_PAYROLL, API_PAYROLL_INCIDENCE } from '../constants/api';

export const getLatestPayrollList = (
  fromDate = '',
  toDate = '',
  maxResults = 5
) =>
  request(`${API_PAYROLL}/list`, {
    method: 'GET',
    params: { fromDate, toDate, maxResults }
  });

export const getDatesWithPayrolls = () =>
  request(`${API_PAYROLL}/months`, {
    method: 'GET'
  });

export const getPayrollFile = (
  payslipDate,
  allocationDate,
  payslipType = 'PAYMENT',
  token
) =>
  request(
    API_PAYROLL,
    {
      method: 'GET',
      responseType: 'blob',
      params: { payslipDate, allocationDate, payslipType, token }
    },
    false
  );

export const getPayrollsListWithSensitiveData = (yearMonth, token) =>
  request(`${API_PAYROLL}/month`, {
    method: 'GET',
    params: { yearMonth, token }
  });

export const postPayrollIncidence = (matter, incidence) =>
  request(`${API_PAYROLL_INCIDENCE}?subject=${matter}&issure=${incidence}`, {
    method: 'POST'
  });
