import React from 'react';
import Player from '@atresmedia/react-player';
import { StyledImg } from '../style';
import Image from '../../../core/Image';
import Galleries from '../../../core/Gallery';

const Video = ({ src }) => <Player model={src} />;
const Gallery = ({ home = false, ...props }) => (
  <Galleries home={home} {...props} />
);
const ImageContainer = ({ src, defaultCrop, onClickContent }) => (
  <StyledImg>
    <Image path={src} onClick={onClickContent} defaultCrop={defaultCrop} />
  </StyledImg>
);

export { ImageContainer as Image, Video, Gallery };
