import React from 'react';
import storiesOf from '../../../../.storybook/util';
import Avatar from '.';

import AvatarPez from '../../../../assets/images/avatars/axis3/pez.svg';
import AvatarGato from '../../../../assets/images/avatars/axis1/gato.svg';
import AvatarPinguino from '../../../../assets/images/avatars/axis2/pinguino.svg';
import Photo from '../../../../assets/images/avatars/perfilPhoto.jpg';
import { array } from '../../../utils/mocks/js';

const styleDiv = {
  width: '100%',
  margin: 'auto'
};

storiesOf('core/Avatar/Avatar', module)
  .add('sin imagen', () => (
    <div style={styleDiv}>
      <Avatar
        type="axis1"
        imgAvatar={AvatarGato}
        name="Pedro Almodobar Benitez"
      />
    </div>
  ))
  .add('con imagen axis1', () => (
    <div style={styleDiv}>
      <Avatar
        type="axis1"
        imgAvatar={AvatarGato}
        perfil={Photo}
        name="Pedro Almodobar Benitez"
      />
    </div>
  ))
  .add('con imagen axis2', () => (
    <div style={styleDiv}>
      <Avatar
        type="axis2"
        imgAvatar={AvatarPinguino}
        perfil={Photo}
        name="Pedro Almodobar Benitez"
      />
    </div>
  ))
  .add('con imagen axis3', () => (
    <div style={styleDiv}>
      <Avatar
        type="axis3"
        imgAvatar={AvatarPez}
        perfil={Photo}
        name="Pedro Almodobar Benitez"
      />
    </div>
  ))
  .add('sin imagen y con error: name is an array', () => (
    <div style={styleDiv}>
      <Avatar type="axis3" imgAvatar={AvatarPez} name={array} />
    </div>
  ));
