import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledBoxGeneric,
  StyledSaveModalButtons,
  StyledDescriptionModal,
  StyledButton
} from './style';
import Modal from '../../../core/Modal';

const Confirmation = ({ onCloseModal, onAcceptModal, data }) => (
  <Modal title={data.TITLE} clickCloseIcon={onCloseModal}>
    <StyledBoxGeneric>
      <StyledDescriptionModal>{data.DESCRIPTION}</StyledDescriptionModal>
      <StyledSaveModalButtons>
        <StyledButton modal onClick={onCloseModal}>
          {data.CANCEL}
        </StyledButton>
        <StyledButton secondary onClick={onAcceptModal}>
          {data.ACCEPT}
        </StyledButton>
      </StyledSaveModalButtons>
    </StyledBoxGeneric>
  </Modal>
);

Confirmation.propTypes = {
  onCloseModal: PropTypes.func.isRequired,
  onAcceptModal: PropTypes.func.isRequired,
  data: PropTypes.shape({
    TITLE: PropTypes.string,
    DESCRIPTION: PropTypes.string,
    ACCEPT: PropTypes.string,
    CANCEL: PropTypes.string
  })
};

export default Confirmation;
