import React, { Component } from 'react';
import moment from 'moment';
import {
  StyledWrapperDateSelector,
  StyledDropdownDay,
  StyledDropdownMonth,
  StyledDropdownYear,
  StyledError
} from './style';

import { getMonthName } from '../../../utils/dates';

const YEAR_SELECTOR = 'YEAR_SELECTOR';
const MONTH_SELECTOR = 'MONTH_SELECTOR';
const DAY_SELECTOR = 'DAY_SELECTOR';

class DateSelector extends Component {
  constructor(props) {
    super(props);
    const { date } = this.props;
    this.state = {
      daySelect: date ? this.formatDate(moment(date).format('D')) : null,
      monthSelect: date ? this.formatDate(moment(date).format('M')) : null,
      yearSelect: date ? this.formatDate(moment(date).format('YYYY')) : null
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.date !== this.props.date) {
      this.setState({
        daySelect: nextProps.date
          ? this.formatDate(moment(nextProps.date).format('D'))
          : null,
        monthSelect: nextProps.date
          ? this.formatDate(moment(nextProps.date).format('M'))
          : null,
        yearSelect: nextProps.date
          ? this.formatDate(moment(nextProps.date).format('YYYY'))
          : null
      });
    }
  }

  formatDate = dat => {
    const datNum = parseInt(dat, 10);
    return datNum < 10 ? `0${datNum}` : datNum.toString();
  };

  formatYear = year => Number(moment().format('YYYY')) - year;

  dateChange = (item, value) => {
    const { daySelect, monthSelect, yearSelect } = this.state;
    const { onChange } = this.props;
    let maxDays;
    let day;
    let month;
    let year;
    switch (item) {
      case DAY_SELECTOR:
        this.setState({
          daySelect: this.formatDate(value.value + 1)
        });
        day = this.formatDate(value.value + 1);
        break;
      case MONTH_SELECTOR:
        maxDays = moment(
          `${yearSelect}-${this.formatDate(value.value + 1)}`,
          'YYYY-MM'
        ).daysInMonth();
        if (maxDays < daySelect) {
          this.setState({
            daySelect: this.formatDate(maxDays),
            monthSelect: this.formatDate(value.value + 1)
          });
        } else {
          this.setState({ monthSelect: this.formatDate(value.value + 1) });
        }
        month = this.formatDate(value.value + 1);
        break;

      case YEAR_SELECTOR:
        maxDays = moment(
          `${this.formatYear(value.value)}-${monthSelect}`,
          'YYYY-MM'
        ).daysInMonth();
        if (maxDays < daySelect) {
          this.setState({
            daySelect: this.formatDate(maxDays),
            yearSelect: this.formatYear(value.value)
          });
        } else {
          this.setState({
            yearSelect: this.formatYear(value.value)
          });
        }
        year = this.formatYear(value.value);
        break;
      default:
        break;
    }
    if ((year || yearSelect) && (month || monthSelect) && (day || daySelect)) {
      onChange(
        /* eslint no-underscore-dangle: 0 */
        moment(
          `${year || yearSelect}-${this.formatDate(
            month || monthSelect
          )}-${this.formatDate(day || daySelect)}`
        )._d
      );
    } else {
      onChange(null);
    }
  };

  render() {
    const { daySelect, monthSelect, yearSelect } = this.state;
    const { years = 20, date, className, disabled } = this.props;
    const dateSelected = date
      ? moment(
          `${yearSelect}-${this.formatDate(monthSelect)}-${this.formatDate(
            daySelect
          )}`
        )
      : moment();
    const yearCurrent = new Date().getFullYear();
    const yearsList = Array.from(new Array(years), (v, i) => ({
      value: i,
      text: (yearCurrent - i).toString()
    }));

    const monthsBase = moment.months();
    const monthsList = monthsBase.map((v, i) => ({
      value: i,
      text: getMonthName(i)
    }));
    const daysBase = dateSelected.daysInMonth();

    const daysList = Array.from(new Array(daysBase), (v, i) => ({
      value: i,
      text: this.formatDate(i + 1)
    }));

    return (
      <StyledWrapperDateSelector className={className}>
        <StyledDropdownDay
          list={daysList}
          label={daySelect ? this.formatDate(daySelect) : 'Día'}
          changeSelect={e => this.dateChange(DAY_SELECTOR, e)}
          error={!!(!daySelect && this.props.error)}
          disabled={disabled}
        />
        <StyledDropdownMonth
          list={monthsList}
          label={monthSelect ? getMonthName(monthSelect - 1) : 'Mes'}
          changeSelect={e => this.dateChange(MONTH_SELECTOR, e)}
          error={!!(!monthSelect && this.props.error)}
          disabled={disabled}
        />
        <StyledDropdownYear
          list={yearsList}
          label={yearSelect ? this.formatDate(yearSelect) : 'Año'}
          changeSelect={e => this.dateChange(YEAR_SELECTOR, e)}
          error={!!(!yearSelect && this.props.error)}
          disabled={disabled}
        />
        {this.props.error && <StyledError>{this.props.error}</StyledError>}
      </StyledWrapperDateSelector>
    );
  }
}

export default DateSelector;
