import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import {
  StyledGeneralEvent,
  StyledIconContent,
  StyledEventRow,
  StyledDate,
  StyledTitle,
  StyledUser,
  StyledDesc,
  StyledIcons,
  StyledBoxInteriorButtonMessage,
  ButtonsEvent,
  StyledTag
} from './styleEvent';

/** API */
import { setCMSAction, checkViewedByContent } from '../../../utils/apiHeader';

/** Utils */
import { icons } from '../../../utils/icoEvents';

/** Models */
import { dataNotificationsModel } from '../../../utils/models';

/** Constants */
const STATUS = {
  VALIDATE_PENDING: 'PENDIENTE',
  VALIDATE_PENDING_GENERAL_SERVICES: 'PENDIENTE'
};

const ACTIONS = {
  VALIDATE: 'VALIDAR',
  REFUSE: 'RECHAZAR'
};

const URL_MATCHING = {
  NEWS: 'news',
  DOCUMENT: 'documents',
  EVENT: 'events'
};

class GeneralNotifications extends Component {
  executeAction = (contentType, action, contentId) => {
    const { update } = this.props;
    // Execute the action
    setCMSAction(
      URL_MATCHING[contentType],
      action.toLowerCase(),
      contentId
    ).then(() => {
      // Set this notification as viewed
      checkViewedByContent(contentId).then(() => update());
    });
  };

  // TODO: Right now the URL works only for news and events
  goToContent = item => {
    const { update, location, history } = this.props;
    const { contentId, contentType } = item;
    const pathFromContentType = contentType === 'NEWS' ? 'noticia' : 'agenda';
    const pathSelected = `/${pathFromContentType}/${contentId}`;

    if (
      pathFromContentType === 'noticia' &&
      location.pathname.includes(pathFromContentType)
    ) {
      window.location.pathname = pathSelected;
    } else {
      history.push(pathSelected);
    }

    checkViewedByContent(contentId).then(() => update());
  };

  render() {
    const { data } = this.props;
    return data
      ? data.map(item => (
          <StyledGeneralEvent
            key={item.id}
            tabIndex="0"
            className="notification"
          >
            <StyledIconContent>
              <StyledIcons
                size="m"
                color={item.accepted ? 'grayDarker' : 'light'}
                svg={icons(item.contentType)}
                className="notification-icon"
              />
              <StyledEventRow className="notification-row1">
                <StyledDate className="notification-date">
                  {item.sendDate}
                </StyledDate>
                <StyledTitle className="notification-title">
                  {item.title}
                </StyledTitle>
              </StyledEventRow>
              <StyledEventRow className="notification-row2">
                {item.creationUser && (
                  <StyledUser className="notification-creator">
                    {item.creationUser}
                  </StyledUser>
                )}
                <StyledDesc className="notification-description">
                  {item.description}
                </StyledDesc>
                {item.stateContent && (
                  <StyledTag>{STATUS[item.stateContent]}</StyledTag>
                )}
              </StyledEventRow>
            </StyledIconContent>
            <StyledBoxInteriorButtonMessage className="notification-box">
              {item.actionsUser &&
                item.actionsUser.length > 0 &&
                item.actionsUser.map(action => (
                  <ButtonsEvent
                    key={action}
                    onClick={() =>
                      this.executeAction(
                        item.contentType,
                        action,
                        item.contentId
                      )
                    }
                    ghost
                  >
                    {ACTIONS[action]}
                  </ButtonsEvent>
                ))}
              <ButtonsEvent onClick={() => this.goToContent(item)} ghost>
                {'VER'}
              </ButtonsEvent>
            </StyledBoxInteriorButtonMessage>
          </StyledGeneralEvent>
        ))
      : false;
  }
}

GeneralNotifications.propTypes = {
  update: PropTypes.func,
  data: dataNotificationsModel
};

export default withRouter(GeneralNotifications);
