import React from 'react';
import storiesOf from '../../../../.storybook/util';
import Multimedia from '.';
import { LANDSCAPE_MEDIUM } from '../../../utils/crops';
import { model as src } from '../../../utils/mocks/js';

storiesOf('UI Kit/Multimedia', module)
  .add('Imagen', () => (
    <Multimedia
      media="IMAGE"
      src="/crops/rabbit"
      defaultCrop={LANDSCAPE_MEDIUM}
    />
  ))
  .add('Video', () => <Multimedia media="VIDEO" src={src} />);
