import React from 'react';
import PropTypes from 'prop-types';
import Carousel from '../Carousel';

import {
  StyledGallery,
  StyledNumber,
  StyledBoxFull,
  StyledFullScreen,
  StyledViewGallery,
  StyledCamera,
  StyledText,
  StyledArrowLeft,
  StyledArrowRight,
  StyledBoxTextGallery,
  StyledImg
} from './style';
import Image from '../Image';
import { SizeFull, Gallery } from '../../../../assets/images/index';
import { onAnimation } from './animation';

const GalleryPager = ({ activePage, pagesNumber }) => (
  <StyledNumber className="pager" activePage>
    {activePage + 1}/{pagesNumber}
  </StyledNumber>
);

const GalleryPreviousButton = ({ ...props }) => (
  <StyledArrowLeft className="pager" {...props} />
);
const GalleryNextButton = ({ ...props }) => (
  <StyledArrowRight className="pager" {...props} />
);

const Galleries = ({
  home,
  onToggleFullSize,
  title,
  subtitle,
  author,
  src
}) => (
  <StyledGallery onMouseLeave={onAnimation} home={home}>
    {home === false && (
      <div>
        <StyledViewGallery onClick={onToggleFullSize}>
          <StyledCamera color="light" svg={Gallery} />
          <StyledText>Ver galería</StyledText>
        </StyledViewGallery>
        <StyledBoxFull onClick={onToggleFullSize}>
          <StyledFullScreen color="light" svg={SizeFull} />
        </StyledBoxFull>
      </div>
    )}

    <Carousel
      previousButton={GalleryPreviousButton}
      nextButton={GalleryNextButton}
      paginator={GalleryPager}
    >
      {src.map(image => (
        <StyledImg key={image} onMouseEnter={e => onAnimation(e, home)}>
          <StyledBoxTextGallery
            className="box"
            title={title}
            paragraph={subtitle}
            author={author}
          />
          <Image
            path={image}
            defaultCrop={{ width: 1239, height: 697 }}
            onClick={onToggleFullSize}
          />
        </StyledImg>
      ))}
    </Carousel>
  </StyledGallery>
);

Galleries.propTypes = {
  home: PropTypes.bool,
  onToggleFullSize: PropTypes.func,
  src: PropTypes.arrayOf(PropTypes.string)
};

export default Galleries;
