import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledLabel,
  StyledBoxInputSkill,
  StyledBoxModule,
  StyledOrderItemSkill,
  StyledIcon,
  StyledBoxRadio,
  StyledRadioButton,
  StyledBoxTwoModules
} from './style';

import Input from '../../../core/Input';
import { SKILLS } from '../../../../constants/literals/modals';
import { Trash } from '../../../../../assets/images/index';

const SkillItem = ({
  listNivel,
  name,
  skillLevel,
  order,
  onClickDown,
  onClickUp,
  onChangeInput,
  onChangeRadio,
  onClickDelete
}) => (
  <StyledBoxModule>
    <StyledOrderItemSkill
      order={order}
      onClickDown={onClickDown}
      onClickUp={onClickUp}
    />
    <StyledBoxTwoModules>
      <StyledBoxInputSkill>
        <StyledLabel>{SKILLS.SKILL}</StyledLabel>
        <Input value={name} onChange={onChangeInput} />
      </StyledBoxInputSkill>
      <StyledBoxRadio>
        {listNivel.map(item => (
          <StyledRadioButton
            key={`${item.value}`}
            id={`${item.value}-${order}`}
            checked={skillLevel === item.value}
            label={item.text}
            value={item.value}
            onChange={onChangeRadio}
          />
        ))}
      </StyledBoxRadio>
    </StyledBoxTwoModules>
    <StyledIcon svg={Trash} hover onClick={onClickDelete} />
  </StyledBoxModule>
);

SkillItem.propTypes = {
  listNivel: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string,
      text: PropTypes.string
    })
  ),
  name: PropTypes.string,
  skillLevel: PropTypes.string,
  order: PropTypes.number,
  onClickDown: PropTypes.func,
  onClickUp: PropTypes.func,
  onChangeInput: PropTypes.func,
  onChangeRadio: PropTypes.func,
  onClickDelete: PropTypes.func
};

SkillItem.defaultProps = {
  listNivel: [
    { text: 'Bajo', value: 'LOW' },
    { text: 'Medio', value: 'MEDIUM' },
    { text: 'Alto', value: 'HIGH' }
  ],
  name: '',
  order: 0,
  skillLevel: ''
};

export default SkillItem;
