import React from 'react';
import storiesOf from '../../../../.storybook/util';
import DropdownInput from '.';
import { list, list2 } from '../../../utils/mocks/js';

storiesOf('UI Kit/Dropdown/DropdownInput', module)
  .add('Default', () => <DropdownInput list={list} selected="Selecciona" />)
  .add('Scroll', () => (
    <DropdownInput
      list={list2}
      label="Selecciona una opcion"
      selected="Opción 0"
    />
  ))
  .add('Disable', () => (
    <DropdownInput list={list} disabled selected="Opción 0" />
  ));
