import React from 'react';
import PropTypes from 'prop-types';
import { StyledNotifications } from './style';

const Notifications = ({ numberNot, className }) => (
  <StyledNotifications className={className}>{numberNot}</StyledNotifications>
);

Notifications.propTypes = {
  numberNot: PropTypes.number, // PropTypes.oneOf(PropTypes.number, PropTypes.string), TODO
  className: PropTypes.string
};

Notifications.defaultProps = {};

export default Notifications;
