import React from 'react';
import StoryRouter from 'storybook-react-router';
import storiesOf from '../../../../.storybook/util';
import Accordion from '.';
import { OurTeam } from '../OurTeam';
import { dataMapAccordion, dataTeam } from '../../../utils/mocks/js';

const styleDiv = {
  maxWidth: '1280px',
  margin: 'auto'
};

storiesOf('core/Accordion', module)
  .addDecorator(StoryRouter())
  .add('Accordion', () => (
    <div style={styleDiv}>
      {dataMapAccordion.map((item, key) => (
        <Accordion
          title={item.title}
          level={key + 1}
          mainId={item.mainId}
          secondaryId={item.secondaryId}
        >
          <OurTeam dataTeam={dataTeam} />
        </Accordion>
      ))}
    </div>
  ));
