import styled from 'styled-components';
import { rem } from 'polished';
import BigCalendar from 'react-big-calendar';
import estilosBase from 'react-big-calendar/lib/css/react-big-calendar.css';
import mediaqueries from '../../../config/themes/variables/mediaqueries';
import TitleWidget from '../TitleWidget';

const BigWrapper = styled.div`
  touch-action: none;
  margin-left: ${rem('-15px')};
  margin-right: ${rem('-15px')};
  ${mediaqueries.greaterThan('tablet')`
    margin-left: ${rem('24px')};
    margin-right: ${rem('24px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
   max-width: ${rem('1180px')};
  `};
`;

const StyledTitleWidget = styled(TitleWidget)`
  ${mediaqueries.greaterThan('tablet')`
    line-height: ${rem('30px')};
    padding-left: ${rem('32px')};
  `};
`;

const StyledWrapper = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: ${rem('30px')};
  width: 100%;
  height: auto;
  background-color: ${props => props.theme.colors.colorOne};
  ${mediaqueries.greaterThan('desktop')`
   position: relative;
   flex-direction: row;
   background-color: transparent;
  `};
`;
const StyleBigCalendar = styled(BigCalendar)`
  ${estilosBase};

  font-family: ${props => props.theme.fonts.medium};
  font-size: ${rem('12px')};
  height: ${rem('332px')};
  transition: transform 2s ease-in-out;
  width: 100%;
  max-width: ${rem('880px')};

  ${mediaqueries.greaterThan('tablet')`
    max-width: 100%;
  `};

  .rbc-toolbar-label {
    font-family: ${props => props.theme.fonts.medium};
    font-size: ${rem('20px')};
    text-transform: uppercase;
  }
  .rbc-month-header {
    background-color: ${props => props.theme.colors.grayLighter};
  }

  .rbc-header {
    border: none;
    font-size: inherit;
    font-weight: 700;
    padding: 0 ${rem('5px')};
    text-align: center;
    text-transform: uppercase;
  }

  .rbc-header + .rbc-header {
    border: none;
  }

  .rbc-row:nth-child(3),
  .rbc-row:nth-child(4) {
    ${mediaqueries.lessThan('mobile')`
      display: none;
    `};
  }

  .rbc-row-bg {
    background-color: ${props => props.theme.colors.grayLightest};
  }

  .rbc-month-row + .rbc-month-row,
  .rbc-day-bg + .rbc-day-bg {
    border-color: ${props => props.theme.colors.light};
  }

  .rbc-month-row {
    max-height: ${rem('52px')};
    ${mediaqueries.greaterThan('mobile')`
      max-height: inherit;
    `};
  }

  .rbc-date-cell {
    height: ${rem('36px')};
    font-size: ${rem('14px')};
    text-align: center;
    font-weight: 700;
    position: relative;
    ${mediaqueries.greaterThan('mobile')`
      font-size: ${rem('16px')};
      padding: ${rem('10px')} 0 0 ${rem('10px')};
      text-align: left;
      height: auto;
      position: static
    `};
    span {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: ${rem('50px')};
      display: flex;
      justify-content: center;
      align-items: center;
      ${mediaqueries.greaterThan('mobile')`
        position: static;
        width: auto;
        height: auto;
        display: inline;
    `};
    }
  }
  .rbc-month-view {
    height: ${rem('287px')};
    ${mediaqueries.greaterThan('mobile')`
      height:${rem('600px')};      
    `};
    ${mediaqueries.greaterThan('tablet')`
      max-width: inherit;
    `};
    ${mediaqueries.greaterThan('desktop')`
      max-width: ${rem('875px')};
    `};
  }
  .rbc-today {
    background-color: ${props => props.theme.colors.grayLight};
  }

  .rbc-event {
    background-color: transparent;
    padding: 0 ${rem('5px')};
    pointer-events: none;
    ${mediaqueries.greaterThan('mobile')`
      pointer-events: auto;
    `};
  }

  .rbc-event.rbc-selected {
    background-color: ${props => props.theme.colors.colorOne};
    border-radius: 0;
    p {
      color: ${props => props.theme.colors.light};
      :before {
        background-color: ${props => props.theme.colors.light};
      }
    }
  }

  .rbc-date-cell.rbc-now {
    font-weight: normal;
  }

  .rbc-show-more {
    background-color: transparent;
    color: ${props => props.theme.colors.grayDarkest};
    font-family: ${props => props.theme.fonts.medium};
    font-size: ${rem('12px')};
    font-weight: normal;
    padding: ${rem('5px')} ${rem('10px')};
    display: none;
    ${mediaqueries.greaterThan('mobile')`
      display: block;
    `};
  }

  .rbc-selected-cell {
    background-color: ${props => props.theme.colors.grayLighter};
  }
  .rbc-off-range-bg {
    background-color: transparent;
  }
  .rbc-off-range {
    color: ${props => props.theme.colors.gray};
  }
  .rbc-current {
    background-color: ${props => props.theme.colors.colorOne};
    color: ${props => props.theme.colors.light};
    font-family: ${props => props.theme.fonts.medium};
    // It has to be !important to show in bold today's day
    font-weight: bold !important;
    position: relative;
    :after {
      background-color: ${props => props.theme.colors.colorOne};
      content: '';
      height: 370%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
      z-index: -1;
    }
  }

  .rbc-event-content {
    text-align: center;
    > p {
      display: block;
      padding: 0;
      &:before {
        display: inline-block;
        position: static;
        ${mediaqueries.greaterThan('desktop')`
          position: absolute;
        `};
      }
    }
    ${mediaqueries.greaterThan('mobile')`
      text-align: left;
    `};
  }
  ${mediaqueries.greaterThan('tablet')`
   height: ${rem('600px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
   width:100%;
  `};
`;

const StyleEventTitle = styled.p`
  color: ${props => props.theme.colors.dark};
  display: inline;
  font-size: ${rem('12px')};
  font-weight: normal;
  margin: 0;
  padding: ${rem('10px')} 0 0 ${rem('10px')};
  position: relative;

  :before {
    background-color: ${props => props.theme.colors.dark};
    border-radius: 50%;
    content: '';
    height: ${rem('6px')};
    width: ${rem('6px')};
    position: absolute;
    top: ${rem('10px')};
    left: 0;
  }

  ${mediaqueries.greaterThan('mobile')`
    display: block;
    padding: 0 0 0 10px;
  `};
`;
const StyleEventText = styled.span`
  display: none;

  ${mediaqueries.greaterThan('mobile')`
    display: inline-block;
    padding-left: ${rem('2px')};
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 97%;
    overflow: hidden;
    padding-left: ${rem('10px')};
  `};
`;

const StyledMonthHeader = styled.span`
  color: ${props => {
    if (props.weekend) {
      return props.theme.colors.gray;
    }
    return props.theme.colors.dark;
  }} !important;
`;

const StyledDay = styled.span`
  color: ${props => props.theme.colors.gray};
`;

export {
  StyledTitleWidget,
  StyleBigCalendar,
  StyleEventTitle,
  StyleEventText,
  StyledWrapper,
  StyledMonthHeader,
  StyledDay,
  BigWrapper
};
