import React from 'react';
import PropTypes from 'prop-types';

import ModalPassword from '../../ModalPassword';

import {
  StyledWithholding,
  StyledTitle,
  StyledWithholdingContent,
  StyledBtnContent,
  StyledDropdown,
  StyledLabel,
  StyledButton
} from './style';

const Withholding = ({
  onSelectYear,
  onClickDownload,
  years,
  year,
  modalPassword,
  checkPassword,
  hideModalPassword,
  errorPassword
}) => (
  <StyledWithholding>
    <StyledTitle>Certificado de retenciones</StyledTitle>
    {years ? (
      <React.Fragment>
        <StyledWithholdingContent>
          <StyledLabel id="year">Año</StyledLabel>

          <StyledDropdown
            list={years}
            label={year}
            changeSelect={onSelectYear}
            ariaLabelledby="year"
          />
        </StyledWithholdingContent>
        <StyledBtnContent>
          <StyledButton secondary onClick={() => onClickDownload()}>
            Descargar
          </StyledButton>
        </StyledBtnContent>
        {modalPassword && (
          <ModalPassword
            action={value => checkPassword(value)}
            clickCloseIcon={() => hideModalPassword()}
            errorPassword={errorPassword}
          />
        )}
      </React.Fragment>
    ) : (
      <p>No se encontraron certificados de retenciones</p>
    )}
  </StyledWithholding>
);
Withholding.propTypes = {
  onSelectYear: PropTypes.func,
  onClickDownload: PropTypes.func,
  years: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      text: PropTypes.string,
      value: PropTypes.string
    })
  ),
  year: PropTypes.string,
  modalPassword: PropTypes.bool,
  checkPassword: PropTypes.func,
  hideModalPassword: PropTypes.func,
  errorPassword: PropTypes.bool
};

export default Withholding;
