import styled from 'styled-components';
import { rem } from 'polished';

import mediaqueries from '../../../config/themes/variables/mediaqueries';
import Credit from '../Credit';
import OptionsNews from '../OptionsNews';
import TagCategory from '../TagCategory';
import DateFormat from '../DateFormat';
import Truncate from '../Truncate';
import Headline from '../Headline';
import Icon from '../Icon';

import { StyledAuthor } from '../Credit/style';

const StyledBoxNewsTab = styled.div`
  background-color: ${props => props.theme.colors.grayLighter};
  position: relative;

  ${props =>
    props.orientation === 'horizontal' &&
    `
      margin-bottom: 1rem;
    `};

  &:after {
    content: '';
    height: 0;
    display: block;
    width: 100%;
    clear: both;
  }
`;

const StyledMultimedia = styled.div`
  ${mediaqueries.greaterThan('desktop')`
    ${props =>
      props.orientation === 'vertical' &&
      `
        float: left;
        width: 100%;
      `};
  `};
`;

const StyledBoxNewText = styled.article`
  position: relative;
  padding: ${rem('15px')};
  overflow: hidden;
  height: ${props => (props.orientation === 'vertical' ? '100%' : 'auto')};
  ${props =>
    props.orientation === 'horizontal' &&
    `
      border-bottom: 1rem solid ${props.theme.colors.grayLighter};
    `};

  ${mediaqueries.greaterThan('mobile')`
    padding: ${rem('20px')};
  `};
  ${mediaqueries.greaterThan('desktop')`
    min-height: 342px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  `};
`;

const DeleteItemReadAfter = styled(Icon)`
  margin: ${rem('20px')} ${rem('20px')} 0 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
`;

const StyledTagCategory = styled(TagCategory)`
  margin: 0 0 ${rem('5px')} 0;
  font-size: ${props => props.theme.fontSize.small};
  ${mediaqueries.greaterThan('mobile')`
    margin: 0 0 ${rem('10px')} 0;
  `};
`;

const StyledDateNews = styled.p`
  font-size: ${props => props.theme.fontSize.xsmall};
  font-family: ${props => props.theme.fonts.medium};
  margin: 0 0 ${rem('10px')};
  text-decoration: underline;

  ${mediaqueries.greaterThan('mobile')`
    margin: 0 0 ${rem('20px')};
    font-size: ${props => props.theme.fontSize.xsmall};
  `};
`;

const OverwriteDateFormat = styled(DateFormat)`
  font-family: ${props => props.theme.fonts.medium};
`;

const OverwriteHeadline = styled(Headline)`
  font-size: ${rem('20px')};
  line-height: 1.2;
  margin-bottom: ${rem('5px')};

  ${mediaqueries.greaterThan('mobile')`
    font-size: ${rem('26px')};
  `};

  ${mediaqueries.greaterThan('desktop')`
    font-size: ${rem('14px')};
  `};
`;

const LinkHeader = styled.a`
  font-family: inherit;
  color: inherit;
  font-size: inherit;
  :hover {
    color: ${props => props.theme.colors.primary};
  }
`;

const Summary = styled(Truncate)`
  font-size: ${rem('15px')};
  line-height: 1.47;

  ${mediaqueries.between('tablet', 'desktop')`
    font-size: ${rem('16px')};
    line-height: 1.56;
  `};

  ${mediaqueries.greaterThan('desktop')`
    font-size: ${rem('14px')};
    line-height: 1.14;  
  `};
`;

const StyleLinkRead = styled.a`
  display: none;
  color: ${props => props.theme.colors.primary};
  font-size: ${props => props.theme.fontSize.xsmall};
  font-family: ${props => props.theme.fonts.bold};
  margin: 0.9375rem 0 0;

  &:hover {
    color: ${props => props.theme.colors.gray};
  }

  ${mediaqueries.greaterThan('tablet')`
    display: inline;
  `};
`;

const StyledBoxOptions = styled.div`
  margin: 1.5rem 0 0;
  position: relative;
  width: 100%;

  &:after {
    content: '';
    height: 0;
    display: block;
    width: 100%;
    clear: both;
  }
`;

const StyledCredit = styled(Credit)`
  float: left;

  ${StyledAuthor} {
    font-size: ${rem('12px')};

    ${mediaqueries.greaterThan('mobile')`
      font-size: ${rem('16px')};
    `};

    ${mediaqueries.greaterThan('desktop')`
      font-size: ${rem('12px')};
    `};
  }
`;

const StyledOptionsNews = styled(OptionsNews)`
  float: right;
  margin-top: ${rem('0px')};
`;

export {
  StyledBoxNewsTab,
  StyledMultimedia,
  StyledBoxNewText,
  DeleteItemReadAfter,
  StyledTagCategory,
  StyledDateNews,
  OverwriteDateFormat,
  OverwriteHeadline,
  LinkHeader,
  Summary,
  StyleLinkRead,
  StyledBoxOptions,
  StyledCredit,
  StyledOptionsNews
};
