import styled from 'styled-components';
import { rem } from 'polished';
import mediaqueries from '../../../config/themes/variables/mediaqueries';

const StyledGoldMinute = styled.div`
  display: flex;
  background-color: ${props => props.theme.colors.grayLighter};
`;

const StyledData = styled.div`
  padding-left: ${rem('30px')};
  position: relative;
  width: 100%;
`;

const StyledChannelTitle = styled.h3`
  color: ${props => props.theme.colors.dark};
  font-family: ${props => props.theme.fonts.bold};
  font-size: ${rem('16px')};
  margin-top: ${rem('8px')};
  ${mediaqueries.greaterThan('desktop')`
    margin-bottom: 0;
  `};
`;

const StyledSpan = styled.span`
  color: ${props => props.theme.colors.dark};
  font-family: ${props => props.theme.fonts.normal};
  font-size: ${rem('15px')};
  padding-left: ${rem('15px')};
`;

const StyledSpanRate = styled(StyledSpan)`
  border-right: ${rem('1px')} solid ${props => props.theme.colors.dark};
  padding-left: 0;
  padding-right: ${rem('25px')};
`;

const StyledTime = styled.span`
  background-color: ${props => props.theme.colors.dark};
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  line-height: 1.4;
  font-size: ${rem('12px')};
  height: ${rem('22px')};
  padding: ${rem('3px')};
  position: absolute;
  right: 0;
  text-align: center;
  top: 0;
  width: ${rem('53px')};
`;

export {
  StyledChannelTitle,
  StyledData,
  StyledGoldMinute,
  StyledSpan,
  StyledSpanRate,
  StyledTime
};
