import React from 'react';
import PropTypes from 'prop-types';

import { getCrop } from '../../../utils/crops';

const mimeByFormat = {
  webp: { mime: 'image/webp', extension: 'webp' },
  jpg: { mime: 'image/jpeg', extension: 'jpg' }
};
export default class Image extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = { isLoaded: false };
    this.onImageLoad = this.onImageLoad.bind(this);
    this.onImageError = this.onImageError.bind(this);
  }

  onImageLoad() {
    this.setState({ isLoaded: true });
  }

  onImageError() {
    this.setState({ isError: true });
  }

  render() {
    const {
      crops,
      className,
      defaultCrop,
      densities,
      formats,
      lowCompression,
      path,
      placeholder: Placeholder,
      ...props
    } = this.props;
    const { isLoaded, isError } = this.state;

    return (
      <figure className={className}>
        {!isLoaded && <Placeholder error={isError} />}
        {!isError && (
          <picture>
            {crops &&
              crops.length > 0 &&
              formats.reduce(
                (total, format) =>
                  total.concat(
                    crops.map(({ media, crop }) => (
                      <source
                        key={media + format}
                        type={mimeByFormat[format].mime}
                        media={media}
                        srcSet={getCrop({ path, crop, format })}
                      />
                    ))
                  ),
                []
              )}
            <img
              {...props}
              src={getCrop({ path, crop: defaultCrop, format: 'jpg' })}
              alt={props.alt}
              onLoad={this.onImageLoad}
              onError={this.onImageError}
            />
          </picture>
        )}
      </figure>
    );
  }
}

const CropType = PropTypes.shape({
  height: PropTypes.number.isRequired,
  width: PropTypes.number.isRequired
});

Image.defaultProps = {
  placeholder: () => null,
  lowCompression: false,
  crops: [],
  formats: ['jpg'],
  densities: [1],
  path: undefined,
  defaultCrop: { width: 512, height: 288 }
};

Image.propTypes = {
  className: PropTypes.string,
  path: PropTypes.string,
  placeholder: PropTypes.func,
  lowCompression: PropTypes.bool,
  defaultCrop: CropType,
  crops: PropTypes.arrayOf(
    PropTypes.shape({
      media: PropTypes.string,
      crop: CropType.isRequired
    })
  ),
  densities: PropTypes.arrayOf(PropTypes.oneOf([1, 1.5, 2, 3, 4])),
  formats: PropTypes.arrayOf(PropTypes.oneOf(['webp', 'jpg']))
};
